function Sitter () {
	this.defaultData = {
		'id': 0,
		'consumersId': 0,
		'avatarIdentifier': 0,
		'avatarName': '',
		'sittedByUserAccountIdentifier': 0,
		'type': 0,
		'rights': 0,
		'worldName' : '',
		'sittedByAccountName' : '',
		'ownerAccountName' : '',
		'lastClick': 0
	};

	this.data = {};
	this.set(this.defaultData);
	this.name = 'Sitter';
}

Sitter.TYPE_SITTER = 1;
Sitter.TYPE_DUAL = 2;
Sitter.TYPE_SITTED = 3;
Sitter.TYPE_OWNER = 4;

Sitter.COLLECTION_SITTED_AVATARS = 1;
Sitter.COLLECTION_SITTERS_FOR = 2;
Sitter.COLLECTION_DUALS_FOR = 3;
Sitter.COLLECTION_DUALS_AVATARS = 4;

Sitter.prototype = new AbstractModel();
Cache.register(Sitter);
