GameLobby.app.service('Loading', ['$rootScope', function ($rootScope) {

	function Loading (id) {
		this.id = id || uniqid();
		this.loading = false;
		this.elements = [];
		this.callbacks = [];
	}

	Loading.prototype = {
		start: function () {
			this.loading = true;
			this.emit(this.loading);
		},
		stop: function () {
			this.loading = false;
			this.emit(this.loading);
		},
		emit: function () {
			var that = this;
			this.callbacks.forEach(function (cb) {
				if (!cb) return;
				cb.call(that, that.state)
			});
		},
		addPuppetElement: function (element) {
			this.elements.push(element);
		},
		destroy: function () {
			for (var i = 0; i < this.elements.length; i++) {
				this.elements[i] = null;
			}
			for (var i = 0; i < this.callbacks.length; i++) {
				this.callbacks[i] = null;
			}
		},
		onStateChange: function (cb) {
			this.callbacks.push(cb);
		}
	};

	function LoadingFactory () {
		this.instances = {};
	}

	LoadingFactory.prototype = {
		create: function (id) {
			var l = new Loading(id);
			return this.instances[l.id] = l;
		},
		get: function (id) {
			if (!this.instances[id]) {
				this.create(id);
			}
			return this.instances[id];
		},
		destroy: function (id) {
			this.instances[id].destroy();
			this.instances[id] = null;
		}
	};

	return new LoadingFactory();
}]);