GameLobby.app.service('MellonService', ['$rootScope', '$location', 'SessionService', 'Request', function ($rootScope, $location, sessionService, Request) {
    'use strict';

    var mellonUrl = new MellonUrl(GameLobby.config.mellon.url);
    GameLobby.config.mellon.application.languageId = GameLobby.config.languages[GameLobby.globals.selectedCountry];
    var mellonConfig = {
        'url': mellonUrl.getBase(),
        'application': GameLobby.config.mellon.application,
        'mellon': GameLobby.config.mellon.mellon
    };
	var bridge = new MellonBridge(mellonConfig);

    var fensterClose = function () {
        var modal = $('div.jqFensterModal:visible');
        if (modal.length) {
            $.fensterFinder(modal).close().destroy();
        }
    };

    $rootScope.$on('newTranslation', function() {
        GameLobby.config.mellon.application.languageId = GameLobby.config.languages[GameLobby.globals.selectedCountry];
        bridge = new MellonBridge(mellonConfig);
    });


	$rootScope.$on('login', function() {
		var userAccountIdentifier = $rootScope['Player']['data']['userAccountIdentifier'];
		if(!userAccountIdentifier) {
			return;
		}
        if (mellonUrl['getAjaxSessionValidate']) {
            var url = mellonUrl.getAjaxSessionValidate(userAccountIdentifier)
            url = url + '?msid=' + bridge.getCookie('msid');
            $.ajax({
                url: url,
                type: 'GET',
                datatype: 'json',
                success: function (response) {
                    if( typeof(GameLobby.config['mellon']['checkSession']) != 'undefined'
                        && GameLobby.config['mellon']['checkSession']
                        && !sessionService.isAdmin() ) {
                        if (!response.isValid) {
                            $.removeCookie('msid');
                            sessionService.logout();
                        }
                    }
                }
            });
        }
	});

	$rootScope.$on('mellon-changePasswordDone', function() {
        Request.create(
            'player/logoutAll',
            {},
            function (data) {
                sessionService.logout();
            },
            function (data) {
            }
        );
	});

	return {
        modalCloseIfAny: fensterClose,

        inject: function () {
            if (window.mellonBridgeInit) {
                return;
            }

            if (!mellonConfig.mellon.session) {
                mellonConfig.mellon.session = {};
            }

            // forcibly cookie reset if needed
            var hash = $location.url();
            if (hash && hash.indexOf('msid=') !== -1) {
                var sid = hash.match(/msid=([a-z0-9]+)/)[1] || false;
                if (sid) {
					bridge.setCookie('msid', sid);
                }
            }

            window.mellonBridgeInit = function (urn) {
                var channelInstance = null;

                var channel = bridge.getNewChannel(urn);
                channel
                    .on('close', fensterClose)
                    .on('redirect', function (params) {
                        window.location.href = params.url;
                        // $rootScope.$broadcast('mellon-redirect', params);
                    })
                    .on('resize', function (w, h) {
                        var windowWidth = $(window).width();

                        if (w > windowWidth) {
                          w = windowWidth - 10;
                          $('div.jqFensterHolder:visible').addClass('mobile');
                        }

                        channelInstance.resize(w, h);

                        $('.jqFensterModalContent iframe').css({width: w, height: h + 10});
                        $('.jqFensterModal').css('opacity', 1);
                        $('.jqEbony').removeClass('loading');
                    })
					.on('marketingTracking', function (url) {
						var cb = bridge.getTrackingIframeCallback();
						cb(url, function () {});
					})
                    .on('event', function (id, data) {
                        $rootScope.$broadcast('mellon-' + id, data);
                    });

                channelInstance = channel.createInstance();

                return channelInstance;
            };
            $(window).on('mellon.modalOpen', function () {
                $('.jqEbony').addClass('loading');
                window.setTimeout(function() {
                    $('.jqEbony').addClass('fadeIn');
                }, 200);
            });
            $(window).on('mellon.modalClose', function () {
                $('.jqFensterModal').css('opacity', 0);
            });
        },

        getConfig: function () {
            return mellonConfig;
        },

        getUrlHelper: function () {
            return mellonUrl;
        },

        getModal: function (url) {
            var modal = $.fenster('#jqFensterModalLayout');
            modal.element.data('mellonIframeUrl', url);
            return modal;
        }
    };
}]);
