GameLobby.app.controller('AchievementsController', ['$scope', 'Request', 'SessionService', function ($scope, Request, SessionService) {
	$scope['Achievement'] = Achievement;
	$scope['mysteryAchievementsCount'] = 0;
	var showSpecialAchievements = false;
	var setupDropdown = true;

	// Initially request an achievements update
	GameLobby.request.create('achievements/update');

	// Recalculates the amount of completed, the total amount of achievable levels and the collected prestige based on the selected category
	function updateStatistic (categoryId) {
		$scope['levelsTotal'] = 0;
		$scope['completedTotal'] = 0;
		$scope['totalPrestige'] = 0;
		for (var i = 0; i < $scope['achievements']['data'].length; i++) {
			var data = $scope['achievements']['data'][i]['data'];
			if(categoryId == Achievement.CATEGORY_ALL || data['category'] == categoryId) {
				$scope['levelsTotal'] += data['maxLevel'];
				$scope['completedTotal'] += data['completedLevels'].length;
				for(var j = 0; j < data['completedLevels'].length; j++) {
					$scope['totalPrestige'] += data['completedLevels'][j]['prestige'];
				}
			}
		}
	}

	var unsubscribeIndex = -1;
	$scope['achievements'] = Achievement.getCollection();
	unsubscribeIndex = $scope['achievements'].subscribe(function () {
		// Is there any special achievement
		showSpecialAchievements = false;
		for (var i = 0; i < $scope['achievements']['data'].length; i++) {
			if (!showSpecialAchievements && $scope['achievements']['data'][i]['data']['category'] == Achievement.CATEGORY_SPECIAL) {
				showSpecialAchievements = true;
				break;
			}
		}

		// Setup the category dropdown (if not done yet)
		if (setupDropdown) {
			$scope['achievementsDropdownData'] = {
				'selected': Achievement.CATEGORY_ALL,
				'resize': true,
				'maxHeight': 300,
				'id': 'achievementsCategorySelect',
				'appendTo': '#achievementsCategorySelect',
				'bodyClass': 'achievementsCategory',
				'callback': updateStatistic,
				'selectBy': 'option.key'
			};
			updateStatistic(Achievement.CATEGORY_ALL);
			setupDropdown = false;
		} else {
			updateStatistic($scope['achievementsDropdownData']['selected']);
		}
		// Update the category dropdown
		$scope['achievementsDropdownData']['options'] = [];
		for (var i = 0; i < Achievement.CATEGORIES.length; i++) {
			if (Achievement.CATEGORIES[i] == Achievement.CATEGORY_SPECIAL && !showSpecialAchievements) {
				continue;
			}
			$scope['achievementsDropdownData']['options'].push({
				'key': Achievement.CATEGORIES[i],
				'name': GameLobby.translate('Achievements.Category_' + Achievement.CATEGORIES[i]),
				'selectedName': GameLobby.translate('Achievements.SelectedCategory_' + Achievement.CATEGORIES[i])
			});
		}
	});

	$scope.$on('$destroy', function () {
		if (unsubscribeIndex >= 0) {
			$scope['achievements'].unsubscribe(unsubscribeIndex);
		}
	});
}]);
