/**
 * Generates an element that show the avatar information in small
 *
 * USAGE:
 * <avatar-info-small avatar-identfier="{{1}}" gameworld-id="{{2}}"></avatar-info-small>
 *
 */
/** @expose */
GameLobby.app.directive('avatarInfoSmall', ['WindowService', function (WindowService) {
	return  {
		restrict: 'AE',
		templateUrl: 'templates/partials/avatar/info-small.html',
		scope: {
			avatarIdentifier: '=',
			gameworldId: '=',
			removeAvatar: '='
		},
		link: function (scope, elem, attrs) {
			scope['openSuspendedInfo'] = function (avatar) {
				WindowService.open({
					templateUrl: 'templates/modals/suspendedInfo.html',
					title: GameLobby.txt["SuspendedInfo.Headline"],
					avatar: scope['avatar']
				});
			};
			var updateAvatarIdentifier = function () {
				scope['avatar'] = Avatar.get(scope['avatarIdentifier']);
				scope['AvatarModel'] = Avatar;
				scope['gameworld'] = GameWorld.get(scope['gameworldId']);
				scope['avatarInformation'] = AvatarInformation.get(scope['avatarIdentifier']);
			};
			scope.$watch('avatarIdentifier', updateAvatarIdentifier);
			updateAvatarIdentifier();
		}
	};
}]);