/**
 * Draws a single achievement box and may handle comuncation with an achievements list via emitted events
 *
 * Possible attributes:
 * achievementData: 	object - data object of an achievement model
 * expanded: 			boolean - value that will toggle on expanding and collapsing the achievement box
 * smallSized:			boolean - will change some renderings (texts) to fit smaller screen resolutions
 */

/** @expose */
GameLobby.app.directive('achievementBox', ['$rootScope', '$timeout', function ($rootScope, $timeout) {
	return {
		replace: true,
		templateUrl: 'templates/partials/achievement/box.html',
		scope: {
			'achievementData': '=',
			'expanded': '=',
			'smallSized': '='
		},
		link: function ($scope, element, attrs) {
			$scope['mysteryCategory'] = Achievement.CATEGORY_SPECIAL;

			var transitionEvents = 'transitionend webkitTransitionEnd oTransitionEnd MSTransitionEnd';
			var animationEvents = 'animationend webkitAnimationEnd oAnimationEnd MSAnimationEnd';

			var preventExpanding = false;
			$scope['expand'] = function() {
				// Is it actually allowed to expanded this achievemetn right now? (is it expandable? No animation running?)
				if(!$scope['collecting'] && $scope['achievementData']['completedLevels'].length > 0 && !preventExpanding) {
					$scope['expanded'] = !$scope['expanded'];
				}
			}

			$scope['collecting'] = false;
			$scope['collectReward'] = function (achievementId) {
				if($scope['collecting']) {
					return;
				}
				function startAwardAnimation () {
					element.unbind(transitionEvents, startAwardAnimation);
					// Tell the backend that the achievement gets collected
					GameLobby.request.create('achievements/collectReward', {'achievementId': achievementId});
					$scope['collecting'] = true;
					// Inform the achievements list (it should not reorder, while this animation is running)
					$scope.$emit('achievementAnimation', $scope['achievementData']['type'], 1);
					function onAnimationEnd() {
						$scope['collecting'] = false;
						$scope.$emit('achievementAnimation', $scope['achievementData']['type'], -1);
						requestAngularApply();
					}
					// Let angular render the award container before subscribing to the end of its animation
					$timeout(function() {
						preventExpanding = false;
						var awardAnimationElem = element.find('.awardAnimationContainer table');
						if(awardAnimationElem.length > 0) {
							awardAnimationElem.one(animationEvents, onAnimationEnd);
						} else {
							onAnimationEnd();
						}
					});
					requestAngularApply();
				}
				// If this box is expanded, collapse it before starting the collect animation
				if ($scope['expanded']) {
					$scope['expanded'] = false;
					preventExpanding = true;
					element.bind(transitionEvents, startAwardAnimation);
				} else {
					startAwardAnimation();
				}
			}

			$scope.$on('$destroy', function() {
				if($scope['collecting']) {
					$scope.$emit('achievementAnimation', $scope['achievementData']['type'], -1);
				}
			});
		}
	}
}]);
