function Achievement () {
	this.defaultData = {
		'id': 0,
		'type': 0,
		'value': 0,
		'level': 0,
		'category': 0,	// Config value
		'maxLevel': 0,  // Config value
		'increasing': true, // Config value
		'prestige': 0, // Config value for current level
		'startValue': 0, // Config value for current level
		'endValue': 0, // Config value for current level
		'completedLevels': [], // [{value: int, prestige: int, collected: int}]
		'extraData': null,
		'achieved': false, // current level achieved but not collected (see set())
		'langKeySuffix': '' // some achievements use different language keys depending on specific conditions (see set())
	};
	this.data = {};
	this.set = function (data){
		data['achieved'] = false;
		if(data['level'] < data['maxLevel'] && data['value'] > 0) {
			if(data['increasing']) {
				data['achieved'] = (data['value'] >= data['endValue']);
			} else {
				data['achieved'] = (data['value'] <= data['endValue']);
			}
		}

		if(data['type'] == Achievement.TYPE_FINISHED_A_GAMEWORLD) {
			if (data['extraData']['allianceName']) {
				data['langKeySuffix'] = '_hasAlliance';
			} else if(data['extraData']['kingdomTag']) {
				data['langKeySuffix'] = '_hasKingdom';
			}
		} else if(data['type'] == Achievement.TYPE_WON_A_GAMEWORLD && data['extraData']['kingdomTag']) {
			data['langKeySuffix'] = '_hasKingdom';
		}

		this.parent.set.call(this, data);
	};
	this.set(this.defaultData);
	this.name = 'Achievement';
}

Achievement.CATEGORY_ALL = 0;
Achievement.CATEGORY_BATTLE = 1;
Achievement.CATEGORY_BUILDING = 2;
Achievement.CATEGORY_HERO = 3;
Achievement.CATEGORY_RESOURCES = 4;
Achievement.CATEGORY_POWER = 5;
Achievement.CATEGORY_SOCIAL = 6;
Achievement.CATEGORY_SPECIAL = 7;
Achievement.CATEGORIES = [
	Achievement.CATEGORY_ALL,
	Achievement.CATEGORY_BATTLE,
	Achievement.CATEGORY_BUILDING,
	Achievement.CATEGORY_HERO,
	Achievement.CATEGORY_RESOURCES,
	Achievement.CATEGORY_POWER,
	Achievement.CATEGORY_SOCIAL,
	Achievement.CATEGORY_SPECIAL
];

Achievement.TYPE_WON_A_GAMEWORLD      = 62;
Achievement.TYPE_FINISHED_A_GAMEWORLD = 63;

Achievement.prototype = new AbstractModel();
Cache.register(Achievement);