GameLobby.app.controller('GameworldController', ['$scope', 'Request', '$state', '$stateParams', 'MellonService', 'WindowService', 'Loading', 'SessionService', 'Servertime', '$location', '$rootScope', function ($scope, Request, $state, $stateParams, MellonService, WindowService, Loading, SessionService, ServertimeService, $location, $rootScope) {
	$scope['avatar'] = null;
	$scope['avatarInformation'] = null;
	$scope['showButton'] = true;
	$scope['settings'] = GameLobby.config.avatar;
	$scope['serverTime'] = ServertimeService.getServerTime();
	$scope['gameworldStatus'] = GameWorld.STATUS;
	$scope['SitterModel'] = Sitter;
	$scope['totalDuals'] = 0;
	$scope['AvatarModel'] = Avatar;

	$scope.tooltipSitter = {"tooltipText": GameLobby.txt['Sitter.Tooltip.Text']};
	$scope.tooltipDual = {"tooltipText": GameLobby.txt['Dual.Tooltip.Text']};

	// Type of message for ban message notification
	$scope['baningMessages'] = {};
	$scope['baningMessages'][0] = 'Suspended';
	$scope['baningMessages'][Avatar.ACTION_ISOLATED] = 'Isolated';
	$scope['baningMessages'][Avatar.ACTION_LOCK] = 'Locked';

	function updateTribeStatistic() {
		var colors = [];
		colors[1] = '#c8844d';
		colors[2] = '#bc6a27';
		colors[3] = '#b05001';
		$scope['tribePieData'] = [];
		for (var tribeId in $scope['gameworld']['data']['tribes']) {
			$scope['tribePieData'].push({'label': GameLobby.translate('Tribe_'+tribeId), 'data': parseInt($scope['gameworld']['data']['tribes'][tribeId]), 'color': colors[tribeId]});
		}
	}
	$scope.$on('newTranslation', updateTribeStatistic);

	var unsubscribeWorld = null;
	var unsubscribeDuals = null;
	var unsubscribeGold = null;

	$scope['avatar'] = Avatar.get($stateParams.id);
	if (typeof $stateParams.ban !== 'undefined') {
		$scope['avatar'] =	Avatar.get($stateParams.ban);
		openSuspendedInfo();
	}
	function updateAvatar() {
		if (unsubscribeWorld != null) {
			$scope['gameworld'].unsubscribe(unsubscribeWorld);
			$scope['duals'].unsubscribe(unsubscribeDuals);
		}

		$scope['avatarInformation'] = AvatarInformation.get($scope['avatar']['data']['avatarIdentifier']);
		Cache.ensureNotOlderThan($scope['avatarInformation'], 300);
		var gameWorldId = $scope['avatar']['data']['consumersId'];
		$scope['gameworld'] = GameWorld.get(gameWorldId);
		unsubscribeWorld = $scope['gameworld'].subscribe(updateTribeStatistic);


		$scope['sitters'] = Sitter.getCollection(Sitter.COLLECTION_SITTERS_FOR, $scope['avatar']['data']['country'], $scope['avatar']['data']['avatarIdentifier']);
		$scope['duals'] = Sitter.getCollection(Sitter.COLLECTION_DUALS_FOR, $scope['avatar']['data']['country'], $scope['avatar']['data']['avatarIdentifier']);

		if ($scope['avatar'].data['userAccountIdentifier'] == $rootScope['Player'].data['userAccountIdentifier']) {
			$scope['ownStatus'] = Sitter.TYPE_OWNER;
		} else {
			$scope['ownStatus'] = Sitter.TYPE_SITTER;
		}

		unsubscribeDuals = $scope['duals'].subscribe(function() {
			$scope['totalDuals'] = $scope['duals']['data'].length;
			for (var i = 0; i < $scope['duals']['data'].length; i++) {
				var dual = $scope['duals']['data'][i];
				if (dual.data['sittedByUserAccountIdentifier'] == $rootScope['Player'].data['userAccountIdentifier']) {
					$scope['ownStatus'] = dual.data['type'];

					// If we are the owner of the account remove one from the total duals
					if(dual.data['type'] == Sitter.TYPE_OWNER) {
						$scope['totalDuals'] -= 1;
					}
				}
			}
		});

		$scope['goldList'] = Gold.getCollection($scope['avatar']['data']['country']);
	}

	function openSuspendedInfo () {
		WindowService.open({
			templateUrl: 'templates/modals/suspendedInfo.html',
			title: GameLobby.txt["SuspendedInfo.Headline"],
			avatar: $scope['avatar']
		});
	}
	var unsubscribeAvatar = $scope['avatar'].subscribe(updateAvatar);


	$scope.addSitterDialog = function (numSitters) {
		if (numSitters >= GameLobby.Features.maxSitters) {
			return;
		}

		WindowService.open({
			templateUrl: 'templates/modals/addSitter.html',
			title: GameLobby.txt['AddSitter.Headline'],
			avatarIdentifier: $scope['avatar']['data']['avatarIdentifier']
		});
	};

	$scope.addDualDialog = function (numDuals) {
		if (numDuals >= GameLobby.Features.maxDuals) {
			return;
		}

		WindowService.open({
			templateUrl: 'templates/modals/addDual.html',
			title: GameLobby.txt["AddDual.Headline"],
			avatarIdentifier: $scope['avatar']['data']['avatarIdentifier']
		});
	};

	$scope.openSuspendedInfo = openSuspendedInfo;

	$scope.transferGoldDialog = function (avatarIdentifier, goldId) {
		WindowService.open({
			templateUrl: 'templates/modals/transferGold.html',
			title: GameLobby.txt['Gold.TransferConfirmationHeadline'],
			confirmation: true,
			onClose: function (data) {
				if (data['confirmed'] == true) {
					Request.create(
						'gold/transfer',
						{
							'targetAvatarIdentifier': avatarIdentifier,
							'goldId': goldId
						},
						function (data) {},
						function (data) {
							openErrorGoldTransferPopup(data.message);
						}
					);
				}
			}
		});
	};

	$scope.closeDialog = function () {
		$scope.modal.close();
	};

	function openErrorGoldTransferPopup (errorMessage) {
		WindowService.open({
			templateUrl: 'templates/modals/errorTransferGold.html',
			title: GameLobby.txt['userError.Headline'],
			confirmation: false,
			errorMessage: errorMessage
		});
	}

	$scope.removeSitterDialog = function (avatarIdentifier, sitterAccountIdentifier) {
		var title = GameLobby.txt['RemoveSitter.Headline'];
		var content = 'RemoveSitter.Confirmation';
		if ($scope['ownStatus'] == Sitter.TYPE_SITTER) {
			title = GameLobby.txt['RemovePlayerAsSitter.Headline'];
			content = 'RemovePlayerAsSitter.Confirmation';
		}
		WindowService.open({
			templateUrl: 'templates/modals/simpleDialogContent.html',
			title: title,
			contentTextKey: content,
			confirmation: 'YesNo',
			onClose: function (data) {
				if (data['confirmed'] == true) {
					Request.create(
						'sitter/remove',
						{
							'avatarIdentifier': avatarIdentifier,
							'sitterAccountIdentifier': sitterAccountIdentifier
						},
						function (data) {
							if ($scope['ownStatus'] != Sitter.TYPE_OWNER) {
								window.location.replace('/');
							}
						},
						function (data) {
							$scope['error'] = data;
						}
					);
				}
			}
		});
	};

	$scope.removeDualDialog = function (avatarIdentifier, dualAccountIdentifier) {
		var title = GameLobby.txt['RemoveDual.Headline'];
		var content = 'RemoveDual.Confirmation';
		if ($scope['ownStatus'] != Sitter.TYPE_OWNER) {
			title = GameLobby.txt['RemovePlayerAsDual.Headline'];
			content = 'RemovePlayerAsDual.Confirmation';
		}
		WindowService.open({
			templateUrl: 'templates/modals/simpleDialogContent.html',
			title: title,
			contentTextKey: content,
			confirmation: 'YesNo',
			onClose: function (data) {
				if (data.confirmed === true) {
					Request.create(
						'dual/remove',
						{
							'avatarIdentifier': avatarIdentifier,
							'dualAccountIdentifier': dualAccountIdentifier
						},
						function (data) {
							if ($scope['ownStatus'] != Sitter.TYPE_OWNER) {
								window.location.replace('/');
							}
						},
						function (data) {
							$scope['error'] = data;
						}
					);
				}
			}
		});
	};

	$scope.setSitterPermissionsDialog = function (sitter) {
		WindowService.open({
			templateUrl: 'templates/modals/setSitterPermissions.html',
			title: GameLobby.txt['EditSitter.Headline'],
			context: 'edit',
			sitter: sitter,
			avatarIdentifier: sitter.data.avatarIdentifier,
			sitterAccountIdentifier: sitter.data.sittedByUserAccountIdentifier,
			onClose: function () {
				// invalidate Cache
				Cache.invalidate(sitter.serverId);
				// update this sitter rights
				sitter = Sitter.get(sitter.data.id);
			}
		});
	};

	$scope.$on('$destroy', function () {
		if (unsubscribeWorld != null) {
			$scope['gameworld'].unsubscribe(unsubscribeWorld);
			$scope['duals'].unsubscribe(unsubscribeDuals);
		}
		$scope['avatar'].unsubscribe(unsubscribeAvatar);
	});

	$scope.$on('mellon-gameWorldInMaintenanceMode', function (e, data) {
		MellonService.modalCloseIfAny();
		WindowService.open({
			templateUrl: 'templates/modals/maintenance.html',
			title: GameLobby.txt['userError.Headline'],
			onClose: function () {
			}
		});
	});

}]);