/** @expose */
GameLobby.app.directive('tooltip', ['$templateCache', '$compile', '$parse', '$timeout', '$http', function ($templateCache, $compile, $parse, $timeout, $http) {
	var TOOLTIP_DELAY = 100;

	return {
		'restrict': 'A',
		'scope': {
			tooltipData: '=',
			tooltipHeadline: '=',
			gameworld: '='
		},
		'link': function (scope, element, attrs) {
			var $ttElem = null, $arrowElem;
			var placement = attrs['tooltipPlacement'];
			var renderTooltip = true;
			var timer = null;
			var destroyed = false;

			function show () {
				if (timer != null) {
					$timeout.cancel(timer);
					timer = null;
				}
				if (destroyed) {
					return;
				}
				timer = $timeout(displayTooltipAndPosition, TOOLTIP_DELAY);
			}

			function finalizeHide (elementWasDestroyed) {
				timer = null;
				if ($ttElem == null) {
					return;
				}
				if ((attrs['tooltipForceRender']) || elementWasDestroyed) {
					if ($ttElem) {
						$ttElem['remove']();
					}
					$ttElem = null;
				}
				else {
					$ttElem['hide']();
				}
			}

			function hide (elementWasDestroyed) {
				if (timer != null) {
					$timeout.cancel(timer);
					timer = null;
				}
				if (elementWasDestroyed) {
					finalizeHide(elementWasDestroyed);
				} else {
					timer = $timeout(finalizeHide, 10);
				}
			}

			function displayTooltipAndPosition () {
				if (destroyed) {
					return;
				}
				timer = null;
				if ($ttElem == null) {
					if (attrs['tooltipUrl'] === undefined) {
						return;
					}

					$http.get(attrs['tooltipUrl'], {cache: $templateCache}).success(function (html) {
						$ttElem = new ElementTip(element, html, placement);
						$compile($ttElem.domElement.contents())(scope);
						$timeout(displayTooltipAndPosition, 10);
					});
				} else {
					$ttElem['setPosition']();
					$ttElem['show']();
				}
			}

			PointerHelper.onPointerOver(element[0], function () {
				if(GameLobby.config['environment'] != 'dev' && attrs['tooltipUrl'] == 'templates/partials/tooltip.html' && !GameLobby.txt[scope['tooltipData']]) {
					// Do not show tooltips with untranslated content on live environments
					return;
				}
				if (renderTooltip) {
					show();
				}
			});
			element.bind('$destroy', function () {
				PointerHelper.removeBindings(element[0]);
				destroyed = true;
				hide(true);
			});
			PointerHelper.onPointerOut(element[0], hide);
			PointerHelper.onPointerDown(element[0], function() {
				var isTouch = (navigator.userAgent.toLowerCase().search(/(iphone|ipod|ipad|opera mini|fennec|palm|blackberry|android|symbian|touch;)/) != -1);

				if (isTouch == true && $ttElem != null && $ttElem.domElement.css('visibility') == 'hidden') {
					show();
				} else if (isTouch == false || (isTouch == true && attrs['tooltipTouch'] == undefined) || ($ttElem != null && $ttElem.domElement.css('visibility') == 'visible')) {
					hide();
				}
			});
		}
	};
}]);
