GameLobby.app.controller('GoldTransferController', ['$scope', function ($scope) {
	$scope['avatars'] = [];
	$scope['gameWorlds'] = {};
	var subscriber = null;
	var allAvatars = Avatar.getCollection();
	$scope['gold'] = Gold.get($scope.modal['goldId']);

	$scope['gold'].subscribe(function (gold) {
		var isRegionalGold = GameLobby.Features['goldTransferNotAllowedCountries'].indexOf(gold.data['country']) !== -1;

		subscriber = allAvatars.subscribe(function () {
			for (var i = 0; i < allAvatars['data'].length; i++) {
				var avatar = allAvatars['data'][i];
				var gameWorld = GameWorld.get(avatar['data']['consumersId']);

				$scope['gameWorlds'][avatar.data['consumersId']] = gameWorld;

				if (gameWorld.data['canTransferMoney'] === 1 &&
					gameWorld.data['status'] != GameWorld.STATUS['STATUS_FINISHED'] &&
					gameWorld.data['status'] != GameWorld.STATUS['STATUS_ENDGAME_FINISHED']) {
					if (isRegionalGold) {
						// Regional gold can be transferred only to the same country avatar
						if (gold.data['country'] === gameWorld.data['country']) {
							$scope['avatars'].push(avatar);
						}
					} else {
						$scope['avatars'].push(avatar);
					}
				}
			}
		});
	});

	$scope['closeDialog'] = function () {
		$scope.modal.close();
	};

	$scope.$on('$destroy', function () {
		if (subscriber != null) {
			allAvatars.unsubscribe(subscriber);
		}
	});
}]);