function Notification () {
	this.defaultData = {
		'id': 0,
		'typeId': 0,
		'type': 0,
		'userAccountIdentifier': 0,
		'avatarIdentifier': 0,
		'visibleUntil': 0,
		'read' : false,
		'class' : null,
		'route' : false,
		'routeParam' : null,
		'moreLink': null
	};

	this.data = {};
	this.set(this.defaultData);
	this.name = 'Notification';
}

Notification.TYPE_SITTER_OWNER_INACTIVE = 1;
Notification.TYPE_AVATAR_SUSPENDED = 2;
Notification.TYPE_AVATAR_LOCKED = 3;
Notification.TYPE_AVATAR_ISOLATED = 4;
Notification.TYPE_ACHIEVEMENT_ACHIEVED = 5;

Notification.prototype = new AbstractModel();
Cache.register(Notification);
