/**
 * Converts seconds into a human-readable textual representation - will loose accuracy as time span gets bigger!
 * (e.g. "12 minutes", "13 hours, 10 minutes", "2 days, 10 hours")
 *
 * Attributes
 * seconds        int: length of time span in seconds
 */

/** @expose */
GameLobby.app.filter('timeSpan', function () {
    return function (timeSpan) {
        var hours = 0;
        var minutes = 0;
        var seconds = 0;
        var part1 = '';
        var part2 = '';

        if (timeSpan > 86400) {
            var days = Math.floor(timeSpan/86400);
            part1 = days + " " + (days == 1 ? window['GameLobby'].txt['TimeSpan.Day'] : window['GameLobby'].txt['TimeSpan.Days']);

            hours = Math.floor((timeSpan%86400)/3600);
            if (hours > 0) {
                part2 = hours + " " + (hours == 1 ? window['GameLobby'].txt['TimeSpan.Hour'] : window['GameLobby'].txt['TimeSpan.Hours']);
            }
        } else if (timeSpan > 3600) {
            hours = Math.floor(timeSpan/3600);
            part1 = hours + " " + (hours == 1 ? window['GameLobby'].txt['TimeSpan.Hour'] : window['GameLobby'].txt['TimeSpan.Hours']);

            minutes = Math.floor((timeSpan%3600)/60);
            if (minutes > 0) {
                part2 = minutes + " " + (minutes == 1 ? window['GameLobby'].txt['TimeSpan.Minute'] : window['GameLobby'].txt['TimeSpan.Minutes']);
            }
        } else if (timeSpan > 60) {
            minutes = Math.floor(timeSpan/60);
            part1 = minutes + " " + (minutes == 1 ? window['GameLobby'].txt['TimeSpan.Minute'] : window['GameLobby'].txt['TimeSpan.Minutes']);

            seconds = Math.floor(timeSpan%60);
            if (minutes > 0) {
                part2 = seconds + " " + (seconds == 1 ? window['GameLobby'].txt['TimeSpan.Second'] : window['GameLobby'].txt['TimeSpan.Seconds']);
            }
        } else {
            part1 = seconds + " " + (seconds == 1 ? window['GameLobby'].txt['TimeSpan.Second'] : window['GameLobby'].txt['TimeSpan.Seconds']);
        }

        if (part2 != '') {
            return part1 + ", " + part2;
        } else {
            return part1;
        }
    };
});