GameLobby.app.controller('NotificationController', ['$scope', function ($scope) {
	$scope.notifications = Notification.getCollection();
	$scope.notificationHeightLimit = 0;

	adaptToScreenSize();
	$(window).on('resize', adaptToScreenSize);

	function adaptToScreenSize () {
		var topOffset = 24;
		var bottomOffset = 10;
		var notificationHeight = 94;
		var notificationBottomMargin = 15;
		var height = $(window).height();

		$scope.notificationHeightLimit = Math.floor((height - topOffset - bottomOffset) / (notificationHeight + notificationBottomMargin));
		doAngularApply();
	}
}]);