/**
 * Holds injectable html snippets and objects used in building html snippets
 * These are referenced as filters in the lang.js file and injected in the translate directive
 *
 *  Example 1:
 *    lang.js GameLobby.txt['atTime'] = 'um {{atTime|i18ndt}} Uhr';
 *    is used in the view as : <translate data="atTime:{{header.time}}">atTime</translate>
 *
 *    GameLobby.htmlFilters['i18ndt'] has a placeholder _?_ where {{header.time}} will be 'compiled'
 */
GameLobby.htmlFilters['i18ndt'] = '<span i18ndt="{{_?_}}" format="mediumTime"></span>';
GameLobby.htmlFilters['i18ndtshort'] = '<span i18ndt="{{_?_}}" format="short"></span>';
GameLobby.htmlFilters['i18ndtdate'] = '<span i18ndt="{{_?_}}" format="shortDate"></span>';
GameLobby.htmlFilters['countdown'] = '<span countdown="{{_?_}}"></span>';


/**
 * Example 2
 *
 * Put a tag around a string in a translate template
 * GameLobby.txt['showMeBoldness'] = ' something will be bold: {{something|bold}}';
 *
 * used as: <translate data="something:aThing">showMeBoldness</translate>
 * renders: <translate> something will be bold: <b>aThing</b></translate>
 */
GameLobby.htmlFilters['bold'] = {tag: 'b'};


/**
 */
GameLobby.htmlFilters['r'] = '<br />';
GameLobby.htmlFilters['itemStart'] = '<li>';
GameLobby.htmlFilters['itemEnd'] = '</li>';

GameLobby.htmlFilters['playerLink'] = '<player-link class="truncated" playerId="{{_?_}}" playerName="{{_?_}}"></player-link>';
GameLobby.htmlFilters['icon'] = '<i class="tkIcon {{_?_}}"></i>';

GameLobby.htmlFilters['coords'] = '<coordinates x="{{_?_}}" y="{{_?_}}"></coordinates>';
GameLobby.htmlFilters['month'] = '{{_?_|date:"MMMM"}}';
GameLobby.htmlFilters['year'] = '{{_?_|date:"yyyy"}}';
GameLobby.htmlFilters['HHMMSS'] = '{{_?_|HHMMSS}}';
GameLobby.htmlFilters['HHMM'] = '{{_?_|HHMM}}';
GameLobby.htmlFilters['fromNow'] = '<span i18ndt="{{_?_}}" relative="from"></span>';
GameLobby.htmlFilters['truncate'] = '<span class="truncated">{{_?_}}</span>';
GameLobby.htmlFilters['percent'] = '{{_?_| bidiNumber:\'percent\':false:false}}';
GameLobby.htmlFilters['ratio'] = '<span class="values" ng-bind-html="0 | bidiRatio:_?_:_?_"></span>';
GameLobby.htmlFilters['defaultName'] = '{{_?_|defaultName}}';