/** @expose */
GameLobby.app.filter('bidiRatio', function() {

	return function(value, current, max, styled, unit){

		return new BidiHelper({
			current: current,
			fullCapacity: max,
			threshold: 9999,
			digits:0,
			styled: styled, //true: wraps the values in <span>s. Only used in an ng-bind-html
			inline: true,
			unit: unit
		}).getBidiRatio();

	}

});
