/**
 * @constructor
 */
function AbstractModel () {
	this.name = 'abstract';
	this.data = null;

	/**
	 * @returns {Object}
	 */
	this.get = function () {
		return this.data;
	};

	/**
	 * @param {String} field
	 * @returns {Number|String|Object|Array}
	 */
	this.getField = function (field) {
		return this.data[field];
	};

	/**
	 * @param {String} data
	 * @param {Boolean|undefined} recursive
	 * @returns {void}
	 */
	this.set = function (data, recursive) {
		if (typeof recursive === 'undefined') {
			recursive = true;
		}

		// merge objects recursively
		if (recursive) {
			_.merge(this.data, data);
		} else {
			this.data = data;
		}
		// cast everything that seems to be a number to a number
		for (var key in this.data) {
			var checkVal = Number(this.data[key]);
			if ("" + checkVal === this.data[key]) {
				this.data[key] = checkVal;
			}
		}
	};

	this.parent = this;
}