function BidiHelper(opts) {

	//for bidi number used
	this.value = opts.value || null;

	//for bidi coordinates
	this.xCoord = opts.xCoord || null;
	this.yCoord = opts.yCoord || null;

	//for bidi Ratio
	this.current = opts.current || null;
	this.fullCapacity = opts.fullCapacity || null;
	this.styled = opts.styled || null; //true: wraps the values in <span>s. Only used in an ng-bind-html

	//unit can be one of the predefined units or a translation key string
	this.unit = opts.unit || null;
	this.showSign = opts.showSign;
	this.inline = opts.inline;
	this.threshold = opts.threshold;
	this.digits = opts.digits;
	this.fractionSize = opts.fractionSize;


	/**
	 * Directions, Markers and Overrides
	 * @type {{LTR: string, RTL: string, OVERRIDE_LTR: string, OVERRIDE_RTL: string, OVERRIDE_STOP: string}}
	 */
	this.config = {
		/**
		 * Constants for directions
		 */
		LTR: 'ltr',
		RTL: 'rtl',

		/**
		 * Implicit Directional Marks
		 */
		MARK_RTL: '\u200f',
		MARK_LTR: '\u200e',
		/**
		 * Explicit Left to Right Directional Override
		 * Force following characters to be treated as strong left-to-right characters
		 */
		OVERRIDE_LTR: '\u202d',

		/**
		 * Explicit Right to Left Directional Override
		 * Force following characters to be treated as strong right-to-left characters
		 */
		OVERRIDE_RTL: '\u202e',

		/**
		 * Terminating Explicit Directional Embeddings and Overrides
		 * End the scope of the last LRE, RLE, RLO, or LRO
		 */
		OVERRIDE_STOP: '\u202c',

		/**
		 * Exceptions in directions
		 */
		EXCEPTIONS: {
			'il': 'ltr'
		}

	};

	/**
	 * Possible units
	 * @type {{PERCENT: string, TIMES: string}}
	 */
	this.units = {
		'percent': '\x25', 	// %
		'times': '\xd7'		// *
	};

	/**
	 * List of signs
	 * @type {{POSITIVE: string, NEGATIVE: string}}
	 */
	this.signs = {
		'positive': '\x2b',
		'negative': '\x2d'
	};

	this.directions = this.getDirections();
	this.unitKey = this.getUnit();
	this.strongDirectionMark = (this.inline ? this.getDirectionMark(this.directions['surroundingDirection']) : '');
}

BidiHelper.prototype.getDirections = function () {
	var directions = {
		'surroundingDirection': getDirection(),
		'numberDirection': null
	};
	var language = getLanguage();

	if (this.config.EXCEPTIONS.hasOwnProperty(language)) {
		directions['numberDirection'] = this.config.EXCEPTIONS[language];
	} else {
		directions['numberDirection'] = directions['surroundingDirection'];
	}

	return directions;
};


BidiHelper.prototype._createBidiRatio = function () {
	var bidiAmount = this._createBidiNumber(this.current);
	var bidiCapacity = this._createBidiNumber(this.fullCapacity);

	var amount = bidiAmount;
	var divider = getDirection() == this.config.LTR ?  ' / ' : ' \\ ';
	var capacity = bidiCapacity;

	if(this.styled){
		amount = this._createBidiNumber(this.current, 'amount wrapper');
		capacity = this._createBidiNumber(this.fullCapacity, 'capacity');
	}

	return this.strongDirectionMark + amount + divider + capacity;
};

BidiHelper.prototype._createBidiNumber = function(number, styleClass) {

	var useableNumber = (typeof number === 'undefined' ? this.value : number);

	// Exception for a questionmark. This is used to "hide" some number. So we don't want to format it.
	if (useableNumber == '?') {
		return useableNumber;
	}

	//remove any floating zeros produced by the javaScript floating point precision problem
	if(useableNumber != null && typeof useableNumber !== 'undefined'){
		useableNumber = +useableNumber; //cast to integer
		//add precision zeros up to 12 places then remove them by the parseFloat function
		useableNumber = parseFloat(useableNumber.toPrecision(12));
	}
	this.sign = this.getSign( useableNumber);


	var absNumber =  Math.abs(useableNumber);

	if (typeof this.threshold != 'undefined' && this.threshold != null && this.threshold !== false) {
		absNumber = shortBigNumber(absNumber, this.threshold, this.digits);
	}

	//if fractionSize is passed add (or round the number to the) given decimals after floating point.
	if(typeof this.fractionSize != 'undefined' && this.fractionSize != null && this.fractionSize !== false){
		absNumber = absNumber.toFixed(this.fractionSize);
	}
	if (styleClass != null) {
		absNumber = '<span class="'+styleClass+'">' + absNumber + '</span>';
	}
	var finalNumberString = this.config.OVERRIDE_LTR + absNumber + this.config.OVERRIDE_STOP;
	var directionOverride = (this.directions['numberDirection'] === this.config.LTR ? this.config.OVERRIDE_LTR : this.config.OVERRIDE_RTL);
	return this.strongDirectionMark + directionOverride + this.sign + finalNumberString + this.unitKey + this.config.OVERRIDE_STOP;
};

BidiHelper.prototype._createBidiCoordinates = function() {
	var directionOverride = ((this.directions['surroundingDirection'] === this.config.LTR) ? this.config.OVERRIDE_LTR : this.config.OVERRIDE_RTL);
	var parenthesis = {
		left : this.strongDirectionMark + directionOverride + '(' + this.config.OVERRIDE_STOP,
		right : this.strongDirectionMark + directionOverride + ')' + this.config.OVERRIDE_STOP
	};

	var xCoordinate = '<span class="coordinateX">' + parenthesis.left + this._createBidiNumber(this.xCoord) + '</span>';
	var yCoordinate = '<span class="coordinateY">' + this._createBidiNumber(this.yCoord) + parenthesis.right + '</span>';
	var pipeCoordinate = '<span class="coordinatePipe">|</span>';

	return this.strongDirectionMark + directionOverride + xCoordinate + pipeCoordinate + yCoordinate + this.config.OVERRIDE_STOP;
};

/**
 * gets the sign if showSign is true it will show the positive aswell
 * @returns {string}
 */
BidiHelper.prototype.getSign = function getSign( number) {
	var sign = '';

	if (number != 0 && typeof number != 'undefined') {
		if (this.showSign) {
			sign = (number < 0 ? this.signs['negative'] : this.signs['positive']);
		} else {
			sign = (number < 0 ? this.signs['negative'] : '');
		}
	}
	return sign;
};

/**
 * gets from the unit name (e.g. percent) the unitKey given
 * @returns {string}
 */
BidiHelper.prototype.getUnit = function getUnit() {
	var unitKey = '';
	if (this.units.hasOwnProperty(this.unit)) {
		//The passed unit is one of the predefined unites
		unitKey = this.units[this.unit];
	} else if (typeof (GameLobby.txt[this.unit]) != 'undefined') {
		//The passed unit is a translation key
		unitKey = ' ' + GameLobby.translate(this.unit);
	}
	return unitKey;
};


/**
 * gets the direction marker
 * @param direction
 * @returns {string}
 */
BidiHelper.prototype.getDirectionMark = function getDirectionMark(direction) {
	var directionMark = '';
	if (direction === this.config.LTR) {
		directionMark = this.config.MARK_LTR;
	} else if (direction === this.config.RTL) {
		directionMark = this.config.MARK_RTL;
	}
	return directionMark;
};

BidiHelper.prototype.getBidiNumber = function(){
	var output = this._createBidiNumber();
	return output;
};

BidiHelper.prototype.getBidiCoordinates = function(){
	var output = this._createBidiCoordinates();
	return output;
};

BidiHelper.prototype.getBidiRatio = function(){
	var output = this._createBidiRatio();
	return output;
};