GameLobby.app.service('NameService', ['Socket', function (SocketService) {
	var cache = [];
	var queue = [];
	var myself = this;
	var CHANNEL = 'nameService';

	/**
	 * Wird aufgerufen, wenn Nachrichten über das Netz erhalten
	 *
	 * @param data JSON encodiert
	 */
	myself.receive = function (data) {
		if (data != null) {
			putIdForName(data.userAccountIdentifier, data.name);
		}
	};

	myself.initialize = function () {
		//net.listen(CHANNEL, myself.receive);
	};

	myself.getIdForName = function (name, callback) {
		var name = name.toLowerCase();
		if (typeof(cache[name]) != 'undefined') {
			callback(cache[name]);
		}
		else {
			var query = askForId(name, callback);
			if (query != null) {
				SocketService.emit(CHANNEL, query, myself.receive);
			}
		}
		return null;
	};

	myself.getNameForId = function (id, callback) {
		if (typeof(cache[id]) != 'undefined') {
			callback(cache[id]);
		}
		else {
			var query = askForName(id, callback);
			if (query != null) {
				SocketService.emit(CHANNEL, query, myself.receive);
			}
		}
		return null;
	};

	function askForName (id, callback) {
		if (typeof(queue[id]) == 'undefined') {
			queue[id] = [];
			queue[id].push({callback: callback});
			return {'type': 'askForName', 'query': id};
		}
		else {
			//Es ist schon eine Anfrage unterwegs, queue einfach den Callback
			queue[id].push({callback: callback});
			return null;
		}
	}

	function askForId (name, callback) {
		if (typeof(queue[name]) == 'undefined') {
			queue[name] = [];
			queue[name].push({callback: callback});
			return {'type': 'askForId', 'query': name};
		}
		else {
			//Es ist schon eine Anfrage unterwegs, queue einfach den Callback
			queue[name].push({'callback': callback});
			return null;
		}
	}

	function putIdForName (id, name) {
		if (typeof(cache[id]) == 'undefined') cache[id] = name;
		if (typeof(queue[id]) != 'undefined') {
			for (var i = 0; i < queue[id].length; i++) {
				var d = queue[id][i];
				d.callback(name);
				queue[id].splice(i, 1);
				i--;
			}
		}
		var name = name.toLowerCase();
		if (typeof(cache[name]) == 'undefined') cache[name] = id;
		if (typeof(queue[name]) != 'undefined') {
			for (var i = 0; i < queue[name].length; i++) {
				var d = queue[name][i];
				d.callback(id);
				queue[name].splice(i, 1);
				i--;
			}
		}
	}

	myself.initialize();
}]);
