GameLobby.app.service('Request', ['$http', '$state', 'GlobalState', function ($http, $state, GlobalState) {

	"use strict";

	function Request (url, data, successCallback, errorCallback, session) {
		var urlParts = url.split('/');
		var controller = urlParts[0];
		var action = urlParts[1];

		if (typeof data === 'undefined') {
			data = {};
		}

		function callbackWrapper (data) {
			if (typeof data['response'] === 'undefined' || (data['error'] && data['error']['message'])) {
				// error
				if (typeof errorCallback !== 'undefined') {
					errorCallback(data['error']);
				}
			} else
				if (typeof data['response']['errors'] !== 'undefined') {
					// error
					if (typeof errorCallback !== 'undefined') {
						errorCallback(data['response']['errors']['0']);
					}
				}

			if (typeof successCallback === 'function' &&
				typeof data['response'] !== 'undefined' &&
				typeof data['response']['errors'] === 'undefined' &&
				typeof data['error'] === 'undefined') {
				successCallback(data['response']);
			}

			GlobalState.handleResponse(data);
		}

		var getQuery = function (controller, action, params) {
			var query = {};
			query['controller'] = controller;
			query['action'] = action;
			query['params'] = params;

			if (session && typeof session !== 'undefined') {
				query['session'] = session.sessionId;
			}

			return query;
		};

		$http.post(GameLobby.config.backendUrl, getQuery(controller, action, data)).success(callbackWrapper).error(callbackWrapper);
	}

	function RequestFactory () {
		this.session = null;
	}

	RequestFactory.prototype.create = function (url, data, successCallback, errorCallback) {
		if (!url) throw 'Request: url is not optional';
		data = data || {};
		successCallback = successCallback || function () {
		};
		errorCallback = errorCallback || function () {
		};

		return new Request(url, data, successCallback, errorCallback, this.session);
	};

	RequestFactory.prototype.setSession = function (session) {
		this.session = session;
	};

	var requestFactory = new RequestFactory();

	GameLobby.request = requestFactory;
	return requestFactory;

}]);