GameLobby.app.service('WindowService', ['$rootScope', function ($rootScope) {

	function Window (_id, data, ws) {
		this.onClose = function () {};
		$.extend(this, data);
		this._windowId = _id;
		this._windowService = ws;
	}

	Window.prototype = {
		close: function () {
			this.onClose({});
			this._windowService.closeWindow(this._windowId);
		},
		accept: function () {
			this.onClose({
				confirmed: true
			});
			this._windowService.closeWindow(this._windowId);
		},
		decline: function () {
			this.onClose({
				confirmed: false
			});
			this._windowService.closeWindow(this._windowId);
		}
	};

	function WindowService () {
		this.modals = {};
		this.openWindows = 0;
	}

	WindowService.prototype = {
		open: function (data) {
			var _windowId = uniqid();
			this.modals[_windowId] = new Window(_windowId, data, this);
			this.openWindows++;
		},
		getModals: function () {
			return this.modals;
		},
		closeWindow: function (_windowId) {
			this.modals[_windowId] = null;
			this.openWindows--;
		}
	};

	var ws = new WindowService();

	$rootScope.openModal = function (data) {
		ws.open(data);
	};

	return ws;

}]);