/**
 *    Global translation directive.
 *    Matches keys in GameLobby.txt object dynamically and replaces variables in the matched values
 *
 *    Usage examples:
 *
 *    Basic : GameLobby.txt['level'] = 'level';
 *        <span translate >level</span>
 *        <translate>level</translate>
 *
 *    Dynamic key name : GameLobby.txt['Building_15'] = 'Hauptgebäude';
 *    Note: Single data items can be passed as strings . See bellow for the format for passing multiple data items
 *        <span translate options="{{building.data.buildingType}}">Building_?</span>
 *        <translate options="{{building.data.buildingType}}">Building_?</translate>
 *
 *    Both options and data accept multiple comma separated values :
 *    GameLobby.txt['upgradeBuildingFromLevelToNext'] = 'Upgrade building from level {{currLevel}} to level {{nextLevel}}';
 *    Important notes:    if multiple, data items must also be named. key1:val1, key2:val2 .
 *                        options need to appear in the exact order that they are used.
 *    <h5 translate data="currLevel:{{building.data.lvl}},nextLevel:{{building.data.lvlNext}}">upgradeBuildingFromLevelToNext</h5>
 *
 *        and / or
 *
 *        GameLobby.txt['foo_1_Bar_2] = 'text for Foo = 1 and Bar = 2';
 *        <translate options="{{1}},{{2}}" >foo_?_Bar_?</translate>
 *
 *    Usage with custom filters, Eg. internationalised date is the same as above.
 *    Only diference is in the lang.js file where  this is supported : GameLobby.txt['atTime'] = 'um {{atTime|i18ndt}} Uhr';
 *    See htmlFilters.js for: GameLobby.htmlFilters['i18ndt'] = '<span i18ndt="{{_?_}}" format="mediumTime"></span>';
 *    <translate data="atTime:{{header.time}}">atTime</translate>
 *
 * @param $compile
 * @returns {{scope: {data: string, options: string}, link: Function}}
 */
/** @expose */
GameLobby.app.directive('translate', ['$compile', '$rootScope', function ($compile, $rootScope) {
	return {
		restrict: 'EA',
		scope: {
			'dataobject': '='
		},
		link: function (scope, elem, attrs) {
			var elText = elem.text();
			var elementContentsScope = null;
			$rootScope.$broadcast('startTranslate');

			//Both "data" and "options" are OPTIONAL with translate directive.
			//Only multiObserve for attrs that ARE used in this instance.
			var observables = [];
			var limitTo = 0;
			if ('data' in attrs && attrs['data'] != '') observables.push('data');
			if ('options' in attrs) observables.push('options');
			if ('limit' in attrs) {
				limitTo = +attrs['limit'];
			}

			if (observables.length > 0) {
				multiObserve(attrs, observables, handleOptionsAndData);
			}
			else {
				//Nothing to observe, no relevant attrs found
				handleOptionsAndData();
			}
			scope.$on('newTranslation', handleOptionsAndData);

			scope.$watch('dataobject', function (newDataObject) {
				if (newDataObject && typeof(newDataObject) == 'object') {
					$.each(newDataObject, function (key, value) {
						scope[key] = value;
					});
				}
			});

			function handleOptionsAndData () {
				if (typeof attrs['data'] != 'undefined') {
					var dataArr = attrs['data'].split(',');

					var lastVarName = "";
					$.each(dataArr, function (i, v) {
						var tmpStr = v.trim();
						var colonPos = tmpStr.indexOf(':');
						if (colonPos > 0) {
							lastVarName = tmpStr.substr(0, colonPos);
							scope[lastVarName] = tmpStr.substr(colonPos + 1).trim();
						} else {
							// No colon? This has to mean that we eleminated a comma that was part of a string
							scope[lastVarName] = scope[lastVarName] + "," + tmpStr;
						}
					});
				}

				if (typeof attrs['options'] != 'undefined') {
					var tplSlotsArr = elText.match(/\?/g);
					var optionsArr = attrs['options'].split(',');

					//check that numer of declared options is equal to
					// the number of slots available in the key name
					if (tplSlotsArr != null && tplSlotsArr.length != optionsArr.length) return;

					//fill slots with options and generate the dynamic key name: elTextNew
					var elTextNew = elText;

					for (var i = 0; i <= optionsArr.length; i++) {
						elTextNew = elTextNew.replace('?', optionsArr[0].trim());
						optionsArr.shift();
					}
					compile($.trim(elTextNew));
				}
				else //No options were declared
				{
					//Halt if options should have been declared
					if (elText.match(/\?/g) != null) return;

					compile($.trim(elText));
				}
			}

			function compile (key) {
				var translatedTpl = window['GameLobby'].txt[key];

				if (typeof translatedTpl == 'undefined') {
					elem.html('!!' + key + '!!');
					return;
				}
				if (GameLobby.config['showLangKeys']) {
					elem.html(key);
					return;
				}

				/**
				 * Before the actual compilation faze parse the translatedTemplate for any occurrences
				 * of custom GameLobby htmlFilters, i.e. directives or other snippets of code to be injected
				 * before the compilation
				 */

				translatedTpl = bbCodeParser(translatedTpl);
				//if there are expressions inside the translated template AND at least one filter
				if (translatedTpl.indexOf('{{') > -1 && translatedTpl.indexOf('}}') > -1 && translatedTpl.indexOf('|') > -1) {
					/**
					 * find every occurrence of {{|}}. This denotes a custom htmlFilter
					 */


					function getIndicesOf (searchStr, str) {
						var startIndex = 0, searchStrLen = searchStr.length;
						var index, indices = [];

						while ((index = str.indexOf(searchStr, startIndex)) > -1) {
							indices.push(index);
							startIndex = index + searchStrLen;
						}
						return indices;
					}

					var indicesOpen = getIndicesOf("{{", translatedTpl);
					var indicesClose = getIndicesOf("}}", translatedTpl);

					//stop if not all expressions are properly formed
					if (indicesOpen.length != indicesClose.length) return;

					//holds data objects that contain html injection info
					// replaceable : injectable = string to be replaced : string to be injected
					//will remain empty if no actual custom filters are found bellow
					var injectArr = [];

					//build expression strings
					$.each(indicesOpen, function (i, v) {
						var startIndex = v;
						var endIndex = indicesClose[i];
						var expression = translatedTpl.substr(startIndex, endIndex + 2 - startIndex);

						var exprArr = expression.replace('{{', '').replace('}}', '').split('|');

						//HERE is where we actually tell if this particular expression IS a custom filter
						if (exprArr.length <= 1) return; //not a filterable expresion, no html injection will occur

						var variableNameStrTemp = exprArr.splice(0, 1);
						var variableNameStr = variableNameStrTemp[0];

						//hold the variable names in Array.
						var variableNameArr = variableNameStr.split(',');

						//Remaining elemnts in array are the filters. In theory any number can be applied.
						//Currently only one is tested for & supported
						// for each requested filter, find the appropriate rule.
						// if one rule is missing, throw the whole expression away
						$.each(exprArr, function (i, v) {
							var obj = {'replaceable': translatedTpl.substr(startIndex, expression.length)};

							if (typeof GameLobby.htmlFilters[v] == 'undefined') {
								obj.injectable = '';
							}
							else {
								//we found an HTML filter match
								if (typeof GameLobby.htmlFilters[v] == 'string') {
									var replacementCount = 0;
									var replacementArr = GameLobby.htmlFilters[v].match(/_\?_/g);
									if (replacementArr) {
										replacementCount = replacementArr.length;
									}
									obj.injectable = GameLobby.htmlFilters[v];
									//iterate through detected variable names and insert at right locations in template
									for (var i = 0; i < replacementCount; i++) {
										obj.injectable = obj.injectable.replace('_?_', (variableNameArr[i] || '').trim());
									}

								}
								//more complex object, do more detection
								// DEPRECATED
								else
									if (typeof GameLobby.htmlFilters[v] == 'object') {
										if (GameLobby.htmlFilters[v]['tag']) //we want to wrap the variable value in a tag. (Eg. <b>)
										{
											var tag = GameLobby.htmlFilters[v]['tag'];
											obj.injectable = '<' + tag + '>{{' + variableNameArr[0] + '}}</' + tag + '>';
										}
									}
							}
							injectArr.push(obj);
						});

					});

					//inject the html snippets at the right locations
					$.each(injectArr, function (i, obj) {
						translatedTpl = translatedTpl.replace(obj.replaceable, obj.injectable);
					});
				}
				//And finally compile
				if (limitTo) {
					translatedTpl = translatedTpl.slice(0, limitTo);
				}

				elem.html(translatedTpl);
				elementContentsScope = scope.$new();
				$compile(elem.contents())(elementContentsScope);
				$rootScope.$broadcast('endTranslate');

			}

			scope.$on('$destroy', function () {
				if (elementContentsScope !== null) {
					elementContentsScope.$destroy();
				}
				elem.remove();
			});
		}
	}
}]);