GameLobby.app.service('SessionService', ['Request', '$state', 'Socket', '$rootScope', '$cookieStore', '$timeout', function (Request, $state, SocketService, $rootScope, $cookieStore, $timeout) {

	"use strict";

	var userCookieName = 'gl5SessionKey';
	var userPlayerId = 'gl5PlayerId';
	var sessionService = null;

	var loginListeners = [];
	var loginFinished = false;
	var session = null;

	$rootScope.loggedIn = false;

	function SessionService() {
		this.session = null;
	}

	var finishLogin = function () {
		if(typeof GlobalLoadingScreenManager != 'undefined') {
			GlobalLoadingScreenManager['achieveStep']('get_all');
		}
		var sessionData = sessionService.getSession();

		GameLobby.Player = Player.get(sessionData.playerId);
		$rootScope['Player'] = GameLobby.Player;

		//Set the mellon langaugeId config
		session = Session.get(sessionData.sessionId);
		session.subscribe(function () {
			GameLobby.config.mellon.application.languageId = GameLobby.config.languages[session['data']['country']];
			$rootScope.$broadcast('login');
			$rootScope.loggedIn = true;

			if(!loginFinished) {
				window.setTimeout(function() {
					loginFinished = true;
					for (var i=0; i<loginListeners.length; i++) {
						loginListeners[i]();
					}
					loginListeners = [];
				});
			}
		});
		requestAngularApply();
	};

	SessionService.prototype.isAdmin = function (fct) {
		if(session != null && session['data']['type'] == Session.TYPE_ADMIN) {
			return true;
		}
		return false;
	};


	SessionService.prototype.onLogin = function (fct) {
		if (loginFinished) {
			fct();
		} else {
			loginListeners.push(fct);
		}
	};

	SessionService.prototype.storeSession = function (session) {
		this.session = session;

		$cookieStore.put(userCookieName, {
			key: this.session.sessionId
		});
		$cookieStore.put(userPlayerId, {
			key: this.session.playerId
		});
	};

	SessionService.prototype.clearSession = function () {
		GameLobby.Player = null;
		$rootScope.Player = null;
		$cookieStore.remove(userCookieName);
		$cookieStore.remove(userPlayerId);

		if(!this.session) {
			Request.create(
				'login/logout',
				{},function() {
					window.location = GameLobby.config.redirectAfterLogout;
				});
			return;
		}
		session = Session.get(this.session.sessionId);
		var country = session['data']['country'];
		this.session = null;

		Request.create(
			'login/logout',
			{},
			function () {
				session.subscribe(function () {
					var country = session['data']['country'];
					if(country == 'en') {
						country = 'com';
					}
					window.location = GameLobby.config.redirectAfterLogout.replace("#",country+"#");
				});
			}
		);
	};

	SessionService.prototype.getSession = function () {
		return this.session;
	};

	SessionService.prototype.hasValidSession = function () {
		var session;
		var cookieSession = $cookieStore.get(userCookieName);
		var playerId = $cookieStore.get(userPlayerId);

		if(GameLobby.globals['sessionId']) {
			// from backend
			session = {
				sessionId: GameLobby.globals['sessionId'],
				playerId: GameLobby.globals['playerId']
			}
		} else if (cookieSession && cookieSession.key && cookieSession.id && playerId) {
			// cookie
			session = {
				sessionId: cookieSession.key,
				playerId: playerId
			}
		}
		this.session = session;
		Request.setSession(session);

		return this.session ? true : false;
	};

	SessionService.prototype.startSocketConnection = function startSocketConnection() {
		SocketService.setSessionData(this.getSession());
	};

	SessionService.prototype.init = function () {
		if (this.hasValidSession()) {
			Request.setSession(this.session);
			this.startSocketConnection();
			Request.create('player/getAll', {}, finishLogin);
		} else {
			//No valid session at all? Logout!
			sessionService.logout();
		}
	};

	SessionService.prototype.logout = function () {
		$rootScope.loggedIn = false;
		Cache.c = {};
		this.clearSession();
	};

	sessionService = new SessionService();

	$rootScope.$on('forceLogout', function () {
		sessionService.logout();
	});

	return sessionService;

}]);
