/**
 * Creates a touch-friendly dropdown list that can be styled in every way.
 * The body of this drop down is part of the html body element and is NOT a child of this directive.
 * (That is why we create it in here. The reason for that is, that it should be able to overlap every container)
 * You can set the template for rendering every entry simply by writing it inside the directive HTML.
 * <dropdown data="">This is the template. Option: {{option.name}}</dropdown>
 * So the data for earch entry comes in the variable "option".
 *
 * This directive expects one big configuration object that is given via the "data" attribute.
 * data = {
 * 		'placeholder': 'someTranslationKey',		// Placeholder
 *		'resize': false,							// If set to true, it matches the width of the header and the body, needs lazyRendering turned off
 *		'maxHeight': 200,							// Maximum height of the dropdown in pixels. If there are more entries, it will scroll.
 *		'bodyClass': 'someClassName',				// Class name for the dropdownBody element
 *		'selected': null,							// Holds the key of the selected entry. Remember: This is a two-directional binding!
 *		'disable'									// If true, dropdown is disabled
 *		'disabledEntries': [],						// Array of keys of the entries, which should get disabled (css class, no hover, not clickable)
 *		'callback': function(){},					// Callback that is called when one element was selected. Gets the key as the arguments.
 *		'options': {},								// object (or array) that holds all the key->value pairs that should be displayed
 *		'selectBy': 'option.key',					// Per default the keys of the options object are the unique keys. If you don't want that, you can specify where to find them.
 *		'orderBy': 'key'							// Only works when options is an array! The entries are then ordered by the given key (property name in each entry)
 *		'lazyRendering': false						// If this is set to true the dropdown body element will be rendered when it's first used. This only makes sense when
 *													// combined with resize=false because the size of the head can only be determined when we render the body
 *		'hoverable': false							// dropdown toggles by hover, instead of click
 *		'hoverContainer': 'key'						// css id for container, which fires mouseevents
 *		'pinable': false							// dropdown can be pinned opened, it will not toggle on hover
 *		'pinId': ''									// css Id for pin
 *		'stayAtClickOpen': false					// should the dropdown close after select an element?
 *		'noTruncate': false							// The selected item will be shown truncated in the header or nor
 *		'appendTo': ''								// String: html element selector to append the dropdown body element to.
 *		'disregardPageScroll': false				// Bool: when true the dropdown body will show up on a fixed position in spite of the scroll on page (useful when the dropdown is used in a model (fixed) dialog)
 * }
 *
 * You don't have to set anything else but the options. If you don't specify a template, the values of the options are
 * supposed to be pure HTML (or text).
 *
 */

/** @expose */
GameLobby.app.directive('dropdown', ['$window', '$compile', '$timeout', function ($window, $compile, $timeout) {
	return  {
		replace: true,
		scope: {
			'data': '=',
			'loggedIn': '='
		},
		restrict: 'EA',
		template: '<div class="dropdownContainer">' +
			'<div class="dropdownHead openDownwards closed" ng-click="clickDropdown()">' +
			'<span class="selectedItem truncated"></span>' +
			'</div>' +
			'<input class="dropdownInput" type="text">' +
			'</div>',
		// Let the user define a template for every entry in this dropdown by setting it inside of the directive
		transclude: 'element',
		compile: function compile (tElement, tAttrs, transclude) {
			return {
				// Before do anything we need to fetch the HTML that was inside this HTML
				pre: function (scope) {
					transclude(scope, function (clone) {
						scope['template'] = clone.html();
						//we use 'manual' DOM generation below and then call $compile. This prevents Angular from correctly
						//disposing of the cloned DOM elements and results in a memory leak
						scope.$on('$destroy', function () {
							clone.remove();
						});
					});
				},
				// This is the normal linking function.
				post: function (scope, elem, attrs) {
					var isExpanded = false;
					var value2index = {};
					var index2value = [];
					var selectedIndex = -1;
					var bodyElement = null;
					var rendered = false;
					var pinned = false;
					var htmlBody = $('body')[0];
					var headerElement = elem.find('.dropdownHead');
					var hoverContainer = null;
					var pinElement = null;

					// Function that checks if the dropdown should lose focus
					function globalMouseUp () {
						if (!bodyElement) {
							return false;
						}
						var status = PointerHelper.getStatus();
						var obj = status.currentObj;
						var inBody = bodyElement.find(obj).length || bodyElement[0] == obj;
						var inParent = elem.find(obj).length || elem[0] == obj;

						// click within hoverContainer is ok
						var inHoverContainer = 0;
						if (scope['data']['hoverContainer'] && typeof hoverContainer != 'undefined') {
							inHoverContainer = hoverContainer.find(obj).length || hoverContainer[0] == obj;
						}

						// in case its not pinable, its always true
						var inPin = 0;
						if (scope['data']['pinable'] && typeof pinElement != 'undefined') {
							inPin = pinElement.find(obj).length || pinElement[0] == obj;
						}

						if (!inBody && !inParent && !inHoverContainer && !inPin && !pinned) {
							dropdownToggle(false);
						}
					}

					function selectNextItem (direction) {
						var newKey = null;
						var newIndex = selectedIndex;
						// Skip group headers
						do {
							newKey = index2value[newIndex + direction];
							newIndex = newIndex + direction;
						} while (newKey === '');
						// No more entries below the selected one?
						if (typeof(newKey) == 'undefined') {
							return;
						}
						selectItem(newKey, true);
					}

					elem.find('input').focus(function () {
						elem.find('.dropdownHead').addClass('focus');
					});

					elem.find('input').focusout(function () {
						elem.find('.dropdownHead').removeClass('focus');
					});

					elem.find('input').keyup(function (evt) {
						var code = evt.keyCode;
						// Key down?
						if (code == 40) {
							selectNextItem(1);
						}
						// Key Up?
						if (code == 38) {
							selectNextItem(-1);
						}
						// Enter?
						if (code == 13) {
							dropdownToggle();
						}
					});

					var visible = false;

					function changeVisibility () {
						if (scope['data']['hoverable']) {

							if (visible) {
								PointerHelper.onPointerOver(pinElement[0], show);
								PointerHelper.onPointerOut(pinElement[0], hide);
							}
							else {
								PointerHelper.removeBindings(pinElement[0], 'onmouseover');
								PointerHelper.removeBindings(pinElement[0], 'touchOver');
								PointerHelper.removeBindings(pinElement[0], 'onmouseout');
								PointerHelper.removeBindings(pinElement[0], 'touchOut');
							}

							dropdownToggle(visible);
						}
					}

					function show () {
						if (!pinned) {
							visible = true;
							$timeout(changeVisibility, 10);
						}
					}

					function hide () {
						if ((!pinned) && (isExpanded)) {
							visible = false;
							$timeout(changeVisibility, 10);
						}
					}
					if (scope['data']['id']) {
						elem.attr('id',scope['data']['id']);
					}

					var globalUnlistener1 = PointerHelper.addGlobalCallback('up', globalMouseUp);
					var globalUnlistener2 = PointerHelper.addGlobalCallback('down', globalMouseUp);
					if (scope['data']['hoverable']) {

						hoverContainer = $('#' + scope['data']['hoverContainer']);
						if (typeof hoverContainer[0] != 'undefined') {
							PointerHelper.onPointerOver(hoverContainer[0], show);
							PointerHelper.onPointerOut(hoverContainer[0], hide);
						}
						else {
							PointerHelper.onPointerOver(headerElement[0], show);
							PointerHelper.onPointerOut(headerElement[0], hide);
						}
					}

					function calculateBodyPosition () {
						var offsetY, offsetX, totalHeight;
						// display the body element for a short while to calculate the height
						var oldBodyDisplay = bodyElement.css('display');
						bodyElement.css('display', 'block');
						// Position the body under or on top of the header line
						var boundingClientRect = elem[0].getBoundingClientRect();
						if (typeof($window.pageYOffset) != 'undefined') {
							offsetY = $window.pageYOffset;
							offsetX = $window.pageXOffset;
							totalHeight = $window.outerHeight;
						}
						else {
							offsetY = document.body.scrollTop;
							offsetX = document.body.scrollLeft;
							totalHeight = $(window).height();
						}
						//if the appendTo option is provided, set the provided element as the reference element instead of the html body.
						if (typeof(scope['data']['appendTo']) != "undefined"){
							referenceRec = $(scope['data']['appendTo'])[0].getBoundingClientRect();
						} else {
							var referenceRec = $(htmlBody)[0].getBoundingClientRect();
						}
						//if the disregardPageScroll option is true, ignore (reset) the offset variables
						if (typeof(scope['data']['disregardPageScroll']) != "undefined" && scope['data']['disregardPageScroll']==true){
							offsetY = 0;
							offsetX = 0;
						}
						bodyElement.css('left', (boundingClientRect.left - referenceRec.left + offsetX) + "px");
						// Get height of content, and outer height of surrounding box
						bodyElement.height(0);
						var height = bodyElement.find('.scrollContentInnerWrapper').outerHeight(true);
						var outerHeightAddition = bodyElement.outerHeight(true);

						// Where do we have more space?
						var startOfParentElement = boundingClientRect.top + offsetY;
						var spaceBelow = totalHeight - startOfParentElement - elem.outerHeight(true);
						var spaceAbove = startOfParentElement;
						var openBelow = true;
						if (spaceAbove > spaceBelow) {
							openBelow = false;
						}

						// header and body have to know in which direction the dropdown opens
						var dropdownHeader = elem.find('.dropdownHead');
						if (openBelow) {
							dropdownHeader.addClass('openDownwards');
							bodyElement.addClass('openDownwards');
							dropdownHeader.removeClass('openUpwards');
							bodyElement.removeClass('openUpwards');
						} else {
							dropdownHeader.addClass('openUpwards');
							bodyElement.addClass('openUpwards');
							dropdownHeader.removeClass('openDownwards');
							bodyElement.removeClass('openDownwards');
						}

						// Never let the dropdown be higher than 200px (default)
						var maxHeight = 200;
						if (typeof(scope['data']['maxHeight']) != 'undefined') {
							maxHeight = scope['data']['maxHeight'];
						}
						if (height > maxHeight) {
							height = maxHeight;
						}

						if (openBelow) {
							bodyElement.css('top', (startOfParentElement - referenceRec.top + elem.outerHeight(true)) + 'px');
						}
						else {
							bodyElement.css('top', (startOfParentElement - referenceRec.top - height - outerHeightAddition + 'px'));
						}
						bodyElement.css('height', height + 'px');
						if (typeof(scope['data']['bodyStyle']) != 'undefined') {
							bodyElement.css(scope['data']['bodyStyle']);
						}

						// And now match the width of body and header (if this is enabled)
						if ((typeof(scope['data']['resize']) != 'undefined') && scope['data']['resize'] === true) {
							if ((typeof(scope['scrollbarApi']) != 'undefined') && (typeof(scope['scrollbarApi']['update']) != 'undefined')) {
								scope['scrollbarApi']['update']();
							}

							var dropDownOuterWidth = Math.max(headerElement.outerWidth(), bodyElement.outerWidth());
							var dropDownInnerWidth = dropDownOuterWidth - (bodyElement.outerWidth() - bodyElement.innerWidth());
							headerElement.css('width', dropDownOuterWidth + 'px');
							bodyElement.css('width', dropDownInnerWidth + 'px');
							bodyElement.find('ul').css('width', dropDownInnerWidth + 'px');
						}

						bodyElement.css('display', oldBodyDisplay);
					}

					scope['clickDropdown'] = function () {
						if (!scope['data']['hoverable']) {
							dropdownToggle();
						}
					};

					scope['pinDropdown'] = function () {
						// toggle state
						pinned = !pinned;
						scope['pinned'] = pinned;
						pinElement.toggleClass('pinned');
						dropdownToggle();

						PointerHelper.removeBindings(pinElement[0], 'onmouseover');
						PointerHelper.removeBindings(pinElement[0], 'touchOver');
						PointerHelper.removeBindings(pinElement[0], 'onmouseout');
						PointerHelper.removeBindings(pinElement[0], 'touchOut');
					};

					function dropdownToggle (forceTo) {
						if (!rendered && scope['data']['lazyRendering'] === true) {
							return renderBody(true);
						}
						if (bodyElement == null) {
							return;
						}
						if (arguments.length > 0) {
							isExpanded = !forceTo;
						}

						var dropdownHeader = elem.find('.dropdownHead');

						if (isExpanded) {
							if (!pinned) {
								//only close onclick if its not pinned
								isExpanded = false;
								bodyElement.css('display', 'none');
								dropdownHeader.addClass('closed');
								dropdownHeader.removeClass('opened');
							}
						}
						else {
							isExpanded = true;
							bodyElement.css('display', 'block');
							dropdownHeader.addClass('opened');
							dropdownHeader.removeClass('closed');
							calculateBodyPosition();

							// Focus on the hidden input field to capture cursor movements
							if (PointerHelper.getPointerType() != 'touch') {
								elem.find('input').focus();
							}
						}
					}

					function updateSelectedItem () {
						if (bodyElement == null) {
							// Only update the header. This is always there
							elem.find('.selectedItem').text(value2index[scope['data']['selected']]);
							return;
						}
						// Update the header line and grey it out when nothing is selected
						var key = scope['data']['selected'];
						if (scope['data']['selected'] === null) {
							elem.find('.selectedItem').addClass('empty');
							if (typeof(scope['data']['placeholder']) != "undefined") {
								elem.find('.selectedItem').text(GameLobby.translate(scope['data']['placeholder']));
							}
							selectedIndex = -1;
						}
						else {
							elem.find('.selectedItem').removeClass('empty');
							var index = value2index[scope['data']['selected']];
							if (typeof(index) != "undefined" && scope['data']['options'][index]['selectedName']) {
								// Show a custom name in the header
								elem.find('.selectedItem').text(scope['data']['options'][index]['selectedName']);
							} else {
								var html = bodyElement.find('.dropDownItem' + key).html();
								elem.find('.selectedItem').html(html);
							}
							selectedIndex = value2index[key];
						}
						// Add the "selected" class to (only) the selected element
						bodyElement.find('.dropdownOption').removeClass('selected');
						bodyElement.find('.dropDownItem' + key).addClass('selected');
						if (!scope['data']['noTruncate']) {
							elem.find('.selectedItem').addClass('truncated');
						} else {
							elem.find('.selectedItem').removeClass('truncated');
						}
					}

					function updateDisabledEntries () {
						if (bodyElement == null || typeof scope['data']['disabledEntries'] == 'undefined') {
							return;
						}

						bodyElement.find('.dropdownOption').removeClass('disable');

						$.each(scope['data']['disabledEntries'], function (i, key) {
							bodyElement.find('.dropdownOption' + key).addClass('disable');
						});
					}

					function updateDisabledState () {
						if (typeof scope['data']['disabled'] != 'undefined') {
							elem.find('input').prop('disabled', scope['data']['disabled']);

							// set class to disable the click
							if (scope['data']['disabled']) {
								elem.find('.dropdownHead').addClass('disabled');
								// close dropdown
								dropdownToggle(false);
							}
							else {
								elem.find('.dropdownHead').removeClass('disabled');
							}
						}
					}

					function selectItem (key, dontClose) {
						if ((key !== '') && (typeof(key) != 'undefined') && (key !== null)) {
							// Only select item, if it is not disabled
							if ($.inArray(key, scope['data']['disabledEntries']) > -1) {
								return;
							}

							scope['data']['selected'] = key;
							if (!dontClose && !scope['data']['stayAtClickOpen'] && !pinned) {
								dropdownToggle(false);
							}
							updateSelectedItem();
							selectedIndex = value2index[key];
						}
						if (typeof(scope['data']['callback']) == 'function') {
							scope['data']['callback'](key);
						}
					}

					scope['selectItem'] = selectItem;

					var unwatchSelected = scope.$watch('data.selected', updateSelectedItem);
					var unwatchDisabledEntries = scope.$watch('data.disabledEntries', updateDisabledEntries);
					var unwatchDisabledState = scope.$watch('data.disabled', updateDisabledState);

					if (typeof(scope['data']['placeholder']) != "undefined") {
						elem.find('.selectedItem').text(GameLobby.translate(scope['data']['placeholder']));
					}
					else {
						elem.find('.selectedItem').html('&nbsp;');
					}

					function renderHeader () {

						headerElement = elem.find('.dropdownHead');
						headerElement.addClass('closed');
						//elem.find('.selectedItem').text(value2index[scope['data']['selected']]);

						if (typeof scope['data']['pinable'] != 'undefined' && scope['data']['pinable']) {
							// but only if not there already
							if (typeof $(htmlBody).find('.pin')[0] == 'undefined') {
								pinElement = $('<div ng-click="pinDropdown()" tooltip tooltip-placement="after" tooltip-url="tpl/mainlayout/partials/villageListPinTooltip.html" ng-show="loggedIn">' +
									'<i></i>' +
									'<div class="ending"></div>' +
									'</div>');
								pinElement.attr('id', scope['data']['pinId']);
								pinElement.addClass('pin');
								$compile(pinElement)(scope);
								$(htmlBody).append(pinElement);
							}
						}
					}

					function renderBody (showAfterRender) {

						showAfterRender = showAfterRender || false;
						rendered = true;

						// create dropdown body element
						bodyElement = $('<div scrollable>');
						bodyElement.addClass('dropdownBody');
						bodyElement.addClass(scope['data']['bodyClass']);
						if (scope['data']['hoverable']) {
							PointerHelper.onPointerOver(bodyElement[0], show);
							PointerHelper.onPointerOut(bodyElement[0], hide);
						}
						// add dropdown body to document body
						if (typeof(scope['data']['appendTo']) != "undefined"){
							$(scope['data']['appendTo']).append(bodyElement);
						} else {
							$(htmlBody).append(bodyElement);
						}

						// hide the body element
						dropdownToggle(false);

						// render the elements
						selectedIndex = -1;

						var selectBy = scope['data']['selectBy'] || 'key';
						var lineHTML = '';

						// "orderBy" only works with arrays. Angular doesn't like the other notation then.
						if (typeof(scope['data']['orderBy']) != 'undefined') {
							lineHTML += '<li ng-repeat="option in data.options | orderBy:\'' + scope['data']['orderBy'] + '\'" ';
						} else {
							lineHTML += '<li ng-repeat="(key, option) in data.options track by $index" ';

						}
						lineHTML += ' class="dropdownOption dropDownItem{{' + selectBy + '}} truncated" ';
						lineHTML += ' ng-class="{selected: \'{{' + selectBy + '}}\' == data.selected, disabledEntries: data.disabledEntries.indexOf(\'{{' + selectBy + '}}\') > -1}" ng-click="selectItem(' + selectBy + ')">';

						// Do we have a special template for this dropdown?
						if (scope['template'] != '') {
							lineHTML += scope['template'] + '</li>';
						} else {
							lineHTML += '{{option}}</li>';
						}
						var innerElement = $('<ul>' + lineHTML + '</ul>');
						bodyElement.html(innerElement);
						var dropdownContentElement = $compile(bodyElement)(scope);

						// Wait until everything is rendered. After that we can initialize the scrollbar again
						$timeout(function () {
							calculateBodyPosition();
							if ((typeof(scope['scrollbarApi']) != 'undefined') && (typeof(scope['scrollbarApi']['updateHeight']) != 'undefined')) {
								scope['scrollbarApi']['updateHeight']();
							}
							updateSelectedItem();
							updateDisabledEntries();
							updateDisabledState();
							dropdownToggle(showAfterRender);
						}, 0);
						updateSelectedItem();
						updateDisabledEntries();
						updateDisabledState();
					}

					var unwatchOptions = scope.$watch('data.options', function () {

						if (scope['data']['static'] === true) {
							unwatchOptions();
						}
						var cnt = 0;
						value2index = {};
						index2value = [];
						for (var key in scope['data']['options']) {
							if (!scope['data']['selectBy']) {
								value2index[key] = cnt;
								index2value[cnt] = key;
							} else {
								var selectBy = scope['data']['selectBy'].substr(7);
								var newKey = scope['data']['options'][key][selectBy];
								value2index[newKey] = cnt;
								index2value[cnt] = newKey;
							}
							cnt++;
						}

						// remove the dropdown body from the reference element
						if (bodyElement != null) {
							if (typeof(scope['data']['appendTo']) != "undefined"){
								$(scope['data']['appendTo'])[0].removeChild(bodyElement[0]);
							}else {
								htmlBody.removeChild(bodyElement[0]);
							}
						}

						renderHeader();

						if (scope['data']['lazyRendering'] !== true) {
							renderBody(isExpanded);
						}
					}, true);

					elem.bind('$destroy', function () {
						globalUnlistener1();
						globalUnlistener2();
						PointerHelper.removeBindings(elem[0]);
						elem.find('input').off();
						unwatchDisabledEntries();
						unwatchDisabledState();
						unwatchOptions();
						unwatchSelected();
						if (headerElement) {
							PointerHelper.removeBindings(headerElement[0]);
						}
						if (typeof scope['data']['hoverContainer'] != 'undefined') {
							PointerHelper.removeBindings(hoverContainer[0]);
						}
						if (bodyElement) {
							PointerHelper.removeBindings(bodyElement[0]);
							bodyElement.remove();
						}
						if (pinElement) {
							PointerHelper.removeBindings(pinElement[0]);
						}
					});
				}
			}
		}
	};
}]);