GameLobby.app.directive('manageSidebar', [function () {
	return {
		restrict: 'A',
		link: function ($scope, element, attrs) {

			var homeButton = $('#home-button');
			var countryButton = $('#country-button');
			var body = $('body');

			enquire.register("screen and (max-width: 1024px)", {
					match: function () {
						$(element).show();

						var menuSidebar = $('#menu-sidebar');

						var css = {};
						var from = getReadingDirectionFrom();
						if (body.hasClass("isLeft"))
						{

							css[from] = -262;
							menuSidebar.css(css);
							css[from] = -131;
							countryButton.css(css);
							css[from] = -262;
							homeButton.css(css);
							css[from] = 10;
							element.css(css);
							body.addClass("isLeft");
						}
						else
						{
							css[from] = 0;
							menuSidebar.css(css);
							css[from] = 0;
							homeButton.css(css);
							css[from] = 131;
							countryButton.css(css);
							css[from] = 272;
							element.css(css);
							body.removeClass("isLeft");
						}
					},
					destroy: function () {}
				}
			);

			enquire.register("screen and (min-width: 1024px)", {
					match: function () {
						$(element).hide();
						countryButton.removeAttr('style');
						homeButton.removeAttr('style');
					},
					destroy: function () {}
				}
			);

			$scope.$on('activateSidebarNavigationOption', function() {
				if ($(element).css('display') == 'block') {
					var menuSidebar = $('#menu-sidebar');
					if (parseInt(menuSidebar.css(getReadingDirectionFrom())) == 0) {
						$(element).trigger('click');
					}
				}
			});

			$(element).on('click', function () {
				var menuSidebar = $('#menu-sidebar');
				var css = {};
				var from = getReadingDirectionFrom();
				var left = parseInt(menuSidebar.css(from));
				if (left < 0) {
					css[from] = 0;
					menuSidebar.css(css);
					css[from] = 131;
					countryButton.css(css);
					css[from] = 0;
					homeButton.css(css);
					css[from] = 272;
					element.css(css);
					body.removeClass("isLeft");
				} else {
					css[from] = -262;
					menuSidebar.css(css);
					css[from] = -131;
					countryButton.css(css);
					css[from] = -262;
					homeButton.css(css);
					css[from] = 10;
					element.css(css);
					body.addClass("isLeft");
				}
			});

			homeButton.on('click', function () {
				$(element).trigger('click');
			});
		}
	}
}]);