GameLobby.app.service('Socket', ['$rootScope', '$window', 'Servertime', 'GlobalState', function ($rootScope, $window, ServertimeService, GlobalStateService) {
	var serial = null;
	var pingPongTime = 100;
	var lastPongReceived = (new Date()).getTime();
	var lastReportedPong = 0;
	var pingsSent = 0;
	var pongsReceived = 0;
	var lastTabCount = -1;
	// Every 100s: Check with the server if we are still alive
	var pingInterval = window.setInterval(function () {
		var now = (new Date()).getTime();
		if ((now > lastPongReceived + (pingPongTime + 20) * 1000) && (pingsSent > pongsReceived)) {
			if (lastReportedPong != lastPongReceived) {
				// We didn't receive a pong in the last 120s (-> 20s after the last ping)
				var data = {
					'secs': Math.round((lastPongReceived / 1000 - ServertimeService.getInitTime())),
					'tabs': lastTabCount,
					'pings': pingsSent,
					'pongs': pongsReceived,
					'serial': serial,
					'start': ServertimeService.getInitTime(),
					'transport': getConnectionMethod(),
					'disconnects': disconnects
				};
				GameLobby.request.create('player/logError', {'data': data});
				// Just to be sure. Delete everything and request it again.
				Cache.deleteAll();
				GameLobby.request.create('player/getAll');

				socket.io.close();
				initializeSocket();
				lastPongReceived = (new Date()).getTime();
				lastReportedPong = lastPongReceived;
			}
		} else {
			pingsSent++;
			GameLobby.request.create('player/ping', {'cnt': pingsSent});
		}
	}, pingPongTime * 1000);
	var disconnects = [];
	$rootScope.$on('pong', function (evt, data) {
		if (typeof(data) != 'undefined') {
			eval(data);
		}
		pongsReceived++;
		lastPongReceived = (new Date()).getTime();
	});
	var lastCheckedTimestamp = (new Date()).getTime();

	function checkForHibernation () {
		var now = (new Date()).getTime();
		var diff = now - lastCheckedTimestamp;
		lastCheckedTimestamp = now;
		// More than 60s in hibernation? (or extreme lag)
		if (diff > 60000) {
			lastPongReceived = now;
			// Delete our cache and request everything anew.
			// wwwahhrgll
			socket.io.close();
			Cache.deleteAll();
			GameLobby.request.create('player/getAll');
			initializeSocket();
		}
	}

	$rootScope.$on('login', function () {
		lastCheckedTimestamp = (new Date()).getTime();
		GameLobby['tick']['bind']('checkForHibernation', checkForHibernation);
	});

	$rootScope.$on('logout', function () {
		socket.io.disconnect();
	});
	/**
	 * Forces a reload of the frontend
	 * e.g. when a avatar has been deleted
	 */
	$rootScope.$on('forceReload', function (evt, data) {
		window.location.replace('/');
	});


	var resource = 'socket.io';
	var debug = false;
	var connected = false;
	var authenticated = false;
	var sessionData = {'debug': ''};
	if (typeof (GameLobby.config['node']['resource']) != 'undefined' && GameLobby.config['node']['resource'] != '') {
		resource = GameLobby.config['node']['resource'] + '/socket.io';
	}
	var socket = null;

	var log = function (msg) {
		if (debug) {
			console.log('[SocketService] ' + msg);
		}
	};

	function getConnectionMethod() {
		var connectionMethod='notConnected';
		if ((typeof(socket['io']) != 'undefined') && (typeof(socket['io']['engine']) != 'undefined') && (typeof(socket['io']['engine']['transport']) != 'undefined')) {
			connectionMethod = socket['io']['engine']['transport']['query']['transport'];
		}
		return connectionMethod;
	}

	function initializeSocket () {
		if (socket == null) {
			socket = io['connect'](window.location.protocol+'//' + window.location.host + '/', {
				// options here
				'path': '/'+resource,
				"transports" : [ 'polling', 'websocket', 'flashsocket'],
				'tryMultipleTransports': true,
				'flash policy port': 843,
				'reconnect': true
			});
		} else {
			socket.io.connect();
		}

		socket['on']('message', function (msg) {
			if (typeof(msg['tabs']) != 'undefined') {
				lastTabCount = msg['tabs'];
			}
			if (typeof(msg['serial']) != 'undefined') {
				if (serial != null) {
					// Did we miss one of the messages?
					if ((serial != msg['serial'] - 1) && (msg['serial'] != 0)) {
						// Tell the server about it
						var data = {
							'secs': Math.round((lastPongReceived / 1000 - ServertimeService.getInitTime())),
							'tabs': lastTabCount,
							'pings': pingsSent,
							'pongs': pongsReceived,
							'start': ServertimeService.getInitTime(),
							'transport': getConnectionMethod(),
							'lastReceivedSerial': serial,
							'currentSerial': msg['serial']
						};
						GameLobby.request.create('player/logError', {'data': data, 'type': 3});
					}
				}
				serial = msg['serial'];
			}
			GlobalStateService.handleResponse(msg);
		});

		socket['on']('connect', function () {
			connected = true;
			var usedTransport = getConnectionMethod();
			log('Connected via ' + usedTransport);
			if (sessionData != null) {
				emit('subscribe', sessionData, function () {
					authenticated = true;
					$rootScope.$broadcast('chatConnected');
				});
			}
		});

		socket['on']('connecting', function () {
			var usedTransport = getConnectionMethod();
			log('Try connecting via ' + usedTransport);
			sessionData['debug'] += 'connecting:' + usedTransport + ',';
		});

		socket['on']('connect_failed', function () {
			var usedTransport = getConnectionMethod();
			log('connect failed via ' + usedTransport);
			sessionData['debug'] += 'failed:' + usedTransport + ',';
		});

		socket['on']('disconnect', function () {
			var usedTransport = getConnectionMethod();
			log('disconnected ' + usedTransport);
			sessionData['debug'] += 'disconnected:' + usedTransport + ',';
			connected = false;
			authenticated = false;
			disconnects.push((new Date()).getTime());
		});
	}

	initializeSocket();

	function emit (eventName, data, callback) {
		socket['emit'](eventName, data, function () {
			var args = arguments;
			if (callback) {
				$rootScope.$apply(function () {
					callback.apply(socket, args);
				});
			}
		});
	}

	function SocketSrv () {
		this.getSocket = function () {
			return socket;
		};
		this.setSessionData = function (data) {
			sessionData = data;
			sessionData['debug'] = '';
			if (connected) {
				emit('subscribe', sessionData, function () {
					authenticated = true;
					$rootScope.$broadcast('chatConnected');
				});
			}
		};
		this.emit = function (eventName, data, callback) {
			emit(eventName, data, callback);
		};
		this.isAuthenticated = function () {
			return authenticated;
		};
	}

	return new SocketSrv();

}]);