/**
 * Speichert die Referenzen auf Bilder und lädt diese falls nötig
 *
 * @param {String} Ordner aus dem die Kartenbilder geladen werden
 */
var ImgStorage = function ImgStorage (imgBasePath) {
	this.storage = {};
	this.watches= {};
	this.imgBasePath = imgBasePath;
	var spriteObj = {};
	var myself = this;

	/**
	 * Fügt ein neues Bild zum Laden hinzu.
	 *
	 * @param {String} imgPath
	 */
	this.add = function add (imgPath) {
		if (!this.storage.hasOwnProperty(imgPath)) {
			var img = new Image();
			img.src = this.imgBasePath + imgPath + '?v='+config['version'];
			this.storage[imgPath] = img;
		}
	};

	function imageReady(img, url) {
		img.onload = null;
		if (myself.watches[url] !== undefined) {
			for (var i=0; i<myself.watches[url].length; i++) {
				myself.watches[url][i](url, img);
			}
			myself.watches[url] = [];
		}
	}

	/**
	 * Gibt ein Bild zurück oder "null" wenn das Bild noch nicht gelanden wurde. Lädt dann das Bild
	 *
	 * @param {String} imgPath
	 * @returns {Image}
	 */
	this.get = function get (imgPath, callback) {
		// Is this image part of a sprite?
		if (typeof(spriteCfg) == 'undefined') {
			spriteCfg = {};
		}
		if (typeof(config) == 'undefined') {
			config = {};
		}
		if (typeof(spriteCfg['iso/'+imgPath]) != 'undefined') {
			var cfg = spriteCfg['iso/'+imgPath];
			var spriteFileCfg = spriteCfg['files'][cfg.img];
			if (typeof(spriteObj[spriteFileCfg['src']]) == 'undefined') {
				var img = new Image();
				img.src = this.imgBasePath.substr(0,this.imgBasePath.length-4) + spriteFileCfg['src'] + '?v='+config['version'];
				img.onload = function() {
					imageReady(img.src);
				};
				spriteObj[spriteFileCfg['src']] = img;
			}
			if (!spriteObj[spriteFileCfg['src']].complete) {
				return null;
			}
			return {'image': spriteObj[spriteFileCfg['src']], 'cfg': cfg};
		}

		if (imgPath == null) {
			return null;
		}
		if (!this.storage.hasOwnProperty(imgPath)) {
			var img = new Image();
			img.src = this.imgBasePath + imgPath + '?v='+config['version'];
			img.onload = function() {
				imageReady(img, imgPath);
			};
			this.storage[imgPath] = img;
		}

		if (this.storage[imgPath].complete === true) {
			var img = this.storage[imgPath];
			return {'image': this.storage[imgPath], 'cfg': {x: 0, y: 0, w: img.width, h: img.height}};
		} else {
			if (callback !== undefined) {
				if (this.watches[imgPath] === undefined) {
					this.watches[imgPath] = [];
				}
				this.watches[imgPath].push(callback);
			}
		}

		return null;
	};

	/**
	 * Lädt ein oder mehrere Bilder. Gibt "true" zurück, wenn alle gelanden sind.
	 * Ein optionaler Callback wird aufgerufen, sobald alle Bilder geladen sind.
	 *
	 * @param {Array} imgArr
	 * @returns {Boolean}
	 */
	this.load = function load (imgArr, callback) {
		var allImgLoaded = true;
		var finishedImages = [];
		var finishedImageCnt = 0;
		function localCallback(requestedUrl, img) {
			finishedImages[requestedUrl] = img;
			finishedImageCnt++;
			if (finishedImageCnt >= imgArr.length) {
				if (callback !== undefined) {
					callback(finishedImages);
				}
			}
		}

		for (var i = 0; i < imgArr.length; i++) {
			var cacheResult = this.get(imgArr[i], localCallback);
			if (cacheResult === null) {
				allImgLoaded = false;
			} else {
				localCallback(imgArr[i], cacheResult['image']);
			}
		}

		return allImgLoaded;

	};
};