
/** @expose */
GameLobby.app.filter('bidiNumber', function () {
	/**
	 * name		->	currently not used
	 * value	->	the number which is needed in bidi format
	 * threshold->  int: number gets only shortened, if bigger than this value (optional)
	 * digits   ->  int: decimal places after comma (Only used when threshold is passed)
	 * fractionSize -> int: when passed, number will be rounded to the
	 * 					given decimal numbers after floating point.
	 */
	return function (value, unit ,showSign, inline, threshold, digits, fractionSize) {


		return new BidiHelper({
			value: value,
			unit: unit,
			showSign: showSign,
			inline: inline,
			threshold: threshold,
			digits: digits,
			fractionSize: fractionSize
		}).getBidiNumber();

	};
});