/**
 * ng-click-like wrapper for PointerHelper
 */
/** @expose */
GameLobby.app.directive('onPointerOut', [function () {
	return  function (scope, elem, attrs) {
		PointerHelper.onPointerOut(elem[0], function () {
			scope.$eval(attrs['onPointerOut']);
			requestAngularApply();
		});
		scope.$on('$destroy', function () {
			PointerHelper.removeBindings(elem[0]);
		});
	};
}]);