GameLobby.app.directive('rightMenuBar', [function () {
	return {
		restrict: 'A',
		link: function (scope, element, attrs) {

			var $bar = $(element).find('.news-bar');
			var visible = $bar.is(':visible');
			var hideable = true;

			/**
			 * Right sidebar is always visible
			 */
			enquire.register("screen and (min-width: 1330px)", {
					match: function () {
						hideable = false;
						visible = true;
						toggle();
						$(element).find('.newsButton').removeClass("active");
					},
					unmatch: function () {},
					destroy: function () {}
				}
			);

			/**
			 * Right sidebar is toggleable
			 */
			enquire.register("screen and (max-width: 1329px)", {
					match: function () {
						hideable = true;
						toggle(true);
					},
					unmatch: function () {},
					destroy: function () {}
				}
			);

			function toggle (currentState) {
				var css = {};
				var to = getReadingDirectionTo();
				if (!hideable) {
					visible = true;
					$(element).find('.newsButton').addClass("active");
					css[to] = 0;
					return $bar.css(css);
				}
				// hide
				if (currentState === true) {
					visible = false;
					$(element).find('.newsButton').removeClass("active");
					css[to] = -320;
					return $bar.css(css);
				}

				visible = true;
				css[to] = 0;
				$bar.css(css);
				$(element).find('.newsButton').addClass("active");
			}

			scope.showTab = function (tab) {
				scope.menuBarOptions.currentTab = tab;
				toggle(visible);
			}
		}
	}
}]);