function CacheClass () {
	var myself = this;
	// May be checked by other parts of the application to prevent any callSubscriber() calls.
	this.recursionState = 0;
	this.collectionModel = null;
	this.logLevel = 30;
	this.log = function (str, loglevel) {
		if (loglevel > myself.logLevel)
			console.log("[Cache] " + str);
	};

	/**
	 * Der heilige Cache selber :)
	 */
	this.c = {};
	/**
	 * Expire Timer von Elementen aus dem Cache
	 */
	this.e = {};
	/**
	 * Links zwischen Elementen aus dem Cache
	 */
	this.l = {};
	/**
	 * Speichert die Zuordnung zwischen Datenbanknamen und Klassen
	 */
	this.str2obj = {};

	/**
	 * Intern genutzte Variablen, um mehrere Anfragen zu bündeln.
	 */
	this.serverTimer = null;
	this.serverTimeDiff = 0;
	this.serverQueue = [];
	this.serverRequest = function () {
		myself.log("Request to Server for " + myself.serverQueue.length + " Sets", 1);
		GameLobby.request.create('cache/get', {
			'names': myself.serverQueue
		});
		myself.serverQueue = [];
		myself.serverTimer = null;
	};

	/**
	 * Liefert einen Eintrag aus dem Cache zurück.
	 * Sollte er noch nicht existieren, so wird zunächst ein Dummyobjekt mit leeren Werten zurückgegeben.
	 * Gleichzeitig geht dann aber eine Anfrage an den Server raus. Sobald diese beantwortet wurde,
	 * werden alle Werte des Objekts automatisch geupdatet.
	 * Bündelt mehrere Anfragen zu einer einzigen, wenn sie schnell hintereinander kommen.
	 *
	 * Optionales Argument "expireTime": Nach dieser Anzahl Sekunden wird das Objekt im Cache invalidiert
	 */
	this.get = function (objClass, id, expireTime) {
		var name = objClass._dbString;
		if (arguments.length > 1)
			name = objClass._dbString + id;
		var forceGet = false;
		if (arguments.length > 2 && expireTime < 0) {
			//Force Request an den Server
			forceGet = true;
		}

		var now = new Date().getTime();
		if ((typeof(myself.e[name]) != "undefined") && myself.e[name] < now) {
			delete myself.e[name];
			forceGet = true;
		}
		if (forceGet) {
			myself.log("Force Get: " + name, 0);
			if (myself.serverTimer == null) {
				myself.serverTimer = window.setTimeout(myself.serverRequest, 10);
			}
			myself.serverQueue.push(name);
			if (arguments.length > 2 && expireTime > 0) {
				if (typeof(myself.e[name]) == "undefined")
					myself.e[name] = now + expireTime * 1000;
			}
		}
		myself.log("Getting " + name, 0);
		if ((typeof(myself.c[name]) != "undefined")) {
			myself.log("Success! " + name, 2);
			return myself.c[name];
		}
		else {
			if (myself.serverTimer == null) {
				myself.serverTimer = window.setTimeout(myself.serverRequest, 10);
			}
			myself.serverQueue.push(name);
			var newObj = new objClass();
			myself.c[name] = newObj;
			myself.c[name]['serverId'] = name;
			if (arguments.length > 2 && expireTime > 0) {
				if (typeof(myself.e[name]) == "undefined")
					myself.e[name] = now + expireTime * 1000;
			}
			myself.log("Cache miss! Returning " + name + " dummy.", 2);
			return newObj;
		}
	};

	this.mapping = function (id, from, to) {
		to['lastFilled'] = (new Date()).getTime();
		to['serverId'] = id;
		return to.set(from, false);
		/*for (var key in from) {
		 to[key]=from[key];
		 }*/
	};
	/**
	 * Befüllt den Cache mit allen Objekten, die vom Server kommen.
	 * Aktualisiert dabei bereits vorhandene Einträge.
	 *
	 * Optional kann eine serialNo mitgegeben werden. Diese wird dann in jedes Objekt geschrieben. Sollte in einem Objekt bereits eine neuere serialNo stehen, so wird dieses Update ignoriert.
	 */
	this.fill = function (data, serialNo) {
		if ((data == null) || (data == ""))
			return false;
		if (typeof(data['cache']) != "undefined") {
			myself.recursionState++;
			// Alle Objekte sammeln, die geupdatet wurden
			var objectsToUpdate = [];
			for (var i = 0; i < data['cache'].length; i++) {
				var r = data['cache'][i];
				var nameParts = r.name.split(':');
				var classStr = nameParts[0] + ":";
				// Wenn keine Klasse für den Ersten Teil der ID existiert, nachschauen,
				// ob eine zusammengesetzte Klasse aus den beiden ersten Teilen existiert.
				if ((typeof(myself.str2obj[classStr]) == "undefined") && (nameParts.length > 2)) {
					classStr = nameParts[0] + ":" + nameParts[1] + ":";
				}
				var objClass = myself.str2obj[classStr];

				var objectInCache = myself.c[r.name];
				if (typeof(objectInCache) != "undefined") {
					myself.log("Updating " + r.name, 3);
					// Only update this object if the serial (if any) is low than the new provided one
					if (!objectInCache.serialNo || !serialNo || objectInCache.serialNo <= serialNo) {
						if (serialNo) {
							objectInCache.serialNo = serialNo;
						}
						var options = {};
						if ((typeof(objClass) !== "undefined") && (typeof(objClass.mappingIgnores) !== "undefined")) {
							options = {'copy': myself.str2obj[classStr].mappingIgnores};
						}
						myself.mapping(r.name, r.data, objectInCache);
						//ko.mapping.fromJS(r.data, options, myself.c[r.name]);
						objectsToUpdate.push(objectInCache);
						objectInCache.filled = true;
					}
				}
				else {
					myself.log("Filling " + r.name, 1);
					if (typeof(myself.str2obj[classStr]) !== "undefined") {
						var newObj = new myself.str2obj[classStr]();
						var result = myself.mapping(r.name, r.data, newObj);
						if (result !== false) {
							//ko.mapping.fromJS(r.data, {}, newObj);
							myself.c[r.name] = newObj;
							myself.log("Classname " + classStr, 0);
							objectsToUpdate.push(newObj);
							myself.c[r.name].filled = true;
						} else {
							myself.log("Ignoring it. Invalid operation. " + r.name, 1);
						}
					}
					else {
						//myself.mapping(r.data, newObj);
						//myself.c[r.name]=ko.mapping.fromJS(r.data);
						myself.log("Unknown Class " + classStr, 1);
					}
				}
			}
			// Alle Subscriber für alle befüllten Objekte benachrichtigen
			for (var i = 0; i < objectsToUpdate.length; i++) {
				var obj = objectsToUpdate[i];
				obj.callSubscribers();
			}
			myself.recursionState--;
			return objectsToUpdate;
		}
	};
	/**
	 * Leert den kompletten myself.
	 */
	this.deleteAll = function () {
		for (var x in myself.c) {
			myself.invalidate(x);
		}
	};
	/**
	 * Leert alle Elemente aus dem Cache, die mit "name" beginnen.
	 */
	this.deleteElements = function (name) {
		var len = name.length;
		for (var x in myself.c) {
			if (x.substr(0, len) == name) {
				myself.invalidate(x);
			}
		}
	};
	/**
	 * Löscht genau ein Element aus dem myself. Beachtet verlinkungen.
	 */
	this.invalidate = function (obj) {
		var name = obj;
		if (typeof(obj['serverId']) != 'undefined') {
			name = obj['serverId'];
		}
		myself.e[name] = 0;
		if (typeof(myself.c[name]) != "undefined") {
			myself.c[name].filled = false;
		}
		if (typeof(myself.l[name]) != "undefined") {
			for (var i = 0, len = myself.l[name].length; i < len; i++) {
				var linkName = myself.l[name][i];
				myself.invalidate(linkName);
			}
			myself.log("Deleting entry " + name, 1);
			delete myself.l[name];
		}
	};
	/**
	 * Stellt sicher, dass die Daten im angegebenen Objekt nicht älter als die angegebenen Sekunden sind.
	 * Fordert die Daten im Hintergrund neu an, wenn sie doch älter sein sollten.
	 */
	this.ensureNotOlderThan = function (obj, seconds) {
		var now = (new Date()).getTime();
		var secDiff = (now - obj['lastFilled']) / 1000;
		if (secDiff > seconds) {
			myself.get({'_dbString': obj['serverId']}, "", -1);
		}
	};
	/**
	 * Gibt zurück, ob sich ein entsprechender Eintrag im Cache befindet
	 */
	this.isInCache = function (name) {
		if (typeof(myself.c[name]) != 'undefined') {
			return myself.c[name].filled
		}
		return false;
	};
	/**
	 * Returns all elements of the cache with matching prefix name part
	 */
	this.getCachedElements = function (name) {
		var len = name.length;
		var elements = [];
		for (var x in myself.c) {
			if (x.substr(0, len) == name) {
				elements.push(myself.c[x]);
			}
		}
		return elements;
	}
	/**
	 * Erstellt eine Verknüpfung zwischen src und dest.
	 * Sobald sich dest ändert oder gelöscht wird, wird auch src gelöscht
	 */
	this.linkTo = function (src, dest) {
		var destName = null;
		var srcName = null;
		for (var x in myself.c) {
			if (myself.c[x] == src)
				srcName = x;
			if (myself.c[x] == dest)
				destName = x;
		}
		if ((srcName == null) || (destName == null))
			return false;
		if (typeof(myself.l[destName]) == "undefined")
			myself.l[destName] = [];
		myself.l[destName].push(srcName);
	};
	this.register = function (obj, dbName, expire) {
		// Dem Objekt eine Variable hinzufügen, die anzeigt, ob das Objekt befüllt ist
		obj.prototype.filled = false;
		if (arguments.length > 1) {
			obj._dbString = dbName;
			if (arguments.length > 2) {
				obj._cacheExpire = expire;
			}
			else {
				obj._cacheExpire = 0;
			}
		} else {
			if (typeof(obj.name) != 'undefined') {
				obj._dbString = obj.name + ":";
			} else {
				obj._dbString = obj.toString().match(/^function\s*([^\s(]+)/)[1] + ":";
			}
		}
		obj.get = function () {
			var keyId = "";
			for (var i = 0, len = arguments.length; i < len; i++) {
				if (i > 0)
					keyId += ":";
				keyId += arguments[i];
			}
			return myself.get(obj, keyId, obj._cacheExpire);
		};
		obj.getCollection = function () {
			var keyId = "";
			for (var i = 0, len = arguments.length; i < len; i++) {
				if (i > 0)
					keyId += ":";
				keyId += arguments[i];
			}
			return myself.get(myself.collectionModel, obj._dbString + keyId, obj._cacheExpire);
		};
		obj.classReference = obj;
		myself.str2obj[obj._dbString] = obj;
		myself.str2obj['Collection:' + obj._dbString] = myself.collectionModel;

		// Dem Objekt eine Subscribe funktion hinzufügen.
		// Die Callbacks werden dann gefeuert, wenn hier ein neues Objekt vom Server angekommen ist.
		obj.prototype.subscribe = function (fct) {
			// Falls das Objekt bereits geladen ist, die Funktion sofort ausführen
			if (this.filled) {
				fct(this);
			}

			if (typeof(this.subscriberArray) == "undefined")
				this.subscriberArray = [];
			this.subscriberArray.push({'fct': fct});

			var tmpCnt = 0;
			var subscr = this.subscriberArray;
			for (var ii = 0; ii < subscr.length; ii++) {
				if (subscr[ii] != null) {
					tmpCnt++;
				}
			}
			Cache.log("(+) Subscribers to " + this.serverId + ": " + tmpCnt, 4);

			return this.subscriberArray.length - 1;
		};
		obj.prototype.callSubscribers = function () {
			// Hängen an diesem Object subscriber?
			if (typeof(this.subscriberArray) != "undefined") {
				// Dann alle Callback Funktionen aufrufen
				var subscr = this.subscriberArray;
				for (var ii = 0; ii < subscr.length; ii++) {
					if (subscr[ii] != null) {
						subscr[ii]['fct'](this);
					}
				}
			}
		};
		obj.prototype.unsubscribe = function (idx) {
			if (typeof(this.subscriberArray) == "undefined")
				return false;
			this.subscriberArray[idx] = null;

			var tmpCnt = 0;
			var subscr = this.subscriberArray;
			for (var ii = 0; ii < subscr.length; ii++) {
				if (subscr[ii] != null) {
					tmpCnt++;
				}
			}
			Cache.log("(-) Subscribers to " + this.serverId + ": " + tmpCnt, 4);
		};
	}
}
/** @expose */
window['Cache'] = new CacheClass();