GameLobby.app.controller('SuspendedInfoController', ['$scope', '$stateParams', 'WindowService', function ($scope, $stateParams, WindowService) {

	$scope['banReason'] = {};
	$scope['banReason'].STRIKE_REASON_GENERAL = 1;
	$scope['banReason'].STRIKE_REASON_MULTI_ACCOUNT = 2;
	$scope['banReason'].STRIKE_REASON_PASSWORD_SHARING = 3;
	$scope['banReason'].STRIKE_REASON_BUG_ABUSE = 4;
	$scope['banReason'].STRIKE_REASON_BOT_OR_SCRIPT_USING = 5;
	$scope['banReason'].STRIKE_REASON_SILVER_FRAUD = 6;
	$scope['banReason'].STRIKE_REASON_PAYMENT = 7;
	$scope['banReason'].STRIKE_REASON_HARASSMENT_INSULT = 8;
	$scope['banReason'].STRIKE_REASON_ACP_DELETION = 9;
	$scope['banReason'].STRIKE_REASON_ACP_PAYMENT = 10;

	$scope['links'] = GameLobby.config.links;
	$scope['AvatarModel'] = Avatar;

	$scope['openGameRules'] = function () {
		WindowService.open({
			templateUrl: 'templates/pages/iframe.html',
			title: GameLobby.txt["SuspendedInfo.Ban.RulesPopup.Header"],
			page: 'rules'
		});
	};

	// remove the standard headline because the suspended info popup does have a special one
	$('.modal-title').remove();
}]);
