function AvatarInformation () {
    this.defaultData = {
        'avatarIdentifier': 0,
        'userAccountIdentifier': 0,
        'lastClick': 0,
        'signupTime': 0,
        'lastLogin': 0,
        'ranking': 0,
        'buildingQueue': 0,
        'buildingQueueMaster': 0,
        'incomingAttacks': 0
    };

    this.data = {};
    this.set(this.defaultData);
    this.name = 'AvatarInformation';
}

AvatarInformation.prototype = new AbstractModel();
Cache.register(AvatarInformation);