GameLobby.app.controller('PrestigeController', ['$scope', 'Request', 'SessionService', 'Servertime', 'WindowService', function ($scope, Request, SessionService, ServertimeService, WindowService) {

	$scope.serverTime = ServertimeService.getServerTime();

	// Request needed data and calculate the progress for next level
	Request.create(
		'player/getPrestigeOnWorlds', {'type': $scope['modal']['type']}, function (data) {
			$scope['prestigeWorlds'] = data;
		}
	);
}]);