/** @expose */
GameLobby.app.directive('prestigeStars', [function () {
	return {
		restrict: 'E',
		scope: {
			'stars': '='
		},
		templateUrl: 'templates/directives/prestigeStars.html',
		replace: true,
		link: function (scope) {
			scope['badge'] = false;

			scope.$watch('stars', function() {
				if (!scope['stars']) {
					return;
				}

				setStars(scope['stars']);
			});

			function setStars (stars) {
				var types = ['bronze', 'silver', 'gold', 'bronzeBadge', 'silverBadge', 'goldBadge'];

				scope['star'] = [
					'Empty', 'Empty', 'Empty'
				];
				scope['badge'] = false;

				angular.forEach(types, function(type) {
					if (stars[type]) {
						if (type.indexOf('Badge') !== -1) {
							scope['badge'] = true;
							scope['badgeType'] = type;
						} else {
							var value = stars[type];
							while (value > 0) {
								value--;
								scope['star'][value] = type.charAt(0).toUpperCase() + type.slice(1);
							}
						}
					}
				});
			}
		}
	}
}]);