/**
 * Scrollbar Library
 *
 * @param options
 * @constructor
 */
function Scrollbar (options) {
	var $elem = options['element'];

	function scrollDebug(what) {
		//console.log("[scrollbar.js] "+what);
	}

	// config lines
	/**
	 * Pixels to be scrolled up or down
	 * when the user clicks on the scrolltrack
	 * @type {number}
	 */
	var scrollOnTrackClick = 100;
	/**
	 * Time in milliseconds how often scrolling should occur
	 * when the user clicks and holds the left mouse button
	 * @type {number}
	 */
	var trackClickRepetition = 180;

	/**
	 * This is the element which is overflow hidden
	 * and defines the size of the scrolling area
	 * @type {jQuery|HTMLElement}
	 */
	var $outerWrapper = $('.scrollContentOuterWrapper', $elem);

	/**
	 * This is the element which has overflow-y: scroll;
	 * @type {jQuery|HTMLElement}
	 */
	var $container = $('.scrollContent', $elem);

	/**
	 * This is the element which represents the width of the content
	 * @type {jQuery|HTMLElement}
	 */
	var $innerWrapper = $('.scrollContentInnerWrapper', $elem);

	/**
	 * This is the scroll track
	 * @type {jQuery|HTMLElement}
	 */
	var $track = $('.scrollTrack', $elem);

	/**
	 * This is the scroll handle
	 * @type {jQuery|HTMLElement}
	 */
	var $handle = $('.scrollHandle', $elem);

	// don't change values below this line
	var wrapperHeight = 0;
	var contentHeight = 0;
	var handleHeight = 0;
	var basicScrollBarWidth = 20;
	var disableAutoScroll = false;
	var muteScrollEvent = false;

	var isMac = navigator.platform.toUpperCase().indexOf('MAC') !== -1;
	var isFirefox = $('.Firefox').length > 0;

	// hack for firefox on osx
	if (isMac && isFirefox) {
		basicScrollBarWidth = 10;
	}

	/**
	 * Recalculate height values before all other calculations
	 */
	function calculateHeight () {
		wrapperHeight = $outerWrapper.height();
		scrollDebug('calculateHeight wrapperHeight:'+wrapperHeight);
		/**
		 * Always make sure that the inner container
		 * has the same size the wrapper has
		 */
		$container.height($elem.outerHeight());

		/**
		 * Get the height of the whole content
		 * @type {number}
		 */
		contentHeight = $container[0].scrollHeight;
	}

	/**
	 * Calculates the height of the handle
	 * @returns {number}
	 */
	function calculateHandleHeight () {
		if (wrapperHeight === contentHeight) {
			return 0;
		}

		var percent = wrapperHeight * 100 / contentHeight;
		var result = wrapperHeight * percent / 100;
		handleHeight = result;
		$handle.height(result);
		return result;
	}

	/**
	 * Calculates the position of the handle
	 */
	function calculateHandlePosition () {
		var scrollTopPercent = $container.scrollTop() * 100 / contentHeight;
		$handle.css({
			top: wrapperHeight * scrollTopPercent / 100
		});
	}

	/**
	 * Calculates the scrollTop value when the
	 * handle is used for scrolling
	 */
	function calculateScrollTopByHandlePosition () {
		var handlePosY = parseInt($handle.css('top'));
		var handlePosYPercent = handlePosY * 100 / (wrapperHeight-1);

		muteScrollEvent = true;
		$container.scrollTop(Math.ceil(contentHeight * handlePosYPercent / 100));
	}

	/**
	 * Checks whether the tracks needs to be displayed or not.
	 * If the content is not larger than the outer size,
	 * then we dont need to display the track
	 */
	function checkForTrackVisibility () {
		// Before calculating any new width, reset everything to auto
		$container.width('auto');
		$outerWrapper.width('auto');

		var elemWidth = $elem.width();
		var scrollbarWidth = ($container.width() - $innerWrapper.width()) || basicScrollBarWidth;
		$outerWrapper.width(elemWidth);
		$container.width(elemWidth + scrollbarWidth);
		if ((wrapperHeight+1) >= contentHeight || (wrapperHeight == 0)) {
			if (trackVisible) {
				scrollDebug('Disable scrollbar because: '+wrapperHeight+" >= "+contentHeight+" (or is 0)");
			}
			$container.width(elemWidth);
			// hack for ff on osx
			if (isMac && isFirefox) {
				var stylingObj = {};
				stylingObj['padding-' + getReadingDirectionTo()] = 0;
				$innerWrapper.children().first().css(stylingObj);
				$innerWrapper.children().first().addClass('doubleScroll');
			}
			$container.css('overflow-y', 'hidden');
			return hideTrack();
		}

		// hack for ff on osx
		if (isMac && isFirefox) {
			$elem.width(elemWidth);
			var stylingObj = {};
			stylingObj['padding-' + getReadingDirectionTo()] = 0;
			$innerWrapper.children().first().css(stylingObj);
		} else {
			$innerWrapper.width(elemWidth);
		}
		$container.css('overflow-y', 'auto');
		scrollDebug('Enable scrollbar because content:'+contentHeight+" > wrapperHeight:"+wrapperHeight);
		showTrack();
	}

	/**
	 * Indicates whether the track is visible or not
	 * @type {boolean}
	 */
	var trackVisible = false;

	/**
	 * Show the track
	 * This function waits a few ms to avoid that the track is visible
	 * and disappears after a short time.
	 */
	function showTrack () {
		trackVisible = true;
		window.setTimeout(function () {
			if (trackVisible) {
				$track.fadeIn();
				$outerWrapper.addClass('active');
			}
		}, 300);
	}

	/**
	 * Hide the track
	 */
	function hideTrack () {
		if (!trackVisible) {
			$track.hide();
			return false;
		}
		trackVisible = false;
		$track.hide();
		$outerWrapper.removeClass('active');
	}

	function calculateTrackHeight () {
		$track.height($container.height());
	}

	function checkUserScrollAction () {
		queueLazyRenderer();
		if (!isAtEnd()) {
			return disableAutoScroll = true;
		}
		return disableAutoScroll = false;
	}

	function scrollToEnd () {
		$container.scrollTop(contentHeight - $outerWrapper.height());
	}

	function scrollToBeginning () {
		$container.scrollTop(0);
	}

	function scrollBy (value) {
		$container.scrollTop($container.scrollTop() + value);
		update();
	}

	function isAtBeginning () {
		return $container.scrollTop() === 0;
	}

	function isAtEnd () {
		var diff = $container.scrollTop() - (contentHeight - $outerWrapper.height());
		if(Math.abs(diff) < 3) { //If we are almost at end its ok too.
			return true;
		}
		return false;
	}

	// This function checks every child element that wants to be rendered only if it's currently visible
	function checkLazyRenderer () {
		var newElements = false;
		var myTop = $container.scrollTop();
		var myHeight = $outerWrapper.height();
		$container.find('.waitForRender').each(function (idx, e) {
			var isVisible = false;
			if (e.offsetTop <= myTop + myHeight) {
				if (e.offsetTop + e.offsetHeight >= myTop) {
					isVisible = true;
				}
			}
			if (isVisible) {
				if (typeof(e.render) == 'function') {
					e.render();
					newElements = true;
				}
				$(e).removeClass('waitForRender');
			}
		});
		return newElements;
	}

	var rendererRunning = false;

	function queueLazyRenderer () {

		if (rendererRunning) {
			return;
		}

		rendererRunning = true;

		window.setTimeout(function () {
			rendererRunning = false;
			checkLazyRenderer();
		}, 10);
	}

	var oldHeight = -1;
	var oldWidth = -1;
	var oldContainerHeight = -1;
	var oldContainerWidth = -1;

	function update () {

		//The element selector whose width should be checked on window resize to either update dimensions or not.
		var $resizeSensorElem = $container;
		if(typeof $(options['resizeSensorElem'])[0] != 'undefined')
		{
			$resizeSensorElem = $(options['resizeSensorElem']);
		}

		// Don't do anything if the container didn't change
		if ((oldHeight == $innerWrapper[0].scrollHeight) && (oldWidth == $innerWrapper[0].scrollWidth) && oldContainerHeight === $container[0].offsetHeight && oldContainerWidth === $resizeSensorElem[0].offsetWidth) {
			return
		}
		oldHeight = $innerWrapper[0].scrollHeight;
		oldWidth = $innerWrapper[0].scrollWidth;
		oldContainerHeight = $resizeSensorElem[0].offsetHeight;
		oldContainerWidth = $resizeSensorElem[0].offsetWidth;
		calculateHeight();
		calculateHandleHeight();
		calculateTrackHeight();
		checkForTrackVisibility();
		calculateHandlePosition();
		queueLazyRenderer();
		if (options['scrollDown'] !== 'undefined' && options['scrollDown'] && !disableAutoScroll) {
			scrollToEnd();
		}
	}

	function isMouseOnHandle () {
		var handleOffset = $handle.offset();
		var handleWidth = $handle.width();
		var handleHeight = $handle.height();

		return ((options['mousePosition']['x'] > handleOffset.left && options['mousePosition']['x'] < handleOffset.left + handleWidth)
			&& (options['mousePosition']['y'] > handleOffset.top && options['mousePosition']['y'] < handleOffset.top + handleHeight));
	}

	// events
	function onContainerScroll () {
		if (muteScrollEvent) {
			muteScrollEvent = false;
			return;
		}
		calculateHandlePosition();
		checkUserScrollAction();
		if (typeof (options['onScrolling']) == 'function') {
			options['onScrolling']();
		}
	}

	function onHandleDrag () {
		calculateScrollTopByHandlePosition();
		checkUserScrollAction();
	}

	function onHandleClick (e) {
		e.stopPropagation();
		return false;
	}

	function onTrackUp (e) {
		muteScrollEvent = false;
	}

	function onTrackDown (e) {
		if (isMouseOnHandle()) {
			return false;
		}

		var handleOffsetY = $handle.offset().top;
		if (options['mousePosition']['y'] < handleOffsetY) {
			scrollBy(-scrollOnTrackClick);
			return;
		}
		scrollBy(scrollOnTrackClick);
	}

	$($handle).draggable({ 'containment': "parent" });

	$container.on('scroll', onContainerScroll);
	$handle.on('drag', onHandleDrag);
	$handle.on('click', onHandleClick);
	$track.on('mouseup', onTrackUp);
	$track.on('mousedown', onTrackDown);

	/**
	 * When this options is set to true
	 * the scrollbar will be updated every 100ms
	 */
	var tickName = null;
	var stopUpdates = false;
	if (options['autoUpdate'] === true) {
		/**
		 * Either use a supplied ticker or use basic loop
		 */
		if (typeof options.tick !== 'undefined') {
			tickName = options.tick.bind(update);
		}
		else {
			(function basicUpdateLoop () {
				if (!stopUpdates) {
					window.setTimeout(basicUpdateLoop, 100);
					update();
				}
			})();
		}
	}

	// kick off
	calculateHeight();
	calculateHandleHeight();
	calculateHandlePosition();
	if (options['scrollDown'] !== 'undefined' && options['scrollDown']) {
		scrollToEnd();
	}

	/**
	 * Public Methods
	 */
	this['update'] = update;
	this['scrollToBeginning'] = scrollToBeginning;
	this['scrollToEnd'] = scrollToEnd;
	this['scrollBy'] = scrollBy;
	this['checkLazyRenderer'] = queueLazyRenderer;
	this.destroy = function () {
		$container.off('scroll', onContainerScroll);
		$handle.off('drag', onHandleDrag);
		$handle.off('click', onHandleClick);
		$track.off('mouseup', onTrackUp);
		$track.off('mousedown', onTrackDown);
		if (tickName != null) {
			options.tick.unbind(tickName);
		}
		stopUpdates = true;
	};
}