/**
 * Shortens absolute numbers (will ignore signs)
 * @link originally from http://stackoverflow.com/questions/5275224/converting-big-numbers-to-bigger-factors-in-javascript
 *
 * Attributes
 * threshold        int: number gets only shortened, if bigger than this value (optional)
 * digits           int: decimal places after comma
 */

/** @expose */
GameLobby.app.filter('shortBigNumber', function () {
	return function (text, threshold, digits) {
		return shortBigNumber(text, threshold, digits);
	};
});
