String.prototype.trim = function (charlist) {
	var str = String(this);
	var whitespace, l = 0, i = 0;
	str += '';

	if (!charlist) {
		// default list
		whitespace = ' \n\r\t\f\x0b\xa0\u2000\u2001\u2002\u2003\u2004\u2005\u2006\u2007\u2008\u2009\u200a\u200b\u2028\u2029\u3000';
	}
	else {
		// preg_quote custom list
		charlist += '';
		whitespace = charlist.replace(/([\[\]\(\)\.\?\/\*\{\}\+\$\^\:])/g, '$1');
	}

	l = str.length;
	for (i = 0; i < l; i++) {
		if (whitespace.indexOf(str.charAt(i)) === -1) {
			str = str.substring(i);
			break;
		}
	}

	l = str.length;
	for (i = l - 1; i >= 0; i--) {
		if (whitespace.indexOf(str.charAt(i)) === -1) {
			str = str.substring(0, i + 1);
			break;
		}
	}

	return whitespace.indexOf(str.charAt(0)) === -1 ? str : '';
};

/*
 * Recursively merge properties of two objects
 */
function array_merge_recursive (obj1, obj2) {

	for (var p in obj2) {
		try {
			// Property in destination object set; update its value.
			if (obj2[p].constructor == Object) {
				obj1[p] = array_merge_recursive(obj1[p], obj2[p]);

			}
			else {
				obj1[p] = obj2[p];

			}

		}
		catch (e) {
			// Property in destination object not set; create it and set its value.
			obj1[p] = obj2[p];
		}
	}

	return obj1;
}

Number.prototype.toHHMMSS = function (skipSeconds) {
	var sec_numb = Math.max(0,parseInt(this));
	var hours = Math.floor(sec_numb / 3600);
	var minutes = Math.floor((sec_numb - (hours * 3600)) / 60);
	var seconds = sec_numb - (hours * 3600) - (minutes * 60);

	if (hours < 10) {
		hours = '0' + hours;
	}
	if (minutes < 10) {
		minutes = '0' + minutes;
	}
	var time = hours + ':' + minutes;
	if (!skipSeconds) {
		if (seconds < 10) {
			seconds = '0' + seconds;
		}
		time += ':' + seconds;
	}
	return time;
};

Number.prototype.digitLength = function () {
	return (Math.floor(Math.log(this) / Math.LN10) + 1)
};

String.prototype.ucfirst = function () {
	var f = this.charAt(0).toUpperCase();
	return f + this.substr(1);
};

var keyValueToArray = function (obj, keyName, valName) {
	var arr = [];
	for (var key in obj) {
		var tmpObj = {};
		tmpObj[keyName] = key;
		tmpObj[valName] = obj[key];
		arr.push(tmpObj);
	}

	return arr;
};

function uniqid (prefix) {
	if (typeof prefix === 'undefined') {
		prefix = "";
	}

	var retId;
	var formatSeed = function (seed, reqWidth) {
		seed = parseInt(seed, 10).toString(16); // to hex str
		if (reqWidth < seed.length) { // so long we split
			return seed.slice(seed.length - reqWidth);
		}
		if (reqWidth > seed.length) { // so short we pad
			return Array(1 + (reqWidth - seed.length)).join('0') + seed;
		}
		return seed;
	};

	// BEGIN REDUNDANT
	if (!this.t5) {
		this.t5 = {};
	}
	// END REDUNDANT
	if (!this.t5.uniqidSeed) { // init seed with big random int
		this.t5.uniqidSeed = Math.floor(Math.random() * 0x75bcd15);
	}
	this.t5.uniqidSeed++;

	retId = prefix; // start with prefix, add current milliseconds hex string
	retId += formatSeed(parseInt(new Date().getTime() / 1000, 10), 8);
	retId += formatSeed(this.t5.uniqidSeed, 5); // add seed hex string

	return retId;
}