GameLobby.app.controller('SidebarController', ['$scope', 'Request', 'MellonService', 'WindowService', 'SessionService', '$rootScope', function ($scope, Request, MellonService, WindowService, SessionService, $rootScope) {
	var sessionObj = null;

	$scope['logout'] = function () {
		SessionService.logout();
	};

	var session = SessionService.getSession();
	sessionObj = Session.get(session.sessionId);

	// Player prestige stars
	$scope['prestigeDetails'] = PrestigeDetails.get(session.playerId);
	$scope['goldList'] = Gold.getCollection(sessionObj['data']['country']);

	$scope['AvatarModel'] = Avatar;
	$scope['links'] = GameLobby.config.links;
	$scope['offsetMobileSidebar'] = 46;

	$scope['domain'] = 'com';

	$scope['avatars'] = {};
	$scope['avatars']['own'] = Avatar.getCollection();
	$scope['avatars']['sitting'] = Sitter.getCollection(Sitter.COLLECTION_SITTED_AVATARS);
	$scope['avatars']['duals'] = Sitter.getCollection(Sitter.COLLECTION_DUALS_AVATARS);
	$scope['avatars']['own'].subscribe(function () {
		for (var i = 0; i < $scope['avatars']['own']['data'].length; i++) {
			var avatar = $scope['avatars']['own']['data'][i];
			// Prefetch game-world data
			GameWorld.get(avatar['data']['consumersId']);
		}
	});

	var domain = sessionObj['data']['country'];
	if (domain == 'en') {
		domain = 'com';
	}
	$scope['domain'] = domain;

	// Check if there are any uncollected achievements
	var achievements = Achievement.getCollection();
	achievements.subscribe(function () {
		$scope['uncollectedAchievements'] = false;
		for (var i = 0; i < achievements['data'].length; i++) {
			if (achievements['data'][i]['data']['achieved']) {
				$scope['uncollectedAchievements'] = true;
				break;
			}
		}
	});

	var active = '';
	$scope.setActive = function ($id) {
		active = $id;
		$rootScope.$broadcast('activateSidebarNavigationOption');
	};

	$scope.getActive = function () {
		return active;
	};

	$scope.openHelpCenter = function () {
		var country = 'en';
		if (sessionObj) {
			country = sessionObj['data']['country'];
		}
		window.open(GameLobby.config.links.urlHelpCenter + getLocaleRegion(getLocale(country)), '_blank');
	};

	$scope.openForum = function () {
		var country = 'en';
		if (sessionObj) {
			country = sessionObj['data']['country'];
		}
		var url = GameLobby.config.links.urlForum + getLocaleRegion(getLocale(country));
		if (country == 'ae') {
			url = 'http://kingdoms.travian-forum.com/';
		}
		window.open(url, '_blank');
	};

	$scope.openWiki = function () {
		var country = 'en';
		if (sessionObj) {
			country = sessionObj['data']['country'];
		}
		window.open(GameLobby.config.links.urlWiki + getLocaleLanguage(getLocale(country)), '_blank');
	};

	$scope.goldTransferDialog = function (goldId) {
		WindowService.open({
			templateUrl: 'templates/modals/goldTransferInfo.html',
			title: GameLobby.txt['Gold.Transfer'],
			goldId: goldId,
			name: 'goldTransferDialog'
		});
	};

	$scope.openSuspendedInfo = function (avatarIdentifier) {
		var avatar = Avatar.get(avatarIdentifier);
		WindowService.open({
			templateUrl: 'templates/modals/suspendedInfo.html',
			title: GameLobby.txt["SuspendedInfo.Headline"],
			avatar: avatar
		});
	};

	$scope.$on('mellon-gameWorldInMaintenanceMode', function (e, data) {
		MellonService.modalCloseIfAny();
		WindowService.open({
			templateUrl: 'templates/modals/maintenance.html',
			title: GameLobby.txt['userError.Headline'],
			onClose: function () {
			}
		});
	});

	$rootScope.$on('avatar:removed', function (event, args) {
		for (var i = 0; i < $scope['avatars']['own']['data'].length; i++) {
			if ($scope['avatars']['own']['data'][i]['data']['avatarIdentifier'] == args['avatarIdentifier']) {
				$scope['avatars']['own']['data'].splice(i, 1);
				break;
			}
		}
	});

	$scope.closeDialog = function () {
		$scope.modal.close();
	};

	$scope.switchCountry = function (country) {
		Request.create(
			'player/switchCountry',
			{
				'country': country
			},
			function (data) {
				// sets the language for mellon
				GameLobby.config.mellon.application.languageId = GameLobby.config.languages[country];
				window.location.replace('/');
			},
			function (data) {
			}
		);
	};

}]);