var Tick = function($rootScope) {
	var jobs = {};
	var tickCount = 0;
	(function tick () {
		var timeTrack = +new Date();
		window.setTimeout(tick, 1000);

		tickCount++;
		var newObj = {};
		for (var key in jobs) {
			if (!jobs.hasOwnProperty(key) || typeof jobs[key] !== 'function') {
				continue;
			}
			jobs[key]({
				tick: tickCount,
				name: key
			});
			newObj[key] = jobs[key];
		}
		jobs = newObj;
	})();
	$rootScope.recentMaxTick = 0;
	$rootScope.maxTick = 0;
	setInterval(function () {
		$rootScope.recentMaxTick = 0;
	}, 5000);
	return {
		/**
		 * Adds a job to the tick and returns the tick name
		 * @param name
		 * @param [callback]
		 * @returns {*}
		 */
		/** @expose */
		bind: function (name, callback) {
			if (typeof name === 'function') {
				callback = name;
				name = uniqid('tick');
			}
			callback();
			jobs[name] = callback;
			return name;
		},
		/**
		 * Removes a job from the tick
		 * @param name
		 */
		/** @expose */
		unbind: function (name) {
			tickCount--;
			jobs[name] = null;
		}
	}
};