GameLobby.app.directive('mellonUrl', function () {
	'use strict';

	return {
		transclude: true,
		restrict: 'A',
		template: '<span ng-transclude></span>',
		'link': function (scope, element, attrs) {

			var updatePostValue = function () {
				$(element)
				.data({
					mellonIframeUrl: attrs.mellonUrl,
					selector: '#jqFensterModalLayout'
				})
				.addClass('jqFenster');
			};

			scope.$watch(function() {return element.attr('mellon-url'); }, updatePostValue);
			updatePostValue();
		}
	};
});
