GameLobby.app.service('Servertime', ['$rootScope', function ($rootScope) {
	var initTime = Math.round(new Date().getTime() / 1000);

	/**
	 * The offset between server and client
	 *
	 * @type {Number}
	 */
	var offset = 0;

	/**
	 * Calculate the offset
	 *
	 * @param timestamp
	 */
	var calcTimeOffset = function (timestamp) {
		offset = Math.round(new Date().getTime() / 1000) - timestamp;
		calcTime();
	};
	$rootScope['currentServerTime'] = Math.round(new Date().getTime() / 1000);

	var calcTime = function () {
		$rootScope['currentServerTime'] = Math.round(new Date().getTime() / 1000) - offset;
		GameLobby.ServerTime = $rootScope['currentServerTime'];
	};

	// Let the ticks start 1s after the page is loaded (ugly, i know ...)
	window.setTimeout(function () {
		GameLobby['tick']['bind'](calcTime);
	}, 1000);

	function ServertimeSrv () {
		/**
		 * Return the current servertime
		 *
		 * @return {Number}
		 */
		this.getServerTime = function () {
			return $rootScope['currentServerTime'];
		};

		this.setServerTime = function (timestamp) {
			calcTimeOffset(timestamp);
		};

		/**
		 * Returns the timestamp of when this app was initialized
		 */
		this.getInitTime = function () {
			return initTime;
		};

		this.getOffset = function () {
			return offset;
		};
	}

	return new ServertimeSrv();

}]);