/**
 *  attrs:
 *  height-dependency: max or fix (default fix)
 *
 */
/** @expose */
GameLobby.app.directive('scrollable', [function () {
	function scrollDebug(what) {
		//console.log("[scrollable.js] "+what);
	}

	return {
		restrict: 'A',
		scope: false,
		transclude: true,
		'templateUrl': 'templates/partials/scrollable.html',
		link: function (scope, element, attrs) {
			var scrollContentHeightOffset = 0;
			var elementFixedHeight = -1;
			var firstRun = true;

			function setScrollContent() {
				var scrollContent = element.find('.scrollContent');
				if (!scrollContent) return false;

				/**
				 * First check if there is tabulation in this scope.
				 * If there is tabulation then we check if there's
				 * scrollContentHeightOffset set in tabData and if so
				 * we later subtract the offset from the maxBodySize
				 */
				if (typeof scope['tabData'] !== 'undefined') {
					if (typeof scope['tabData']['scrollContentHeightOffset'] === 'number') {
						scrollContentHeightOffset = scope['tabData']['scrollContentHeightOffset'];
					} else if (typeof scope['tabData']['scrollContentHeightOffset'] !== 'undefined' && typeof scope['tabData']['scrollContentHeightOffset'][scope['tabData']['currentTab']] === 'number') {
						scrollContentHeightOffset = scope['tabData']['scrollContentHeightOffset'][scope['tabData']['currentTab']];
					}
				}

				var height = 0;
				var cssProperty = 'height';
				if (typeof scope['w'] !== 'undefined') {
					if (typeof scope['overlayMaxScrollHeight'] != 'undefined') {
						// This scrollbar is within an overlay
						height = scope['overlayMaxScrollHeight'];
						cssProperty = 'maxHeight';
						scrollDebug('Got overlayMaxScrollHeight ' + height);
					} else {
						height = scope['w']['maxWindowBodySize'];
						scrollDebug('Got maxWindowBodySize ' + height);
					}
					if (height > scrollContentHeightOffset) {
						height -= scrollContentHeightOffset;
					}
				}
				var elemHeight = element.outerHeight();
				if (firstRun) {
					firstRun = false;
					var cssHeight = element.css('height');
					if (cssHeight.indexOf('px') > -1) {
						cssHeight = cssHeight.substring(0, cssHeight.indexOf('px'));
						cssHeight = parseInt(cssHeight);
					}
					if (elemHeight != cssHeight && cssHeight != element.outerHeight(true) && cssHeight != 0 && elemHeight != 0) {
						var padding = element.innerHeight() - elemHeight;
						elementFixedHeight = cssHeight - padding;
					}
					if (typeof scope['offsetMobileSidebar'] !== 'undefined') {
						elemHeight -= scope['offsetMobileSidebar'];
					}
				}
				var c = {'height': 'auto', 'maxHeight': 'none'};
				if (elementFixedHeight != -1) {
					c[cssProperty] = elementFixedHeight;
					scrollDebug('Setting elementFixedHeight ' + elementFixedHeight);
				} else {
					var heightDependency = attrs['heightDependency'] || 'fix';
					if (heightDependency == 'fix') {
						c[cssProperty] = elemHeight || height;
						scrollDebug('Set ' + cssProperty + " (fix) to " + elemHeight + " || " + height);
					} else {
						c[cssProperty] = height || elemHeight;
						scrollDebug('Set ' + cssProperty + " to " + height + " || " + elemHeight);
					}
				}
				c['width'] = element.outerWidth();
				scrollContent.css(c);
			}

			var position = element.css('position');
			if (position == 'static') {
				element.css('position', 'relative');
			}

			setScrollContent();

			function handleResize() {
				setScrollContent();
				myOwnApi['update']();
			}

			$(window).on('resize', handleResize);

			var unregisterInit = scope.$on('initChatScroll', function () {
				setScrollContent();
			});
			//The element selector whose width should be checked on window resize to either update dimensions or not.
			var resizeSensorElem = null;
			if (typeof attrs['resizeSensorElem'] != 'undefined') {
				resizeSensorElem = attrs['resizeSensorElem'];
			}
			var myOwnApi = new Scrollbar({
				'element': element,
				'tick': GameLobby.tick,
				'autoUpdate': true,
				'scrollDown': (typeof attrs['scrollDown'] !== 'undefined'),
				'onScrolling': function () {
					scope.$broadcast('contentScrolling', scope['$id']);
				},
				'mousePosition': GameLobby['mousePosition'],
				'resizeSensorElem': resizeSensorElem
			});
			myOwnApi['updateHeight'] = setScrollContent;
			myOwnApi['handleResize'] = handleResize;

			scope['scrollbarApi'] = myOwnApi;

			var unregisterScrollbarApi = scope.$on('scrollbarApi', function (e, name) {
				myOwnApi[name]();
			});

			scope.$on('$destroy', function () {
				unregisterInit();
				unregisterScrollbarApi();
				$(window).off('resize', handleResize);
				myOwnApi.destroy();
			});
		}
	};
}]);