GameLobby.app.controller('AccountController', ['$scope', 'Request', '$state', 'WindowService', 'Loading', 'MellonService', '$stateParams', function ($scope, Request, $state, WindowService, Loading, MellonService, $stateParams) {

	var loading = Loading.get('account');
	loading.start();
	$scope['minPrestigeNeededForRename'] = GameLobby.Features['prestigeNeededForRename'];
	$scope['enableAccountNameChange'] = GameLobby.Features['enableAccountNameChange'];

	Request.create(
		'player/getAccountDetails',
		{},
		function (data) {
			$scope['data'] = data;
			$scope['data']['hasSubsription'] = false;
			for (var nl in data.newsletter) {
				if (data.newsletter[nl]['subscribed']) {
					$scope['data']['hasSubsription'] = true;
					break;
				}
			}
			loading.stop();
		},
		function (data) {
			$scope['data'] = data;
			loading.stop();
		}
	);

	$scope.editPortrait = function () {
		WindowService.open({
			templateUrl: 'templates/modals/editPortrait.html',
			title: GameLobby.txt['EditPortrait.Headline'],
			onClose: function (data) {

			}
		});
	};

	$scope['prestige'] = PrestigeDetails.get(GameLobby.Player.data.playerId);

	$scope.editAccountname = function () {
		if(!GameLobby.Features['enableAccountNameChange']) {
			return;
		}
		WindowService.open({
			templateUrl: 'templates/modals/editAccountname.html',
			title: GameLobby.txt['EditAccountname.Headline'],
			onClose: function () {
			}
		});
	};

	$scope.$on('mellon-changeSubscriptionDone', function (e, data) {
		MellonService.modalCloseIfAny();
		var current = $state.current;
		var params = angular.copy($stateParams);
		$state.transitionTo(current, params, {
			reload: true,
			inherit: true,
			notify: true
		});
	});

}]);
