/**
 * Draws a list of achievement boxes and handles animations and sorting of them
 *
 * Possible attributes:
 * category: 	int - the category of achievements to show in the list (see Achievements.js)
 */

/** @expose */

GameLobby.app.directive('achievementsList', ['$rootScope', '$timeout', function ($rootScope, $timeout) {
	return {
		replace: true,
		templateUrl: 'templates/partials/achievement/list.html',
		scope: {
			'count': '='
		},
		link: function ($scope, element, attrs) {
			var BOX_MARGIN_BOTTOM = 10; // Margin between achievement boxes in pixels
			var HEIGHT = { //MAIN_BOX: Unexpended Box height, HISTORY_ROW: Height of each level row when expanded, HISTORY_FRAME: Padding of the level table when expanded
				'LARGE': {'MAIN_BOX': 90, 'HISTORY_ROW': 50, 'HISTORY_FRAME': 35}, // For screensize >= 600px
				'SMALL': {'MAIN_BOX': 116, 'HISTORY_ROW': 50, 'HISTORY_FRAME': 35} // For screensize < 600px
			};
			var screenSize = 'LARGE';

			var transitionEvents = 'transitionend webkitTransitionEnd oTransitionEnd MSTransitionEnd';

			$scope['expandedAchievements'] = {};
			$scope['achievementStyles'] = {};
			var achievementSorting = [];
			var collectingAchievements = {};
			var runningAnimations = 0;

			$scope['achievementsCollection'] = Achievement.getCollection();
			var unsubscribeIndex = $scope['achievementsCollection'].subscribe(updateList);

			if (attrs['category'] || attrs['category'] == '') {
				// Listen to category change
				attrs.$observe('category', function(categoryId, oldCategoryId) {
					if(typeof(categoryId) == 'undefined' || categoryId == oldCategoryId) {
						return;
					}
					if (categoryId == Achievement.CATEGORY_ALL) {
						// Exclude the mystery achievements from the 'ALL' category
						$scope['achievementsFilter'] = {'data': {'category': '!' + Achievement.CATEGORY_SPECIAL}};
					} else {
						$scope['achievementsFilter'] = {'data': {'category': categoryId}};
					}
					runningAnimations = 0;
					updateList();
					// To avoid the automatic animations on category change make the ng-repeat re-render everything (also the achievements that were already shown before)
					$scope['achievementsCollection'] = null;
					$scope.$evalAsync(function(){
						$scope['achievementsCollection'] = Achievement.getCollection();
					})
				});
			}

			$scope.$on('achievementAnimation', function(e, type, change) {
				// Some achievement was collected manually (while the animation is running, prevent reordering of the list)
				runningAnimations += change;
				if(change < 0) {
					// The animation of this achievement has finished
					if(runningAnimations == 0) {
						// It was the last running animation, so reorder the list now
						updateList();
					}
					collectingAchievements[type] = false;
				} else {
					// The animation started
					collectingAchievements[type] = true;
				}

			});

			$scope.$watch('expandedAchievements', function(newValue, oldValue) {
				if(newValue != oldValue) {
					updateList();
				}
			}, true);

			// Sorts the achievements, calculates the positioning of all achievements and handles some special behaviours (animations)
			function updateList() {
				// Remember the last sorting to check if some sorting has changed
				var lastSorting = {};
				for (var i = 0; i < achievementSorting.length; i++) {
					lastSorting[achievementSorting[i]['id']] = i;
				}
				// Remember the last set styles to compare changes later
				var lastStyles = angular.copy($scope['achievementStyles']);
				// Only reorder if no collect animation is currently running
				if (runningAnimations == 0) {
					sortAchievements();
				}
				// Recalculate the styles for each achievement box according to the sorting and the expanded states
				$scope['achievementStyles'] = {};
				var pos = 0;
				for(var i = 0; i < achievementSorting.length; i++) {
					var id = achievementSorting[i]['id'];
					$scope['achievementStyles'][id] = {'top': pos + 'px', 'zIndex': i};
					// Calculate the height of this box
					var height = HEIGHT[screenSize]['MAIN_BOX'];
					// If the box is expanded, add the height of the history
					if($scope['expandedAchievements'][id]) {
						height += achievementSorting[i]['completedLevels'].length * HEIGHT[screenSize]['HISTORY_ROW'];
						height += HEIGHT[screenSize]['HISTORY_FRAME'];
					}
					$scope['achievementStyles'][id]['height'] = height + 'px';
					// Set the position (top value) for the next box
					pos += height + BOX_MARGIN_BOTTOM;

					// Was this achievement collected manually right now and needs to move down?
					if(collectingAchievements[id] && typeof(lastSorting[id]) != 'undefined' && lastSorting[id] < i) {
						// Add a dropshadow while movement
						var elem = element.find('.achievementType'+id);
						elem.addClass('moving');
						// If the movement distance exceeds the achievement container height, fake a movement to the end of screen before moving it to its correct place
						// (Since we use timed transitions with ease functionality, movements to the bottom would be to fast to recognize)
						var distance = Math.abs(parseInt($scope['achievementStyles'][id]['top']) - parseInt(lastStyles[id]['top']));
						var conatinerHeight = $('.glWindow .achievementsContainer').height();
						var realTop = 0;
						if(distance > conatinerHeight) {
							// Remember the real top value
							realTop = $scope['achievementStyles'][id]['top'];
							// Set the fake top value (current top + scroll container height)
							$scope['achievementStyles'][id]['top'] = (parseInt(lastStyles[id]['top']) + conatinerHeight) + 'px';
						}
						// Setup the event that will reset everything when the transition ended
						(function(elem, type, realTop) {
							elem.one(transitionEvents, function() {
								elem.removeClass('moving');
								if(realTop) {
									$scope['achievementStyles'][type]['top'] = realTop;
									requestAngularApply();
								}
							});
						})(elem, id, realTop);
					}
				}
				$scope['count'] = achievementSorting.length;
				$scope['achievementStyles']['container'] = {'height': pos + 'px'};
			}

			function sortAchievements() {
				// Reset the sorting
				achievementSorting = [];
				for (var i = 0; i < $scope['achievementsCollection']['data'].length; i++) {
					var category = $scope['achievementsCollection']['data'][i]['data']['category'];
					// Only add achievements of the currently selected category
					if(typeof(attrs['category']) != 'undefined') {
						if(attrs['category'] != Achievement.CATEGORY_ALL) {
							if(category != attrs['category']) {
								continue;
							}
						} else {
							if(category == Achievement.CATEGORY_SPECIAL) {
								continue;
							}
						}
					}
					achievementSorting.push(angular.copy($scope['achievementsCollection']['data'][i]['data']));
				}
				// Sort the achievements
				achievementSorting.sort(compareAchievements);
			}

			// This function defines the sorting of the achievements list
			function compareAchievements(a, b) {
				// Achieved Achievements first
				if(!a['achieved'] && b['achieved']) {
					return 1;
				}
				if(a['achieved'] && !b['achieved']) {
					return -1;
				}
				// Completed Achievements last
				if((a['level'] == a['maxLevel']) && !(b['level'] == b['maxLevel'])) {
					return 1;
				}
				if(!(a['level'] == a['maxLevel']) && (b['level'] == b['maxLevel'])) {
					return -1;
				}
				if((a['level'] == a['maxLevel']) && (b['level'] == b['maxLevel'])) {
					// Completed achievements will sort by collection time of the last level
					var competedA = a['completedLevels'][a['completedLevels'].length -1]['collected'];
					var competedB = b['completedLevels'][b['completedLevels'].length -1]['collected'];
					if(competedA < competedB) {
						return 1;
					} else {
						return -1;
					}
				}
				// All other achievements will be sorted by their overall progress
				if(!(a['achieved'] && b['achieved'])) {
					// Achievements with decreasing values are considered having no progress
					var progressA = a['increasing'] ? (a['value'] / a['endValue']) : 0;
					var progressB = b['increasing'] ? (b['value'] / b['endValue']) : 0;
					if(progressA < progressB) {
						return 1;
					}
					if(progressA > progressB) {
						return -1;
					}
				}
				// If progress is the same just sort them by type
				if(a['type'] > b['type']) {
					return 1;
				}
				if(a['type'] < b['type']) {
					return -1;
				}
				return 0;
			}

			function handleResize () {
				// If the screen resolution is smaller than 600px some changes are required
				var smallSized = ($(window).width() < 600);
				if($scope['smallSized'] != smallSized) {
					$scope['smallSized'] = smallSized;
					screenSize = smallSized ? 'SMALL' : 'LARGE';
					// The size of the achievement boxes has changed (by media query), so we need to recalculate the positionings
					updateList();
					requestAngularApply();
				}
			}
			$(window).on('resize', handleResize);
			handleResize();

			$scope.$on('$destroy', function () {
				$scope['achievementsCollection'].unsubscribe(unsubscribeIndex);
			});
		}
	}
}]);
