GameLobby.app.controller('SitterController', ['$scope', 'Request', '$state', 'WindowService', 'SessionService', function ($scope, Request, $state, WindowService, SessionService) {

	if ($scope.modal.context == "edit") {
		$scope['permissions'] = $scope.modal.sitter.data.rights;
	}
	$scope['email'] = '';
	$scope['update'] = function () {
		$scope['showButton'] = !( $scope['selectedWorld'] == null || typeof($scope['avatarName']) == 'undefined' || $scope['avatarName'].trim() == '') || $scope['email'].match(/@.+\.[a-z]{2,}/);
	};
	$scope['update']();

	$scope['mylog'] = function(msg)
	{
		console.log(msg);
	};

	var session = SessionService.getSession();
	var sessionObj = Session.get(session.sessionId);

	$scope['selectedWorld'] = null;
	var worlds = GameWorld.getCollection(sessionObj.data.country);

	$scope['dropdownData'] = {
		'placeholder': 'AddSitter.ChooseGameWorld',
		'selected': null,
		'resize': true,
		'lazyRendering': false,
		'maxHeight': 110,
		'id':'gameWorldSelect',
		'appendTo':'#gameWorldSelect',
		'disregardPageScroll': true,
		'callback': function (key) {
			if(key !== null){
				$scope['selectedWorld'] = key;
			}
			$scope['update']();
		},
		'options': {}
	};
	var fillDropdown = function(){
		for (var worldNo in worlds['data']){
			$scope['dropdownData']['options'][worldNo] = worlds['data'][worldNo]['data']['worldName'];
		}
	};
	var subscribeWorlds = worlds.subscribe(fillDropdown);

	$scope.addSitter = function (avatarIdentifier) {
		var requestData;
		$scope['showButton'] = false;
		if ($scope['email'].match(/@.+\.[a-z]{2,}/)) {
			requestData = {
				'avatarIdentifier': avatarIdentifier,
				'consumersId': '',
				'avatarName': '',
				'email': $scope['email']
			}
		} else {
			requestData ={
				'avatarIdentifier': avatarIdentifier,
				'consumersId': worlds['data'][$scope['selectedWorld']]['data']['consumersId'],
				'avatarName': $scope['avatarName'],
				'email': ''
			}
		}
		Request.create(
			'sitter/add',
			requestData,
			function (data) {
				$scope.modal.close();
				$state.reload();
			},
			function (data) {
				if (data['message'].indexOf('userError') >= 0) {
					$scope['userError'] = data['message'];
				} else {
					$scope['error'] = data;
				}
				$scope['showButton'] = true;
			}
		);
	};

	$scope.setSitterPermissions = function (avatarIdentfier, sitterAccountIdentifier) {
		Request.create(
			'sitter/setPermissions',
			{
				'avatarIdentifier': avatarIdentfier,
				'sitterAccountIdentifier': sitterAccountIdentifier,
				'permissions': $scope['permissions']
			},
			function (data) {
				$scope.modal.close();
			},
			function (data) {
				$scope['error'] = data;
			}
		);
	};

}]);