GameLobby.app.controller('EditAccountnameController', ['$scope', 'Request', '$state', '$rootScope', function ($scope, Request, $state, $rootScope) {

	$scope['account'] = {
		'name': '',
		'error': '',
		'oldName': ''
	};

	function init () {
		$scope['account']['oldName'] = $rootScope['Player']['data']['avatarName'];
		$scope['account']['name'] = $scope['account']['oldName'];
	}
	init();

	$scope['saveAccountname'] = function () {
		if ($scope['account']['name'] != '' && $scope['account']['oldName'] != $scope['account']['name']) {
			Request.create('player/saveName', {'accountName': $scope['account']['name']},
				function (data) {
					$scope.modal.close();
				},
				function (err) {
					$scope['account']['error'] = err['message'];
				}
			);
		}
	};

}]);