/**
 * ng-click-like wrapper for PointerHelper
 */
/** @expose */
GameLobby.app.directive('onPointerOver', [function () {
	return function (scope, elem, attrs) {
		PointerHelper.onPointerOver(elem[0], function () {
			scope.$eval(attrs['onPointerOver']);
			requestAngularApply();
		});
		scope.$on('$destroy', function () {
			PointerHelper.removeBindings(elem[0]);
		});
	};
}]);