GameLobby.app.controller('StartController', ['$scope', 'Request', 'SessionService', 'Servertime', 'WindowService', function ($scope, Request, SessionService, ServertimeService, WindowService) {

	$scope.country = 'en';
	$scope.serverTime = ServertimeService.getServerTime();

	var session = SessionService.getSession();
	var sessionObj = Session.get(session.sessionId);
	var currentAvatar = 0; // Only used on small width design (The current displayed avatar
	$scope.selectedAvatar = null;
	$scope.avatarCount = 0;

	$scope['prestige'] = PrestigeDetails.get(session.playerId);
	var avatars = Avatar.getCollection();
	var avatarInformations = {};
	var unsubscribeAvatars = avatars.subscribe(function(collection) {
		var sorted = [];
		for(var i=0; i < collection.data.length; i++) {
			var avatar = collection.data[i];
			var info = AvatarInformation.get(avatar.data.avatarIdentifier);
			avatarInformations[avatar.data.avatarIdentifier] = info;
			//Sort by insert
			var lastLogin = info.data.lastLogin;
			var inserted=false;
			for(var k=0; k < sorted.length; k++) {
				var actualInfo = avatarInformations[sorted[k].data.avatarIdentifier];
				if(lastLogin > actualInfo.data.lastLogin) {
					sorted.splice(k, 0, avatar);
					inserted=true;
					break;
				}
			}
			if(!inserted) {
				sorted.push(avatar);
			}
		}
		$scope['avatars'] = sorted;
		if($scope.selectedAvatar==null && sorted.length > 0) {
			$scope.selectedAvatar = sorted[0];
		}
		$scope.avatarCount = sorted.length;
	});

	// Calculate the total amount of prestige gained by achievements
	var achievements = Achievement.getCollection();
	var unsubscribeAchievements = achievements.subscribe(function() {
		$scope['totalAchievementsPrestige'] = 0;
		for (var i=0; i<achievements['data'].length; i++) {
			var data = achievements['data'][i]['data'];
			for(var j = 0; j < data['completedLevels'].length; j++) {
				$scope['totalAchievementsPrestige'] += data['completedLevels'][j]['prestige'];
			}
		}
	});

	sessionObj.subscribe(function () {
		$scope['country'] = sessionObj['data']['country'];
	});

	/**
	 * Removes the avatar from the gameworld
	 * to make it seem
	 * @param avatarIdentifier
	 */
	$scope.removeAvatarOnWorld =  function(avatarIdentifier) {
		Request.create('player/deleteAvatar', {'avatarIdentifier': avatarIdentifier}, function (data) {
			for(var i = 0; i < $scope['avatars'].length; i++) {
				if($scope['avatars'][i]['data']['avatarIdentifier'] == avatarIdentifier) {
					$scope['avatars'].splice(i, 1);
					break;
				}
			}
			$scope.$emit('avatar:removed', {'avatarIdentifier': avatarIdentifier});
			$scope.avatarCount = $scope['avatars'].length;
		});
	};

	$scope.showPreviousAvatar = function() {
		if ($scope.avatars.length > 1) {
			currentAvatar --;
			if (currentAvatar < 0) {
				currentAvatar = $scope.avatars.length - 1;
			}
			$scope.selectedAvatar = $scope.avatars[currentAvatar];
		}
	};

	$scope.showNextAvatar = function() {
		if ($scope.avatars.length > 1) {
			currentAvatar++;
			if (currentAvatar > $scope.avatars.length - 1) {
				currentAvatar = 0;
			}
			$scope.selectedAvatar = $scope.avatars[currentAvatar];
		}
	};

	$scope['showPrestige'] = function(type) {
		WindowService.open({
			templateUrl: 'templates/modals/showPrestige.html',
			title: GameLobby.txt["Prestige."+type+"Worlds"],
			type: type
		});
	};

	$scope.$on('$destroy', function() {
		achievements.unsubscribe(unsubscribeAchievements);
		avatars.unsubscribe(unsubscribeAvatars);
	});
}]);