/**
 * Makes the DOM element draggable. While dragging, this will be copied to a new node that is attached to the body.
 * You can drop it on any container with the "droppable" directive. When none is found or that container returns false,
 * the element will snap back to it's old position.
 *
 * You can pass this directive some object that will be passed to the dropable directive.
 *
 * <div draggable="someObject">
 *
 */
/** @expose */
GameLobby.app.directive('draggable', ['$timeout', function ($timeout) {
	return  function (scope, elem, attrs) {
		var object = scope.$eval(attrs['draggable']);
		PointerHelper.isDraggable(elem[0], function () {
			if (attrs['onDragInit']) {
				scope.$eval(attrs['onDragInit'], {'object': object});
			}
			// Whatever is returned here will be passed as an argument to the dropable function
			return object;
		}, function () {
			if (attrs['onDragStop']) {
				scope.$eval(attrs['onDragStop'], {'object': object});
			}
		}, object);
		scope.$on('$destory', function () {
			PointerHelper.removeBindings(elem[0]);
		});
	};
}]);