GameLobby.templateModule = angular.module('templates', []);
GameLobby.app = angular.module('GameLobby', ['ui.router', 'ngCookies', 'ngSanitize', 'ngLocale', 'templates', 'angulartics', 'angulartics.piwik', 'angulartics.google.analytics']);


GameLobby.app.config(['$stateProvider', '$urlRouterProvider', '$analyticsProvider', '$compileProvider', function ($stateProvider, $urlRouterProvider, $analyticsProvider, $compileProvider) {
	$compileProvider.debugInfoEnabled(false);
	$analyticsProvider.virtualPageviews(false);
	$stateProvider
		.state('start', {
			url: "/start",
			templateUrl: "templates/pages/start.html",
			controller: 'StartController'
		})
		.state('newGameworld', {
			url: "/newGameworld",
			templateUrl: "templates/pages/gameworld-new.html",
			controller: 'NewGameworldController'
		})
		.state('gameworld', {
			url: "/gameworld/:id",
			templateUrl: "templates/pages/gameworld.html",
			controller: 'GameworldController'
		})
		.state('gameworldBanDetails', {
			url: "/gameworldBanDetails/:ban",
			templateUrl: "templates/pages/gameworld.html",
			controller: 'GameworldController'
		})
		.state('imprint', {
			url: "/imprint",
			templateUrl: "templates/pages/iframe.html",
			controller: 'IframeController'
		})
		.state('rules', {
			url: "/rules",
			templateUrl: "templates/pages/iframe.html",
			controller: 'IframeController'
		})
		.state('gameRules', {
			url: "/gameRules",
			templateUrl: "templates/pages/iframe.html",
			controller: 'IframeController'
		})
		.state('terms', {
			url: "/terms",
			templateUrl: "templates/pages/iframe.html",
			controller: 'IframeController'
		})
		.state('privacy', {
			url: "/privacy",
			templateUrl: "templates/pages/iframe.html",
			controller: 'IframeController'
		})
		.state('account', {
			url: "/account",
			templateUrl: "templates/pages/account.html",
			controller: 'AccountController'
		})
		.state('achievements', {
			url: "/achievements",
			templateUrl: "templates/pages/achievements.html",
			controller: 'AchievementsController'
		})
		.state('logout', {
			url: "/logout",
			controller: 'LogoutController'
		});

	$urlRouterProvider.otherwise("/start");
}]);

GameLobby.app.run(['SessionService', '$rootScope', 'MellonService', 'WindowService', '$analytics', '$state', function (SessionService, $rootScope, MellonService, WindowService, $analytics, $state) {
	/**
	 * GameLobby.tick should be used instead of own timeouts/intervals
	 */
	GameLobby.tick = new Tick($rootScope);
	GameLobby.rootScope = $rootScope;
	GameLobby.rootScope['$state'] = $state;
	var userIdTracked=false;

	function setUserId() {
		if(userIdTracked) {
			return;
		}
		if(!$rootScope['Player']) {
			return;
		}
		if(!$rootScope['Player']['data']) {
			return;
		}
		if(!$rootScope['Player']['data']['userAccountIdentifier']) {
			return;
		}
		if(typeof(_paq) != 'undefined') {
			_paq.push(['setUserId', $rootScope['Player']['data']['userAccountIdentifier']]);
			userIdTracked = true;
		}
	}

	$rootScope.$on('$stateChangeSuccess', function(event, toState, toParams, fromState, fromParams, error){
		setUserId();
		var url = toState['url'];
		$analytics.pageTrack(url);
	});

	GameLobby.translate = function (key, replaces) {
		if (typeof (GameLobby.txt[key]) != 'undefined') {
			if (!replaces) {
				return GameLobby.txt[key];
			} else {
				var tmp = GameLobby.txt[key];
				for (var key in replaces) {
					tmp = tmp.replace("{{" + key + "}}", replaces[key]);
				}
				return tmp;
			}
		}
		else {
			return '!!' + key + '!!';
		}
	};

	GameLobby['mousePosition'] = {
		'x': 0,
		'y': 0
	};

	$('body').on('mousemove', function (e) {
		GameLobby['mousePosition']['x'] = e.clientX;
		GameLobby['mousePosition']['y'] = e.clientY;
		$rootScope.$broadcast('mousemove', GameLobby['mousePosition']);
	});

	MellonService.inject();
	SessionService.init();

	$rootScope.mellonUrl = MellonService.getUrlHelper();
	$rootScope.languageMapping = GameLobby.languageNames;
	$rootScope['Math'] = Math;
}]);