/**
 * Events from the API are going to be automagically prefixed with api and properly camelCased by the globalState
 * Handler. A typically Event from the API would look like "userUpdate" before.
 *
 * @param {Scope} $rootScope
 */
GameLobby.app.service('GlobalState', ['$rootScope', 'Servertime', '$state', function ($rootScope, ServertimeService) {
	'use strict';
	var initialEvent = true;

	var handleResponse = function (response) {
		if (typeof response['cache'] === 'object') {
			handleServerCache(response);
		}
		if (typeof response['event'] === 'object') {
			var evt = response['event'];
			$rootScope.$broadcast(evt['name'], evt['data']);
		}
		if (typeof response['time'] != 'undefined') {
			ServertimeService.setServerTime(response['time']);
		}

		response = response['response'];

		requestAngularApply();
		return response;
	};

	var handleServerCache = function (cacheObjects) {
		if (cacheObjects['cache'].length > 0) {
			Cache.fill(cacheObjects, cacheObjects['serialNo']);
		}
	};

	function GlobalStateSrv () {
		this.handleResponse = function (response) {
			if (typeof(response['error']) != 'undefined' && response['error']['message'] == 'Authentication failed') {
				$rootScope.$broadcast('forceLogout');
			}
			// check for our special api format and treat it
			if (typeof response === 'object' && typeof response['response'] === 'object') {
				return handleResponse(response);
			}

			return response;
		};
	}

	return new GlobalStateSrv();
}]);