/**
 * Creates a cacheable image of the players' avatar.
 *
 * Example: <hero-face avatar-identifier="5" avatar-class="profile"></hero-face>
 *
 * Usable attributes are:
 * avatar-identifier        int:    id of the player which avatar should be displayed. This parameter is a MUST HAVE.
 * user-account-identifier        int:    id of the player which avatar should be displayed. This parameter is a MUST HAVE.
 * avatar-class        string:    any class which should added to the wrapper <div> of the image.
 * dead:		string: if true, then the skeleton will be displayed
 * size:		string: If you pass "big", then the 350x350 images will be used instead of the 125x125
 * noShoulders: string: if true, then only the face itself will be displayed. useful when you render the whole hero with body
 */

/** @expose */
GameLobby.app.directive('heroFace', [function () {
	var direction = GameLobby.config['direction'] || 'ltr';
	var avatarImgStorage = new ImgStorage(GameLobby.Features['cdnPrefix']+'images-' + direction + '/hero/');
	var imgCache = {};
	var imgWatches = {};
	return {
		restrict: 'E',
		templateUrl: 'templates/partials/avatar/image.html',
		scope: {
			'size': '@',
			'hideHair': '=',
			'avatar': '=',
			'showDefault' : '@?'
		},
		link: function ($scope, elem, attrs) {
			var unsubscribeId = 0;
			var heroFace = null;
			var heroViewModel = null;
			$scope['gender'] = undefined;
			$scope['class'] = '';
			$scope['showDefaultImage'] = false;

			var currentCanvas = null;
			var scaleFactor = 1;
			var isDead = false;
			var noShoulders = false;

			var renderTimeout = null;
			function renderInCanvas() {

				var parentElem = elem[0].querySelector('.heroImage');
				if (currentCanvas != null) {
					parentElem.removeChild(currentCanvas);
				}
				$scope['showDefaultImage'] = false;

				var resolution = '125x125';
				if (attrs['size'] == 'big') {
					resolution = '350x350';
				}
				if (attrs['scale'] !== undefined) {
					scaleFactor = parseFloat(attrs['scale']);
				}

				renderTimeout = null;
				var fileList = [];
				if (heroViewModel == null || heroViewModel['gender'] === undefined || heroViewModel['face'] == undefined || heroViewModel['face']['eye'] === undefined) {
					if($scope["showDefault"] ) {
						$scope['showDefaultImage'] = true;
					}
					return;
				}
				$scope['gender'] = heroViewModel['gender'];

				var fileName = heroViewModel['gender']+"/head/"+resolution+"/face0";
				if (isDead) {
					fileName += '_dead';
				}
				if (noShoulders) {
					fileName += '_1';
				}
				fileName += '.png';
				fileList.push(fileName);
				var zIndexList = [];
				zIndexList.push({'url': fileName, 'z': 10});
				if (!isDead) {
					for (var key in heroViewModel['face']) {
						var value = heroViewModel['face'][key];
						if ((value.type != 'None') && (key != 'hair' || !$scope['hideHair'])) {
							var fileName = heroViewModel['gender']+"/head/"+resolution+"/"+key+"/"+key+value.type+".png";
							fileList.push(fileName);
							zIndexList.push({'url': fileName, 'z': value['z']});
						}
					}
				}
				zIndexList.sort(function(a,b) {
					if (a['z']<b['z']) {
						return -1;
					} else {
						return 1;
					}
				});

				avatarImgStorage.load(fileList, function(allImages) {
					currentCanvas = document.createElement('canvas');
					var context = currentCanvas.getContext("2d");
					for (var i=0; i<zIndexList.length; i++) {
						var fileName = zIndexList[i]['url'];
						if (i==0) {
							currentCanvas.width = allImages[fileName].width * scaleFactor;
							currentCanvas.height = allImages[fileName].height * scaleFactor;
						}
						context.drawImage(allImages[fileName],0,0, allImages[fileName].width*scaleFactor, allImages[fileName].height * scaleFactor);
					}
					parentElem.appendChild(currentCanvas);
					if(!$scope.$$phase) {
						$scope.$digest();
					}
				});
			}

			function reRender() {
				if (renderTimeout == null) {
					renderTimeout = window.setTimeout(renderInCanvas, 50);
				}
			}

			function reset(newHeroFaceCacheObject) {
				if (currentCanvas != null) {
					var parentElem = elem[0].querySelector('.heroImage');
					parentElem.removeChild(currentCanvas);
					currentCanvas = null;
				}
				heroViewModel = {};
				if (heroFace && unsubscribeId) {
					heroFace.unsubscribe(unsubscribeId);
				}
				heroFace = newHeroFaceCacheObject;
				unsubscribeId = heroFace.subscribe(function () {
					heroViewModel = heroFace.getViewModel();
					reRender();
				});
			}

			// Get the face of a specific avatar identifier
			attrs.$observe('avatarIdentifier', function (val) {
				var avatarIdentifier = parseInt(val);
				if (avatarIdentifier > 0) {
					var avatarImage = AvatarImage.get(parseInt(val));
					reset(avatarImage);
				}
			});

			var avatarImage = null;

			// Get the face of a specific user Account
			attrs.$observe('userAccountIdentifier', function (val) {
				var userAccountIdentifier = parseInt(val);
				if(avatarImage) {
					avatarImage.unsubscribe();
				}
				if(userAccountIdentifier > 0) {
					avatarImage = UserAccountImage.get(userAccountIdentifier);
					avatarImage.subscribe(function(){
						reset(avatarImage);
					});
					reset(avatarImage);
				}
			});

			attrs.$observe('avatarClass', function (val) {
				if (val) {
					$scope['class'] = val;
					reRender();
				}
			});

			attrs.$observe('dead', function (val) {
				isDead = (val == 'true');
				reRender();
			});

			attrs.$observe('noShoulders', function (val) {
				noShoulders = (val == 'true');
				reRender();
			});

			$scope.$watch('hideHair', reRender);

			$scope.$on('$destroy', function () {
				if (heroFace && unsubscribeId) {
					heroFace.unsubscribe(unsubscribeId);
				}
			})
		}
	}
}]);
