function Avatar () {
	this.defaultData = {
		'id': 0,
		'consumersId': 0,
		'userAccountIdentifier': 0,
		'avatarIdentifier': 0,
		'avatarName': '',
		'worldName' : '',
		'limitation' : 0,
		'isBanned' : false,
		'isSuspended': false,
		'banReason' : 0,
		'banPaymentProvider' : ''
	};

	this.data = {};
	this.set(this.defaultData, true);
	this.name = 'Avatar';
}

Avatar.ACTION_ISOLATED = 1;
Avatar.ACTION_LOCK = 2;

Avatar.prototype = new AbstractModel();
Cache.register(Avatar);
