/**
 * @constructor
 * @param {Object} modelClass
 */
function AbstractCollection (modelClass) {
	this.parent = this;
	this.cacheService = Cache;
	this.modelClass = modelClass;
	this.data = new Array();

	this.set = function (data) {
		// REPLACE
		if (data.operation == 1) {
			this.data.splice(0, this.data.length);
			var newObjects = this.cacheService.fill(data);
			for (var i = 0; i < newObjects.length; i++) {
				this.data.push(newObjects[i]);
			}
		} else {
			// Every other operation is invalid until we have the initial data for this collection
			if (!this.filled) {
				// But maybe REPLACE is valid, too. I'm not really sure. But I currently need it for chat lines.
				if (data.operation != 5) {
					return false;
				}
			}
		}
		// ADD
		if (data.operation == 2) {
			// Da a REPLACE-OR-ADD instead. Slower, but safer. We had a problem where the user received the same adventure twice. Once via normal collection get and once via push.
			data.operation = 5;
			/*
			 var newObjects = this.cacheService.fill(data);
			 for ( var i = 0; i < newObjects.length; i++ )
			 {
			 this.data.push(newObjects[i]);
			 }*/
		}
		// REMOVE
		if (data.operation == 3) {
			// (We don't delete objects in the cache. We only remove it from the collection (objects might be used
			// somewhere else)
			for (var i = 0; i < data.cache.length; i++) {
				var r = data.cache[i];
				// Get the Object for this entry
				var obj = this.cacheService.c[r.name];
				if (typeof (obj) != "undefined") {
					// Search it in our collection
					for (var j = 0; j < this.data.length; j++) {
						if (this.data[j] == obj) {
							// Delete it
							this.data.splice(j, 1);
							break;
						}
					}
				}
			}
		}
		// INNER UPDATE
		if (data.operation == 4) {
			var newObjects = this.cacheService.fill(data);
		}
		// ADD OR REPLACE
		if (data.operation == 5) {
			var newObjects = this.cacheService.fill(data);
			for (var i = 0; i < newObjects.length; i++) {
				// Check if this object is in the list
				var newId = newObjects[i].serverId;
				var found = false;
				for (var j = 0; j < this.data.length; j++) {
					if (this.data[j].serverId == newId) {
						found = true;
						break;
					}
				}
				if (!found) {
					this.data.push(newObjects[i]);
				}
			}
		}
		// Any data in here?
		if ((this.data.length > 0) && (this.data[0].name)) {
			// Was this a valid className?
			if (typeof(window[this.data[0].name]) != 'undefined') {
				// Does this class want to sort the collection?
				if (typeof(window[this.data[0].name].collectionSortFct) == 'function') {
					this.data.sort(window[this.data[0].name].collectionSortFct);
				}
			}
		}
	};
}
Cache.register(AbstractCollection, 'Collection:');
Cache.collectionModel = AbstractCollection;
