GameLobby.app.controller('NewGameworldController', ['$scope', 'Request', 'Loading', 'WindowService', 'SessionService', function ($scope, Request, Loading, WindowService, SessionService) {

	var loading = Loading.get('newGameworld');
	loading.start();

	$scope['gameWorlds'] = {
		'recommended': [],
		'other': []
	};
	var cluster = [];
	var session = SessionService.getSession();
	var sessionObj = Session.get(session.sessionId);
	var unsubscribeSession = sessionObj.subscribe(function () {
		$scope['selectedCountry'] = sessionObj['data']['country'];
		Request.create('gameworld/getPossibleNewGameworlds', {}, updateGameWorlds);
	});

	function getFlagsForGameWorld (gameWorld) {
		var flags = [];
		//get the flags
		switch (gameWorld['country']) {
			case 'en':
				flags.push('en');
				if ($scope['selectedCountry'] != 'en' && typeof $scope['selectedCountry'] != 'undefined') {
					flags.push($scope['selectedCountry']);
				}
				break;
			case 'gb':
			case 'us':
				flags.push(gameWorld['country']);
				break;
			default:
				flags = cluster;
		}

		// and sort them so my selected flag is always first
		for (var j = 0; j < flags.length; j++) {
			if (flags[j] == $scope['selectedCountry'] && j > 0) {
				var selectedCountryFlag = flags.splice(j, 1);
				flags.unshift(selectedCountryFlag[0]);
			}
		}

		return flags;
	}

	function updateGameWorlds (data) {
		cluster = data['cluster'];

		for (var i = 0; i < data['other'].length; i++) {
			data['other'][i]['flags'] = getFlagsForGameWorld(data['other'][i]);
			data['other'][i]['completeDuration'] = data['other'][i]['speedGame'] == 1 ? GameWorld.SERVER_LENGTH.NORMAL_SPEED : GameWorld.SERVER_LENGTH.OTHER_SPEED;
		}
		for (var i = 0; i < data['recommended'].length; i++) {
			data['recommended'][i]['flags'] = getFlagsForGameWorld(data['recommended'][i]);
			data['recommended'][i]['completeDuration'] = data['recommended'][i]['speedGame'] == 1 ? GameWorld.SERVER_LENGTH.NORMAL_SPEED : GameWorld.SERVER_LENGTH.OTHER_SPEED;
		}

		$scope['gameWorlds'] = data;
		loading.stop();
	}


	$scope['enterBetaKeyDialog'] = function (consumersId) {
		WindowService.open({
			templateUrl: 'templates/modals/enterBetaKey.html',
			title: GameLobby.txt["EnterBetaKey.Headline"],
			consumersId: consumersId
		});
	};

	$scope.$on('destroy', function () {
		if (!sessionObj) {
			return;
		}
		sessionObj.unsubscribe(unsubscribeSession);
	})

}]);