/** @expose */
GameLobby.app.filter('clusterName', function () {
	return function (clusterId) {
		if (clusterId == GameWorld.PTR_CLUSTER) {
			return GameLobby.txt['labelPtrDropdown'];
		}

		var countries = window['GameLobby'].Clusters[clusterId];
		if (typeof(countries) == 'undefined') {
			return '--';
		}

		var languageIdToName = function (languageId) {
			var name = window['GameLobby'].languageNames[languageId];
			return typeof (name) != 'undefined' ? name : '--';
		};

		return countries.map(languageIdToName).join(' / ');
	};
});