var PointerHelper = new (function () {
	var STATUS_IDLE = 0;
	var STATUS_SCROLLING = 1;
	var STATUS_ZOOMING = 2;
	var pointerStatus = {down: false,
		firstTouchID: -1,
		touches: [],
		touchesStart: [],
		lastZoomLevel: 1,
		status: STATUS_IDLE,
		start: {'x': 0, 'y': 0, 'obj': null},
		current: {'x': 0, 'y': 0, 'clientX': 0, 'clientY': 0},
		dragObjStyle: {'top': 0, 'left': 0, 'display': ''},
		startTime: 0,
		dropObjParam : null,
		dragObj: null,
		dragObjClone: null,
		currentObj: null,
		oldObj: null};
	var pointer = 'mouse';
	var myself = this;
	var UserAgent = navigator.userAgent.toLowerCase();
	var viewPortScale = 1;
	var oldDragPosition = {};

	var globalListeners = {'down': [], 'move': [], 'up': []};

	var oldTouchBrowserScrollEnabled = true;
	var resetTouchBrowserScrollOnUp = false;
	this.touchBrowserScrollEnabled = true;
	this.disableMouseWheel = false;

	// Liefert absolute Bildschirmkoordinaten zu einem Objekt
	function getObjectPosition (object) {
		var left = 0;
		var top = 0;

		while (object.offsetParent) {
			left += object.offsetLeft;
			top += object.offsetTop;
			object = object.offsetParent;
		}
		left += object.offsetLeft;
		top += object.offsetTop;
		return {
			x: left,
			y: top
		};
	}

	/**
	 * Fügt einem Mausevent die Attribute pageX und pageY hinzu, sollten sie nicht existieren (wie im IE)
	 * @param evt Mausevent
	 * @returns Mausevent mit pageX und pageY
	 */
	function makeIEcompatible (evt) {
		if (!evt) evt = window.event;
		if (pointer == 'touch') {
			// IE 10 kompatibel machen
			if (typeof(evt.changedTouches) == "undefined") {
				evt.identifier = evt.pointerId;
				evt.changedTouches = [evt];
				evt.touches = [evt];
			}
			//evt.stopPropagation();
			return evt;
		}
		if ((!evt.pageX) && (!evt.pageY)) {
			evt.pageX = evt.clientX + document.body.scrollLeft;
			evt.pageY = evt.clientY + document.body.scrollTop;
		}
		return evt;
	}

	/**
	 * Liefert die aktuellen Koordinaten der Maus bzw des letzten Touchpunkts zurück
	 */
	this.getCurrentCoords = function () {
		return pointerStatus.current;
	};

	this.getStatus = function () {
		return pointerStatus;
	};

	/**
	 * Initialisiert automatisch Maus oder Touchsteuerung, wenn der Browser sich als Mobilbrowser ausgibt
	 */
	this.initForDetectedDevice = function (parentID) {
		if (UserAgent.search(/(iphone|ipod|ipad|opera mini|fennec|palm|blackberry|android|symbian|touch;)/) > -1) {
			myself.initTouch();
		} else {
			myself.initMouse();
		}
	};

	function callGlobalListeners (type) {
		// Alle registrierten MoveCallbacks ausführen
		for (var i = 0; i < globalListeners[type].length; i++) {
			if (globalListeners[type][i] != null)
				globalListeners[type][i](pointerStatus);
		}
	}

	/**
	 * Initialisiert Eingaben per Maus.
	 * Als parentID muss die ID des übergeordnedsten DOM-Elements übergeben werden.
	 * Ansonsten wird alles an "document" gehängt.
	 */
	this.initMouse = function (parentID) {
		pointer = 'mouse';
		var domNode = document;
		if (arguments.length > 0)
			domNode = document.getElementById(parentID);
		document.onmouseout = function (evt) {
			if (evt === undefined || evt.pageX === undefined) {
				evt = makeIEcompatible(evt);
			}
			// Stop any drag&drop when the cursor leaves the browser window
			if ((evt.relatedTarget == null) || (evt.relatedTarget.tagName == 'HTML')) {
				pointerStatus.down = false;
				dragEnd();
			}
		};
		domNode.onmousemove = function (evt) {
			if (evt === undefined || evt.pageX === undefined) {
				evt = makeIEcompatible(evt);
			}
			pointerStatus.current.x = evt.pageX;
			pointerStatus.current.y = evt.pageY;
			pointerStatus.current.clientX = evt.clientX;
			pointerStatus.current.clientY = evt.clientY;
			if (pointerStatus.dragObj != null)
				dragUpdate();
			callGlobalListeners('move');
		};
		domNode.onmousedown = function (evt) {
			if (evt === undefined || evt.pageX === undefined) {
				evt = makeIEcompatible(evt);
			}
			pointerStatus.down = true;
			pointerStatus.start.x = evt.pageX;
			pointerStatus.start.y = evt.pageY;
			pointerStatus.current.x = evt.pageX;
			pointerStatus.current.y = evt.pageY;
			pointerStatus.current.clientX = evt.clientX;
			pointerStatus.current.clientY = evt.clientY;
			pointerStatus.currentObj = document.elementFromPoint(pointerStatus.current.clientX, pointerStatus.current.clientY);
			pointerStatus.start.obj = pointerStatus.currentObj;
			callGlobalListeners('down');
		};

		//	domNode.ondragstart=function() {return false;};
		domNode.onmouseup = function (evt) {
			pointerStatus.down = false;
			dragEnd();
			callGlobalListeners('up');
		};
		// FF kann scheinbar nicht auf DOM-objekte binden. Daher global und weiter verteilen auf das Element unter der Maus
		if (window.addEventListener) {
			window.addEventListener('DOMMouseScroll', function (evt) {
				pointerStatus.currentObj = document.elementFromPoint(pointerStatus.current.x, pointerStatus.current.y);
				return handleWheelEvent(evt, pointerStatus.currentObj, 'onmousewheel');
			}, false);
		}
	};

	/**
	 * Initialisiert Eingaben per Touchscreen.
	 * Als parentID muss die ID des übergeordnedsten DOM-Elements übergeben werden.
	 * Ist scheinbar nötig, da es in "Wichtel" bei einem Test nicht geklappt hat, an "document" zu binden. Dann wurden nur Touches in der linken oberen Ecke erkannt.
	 */
	this.initTouch = function (parentID) {
		if (pointer != "touch") {
			myself.initMouse = function () {};
			pointer = 'touch';

			var domNode = document;
			if (arguments.length > 0) {
				domNode = document.getElementById(parentID);
			}

			domNode.ontouchstart = function (evt) {
				evt = makeIEcompatible(evt);
				pointerStatus.currentObj = evt.target;
				pointerStatus.down = true;
				pointerStatus.touches = evt.touches;
				// Startpunkte für jeden Touch speichern. Wird nachher zB für den Zoom benötigt.
				for (var i = 0; i < evt.changedTouches.length; i++) {
					var tID = evt.changedTouches[i].identifier;
					pointerStatus.touchesStart[tID] = {'x': evt.changedTouches[i].clientX, 'y': evt.changedTouches[i].clientY};
				}
				pointerStatus.start.obj = pointerStatus.currentObj;
				if ((pointerStatus.currentObj != null) && (pointerStatus.currentObj.tagName == 'INPUT')) {
					//console.log("Aborting touch");
					return true;
				}
				// Wenn das der erste Finger ist, dann hier einen touchStart Event triggern
				if (pointerStatus.touches.length == 1) {
					pointerStatus.firstTouchID = pointerStatus.touches[0].identifier;
					pointerStatus.startTime = new Date().getTime();
					updateTouchPoint(evt, true);
					pointerStatus.start.x = pointerStatus.current.x;
					pointerStatus.start.y = pointerStatus.current.y;
					firePointerEvent(pointerStatus.currentObj, 'touchStart');
					firePointerEvent(pointerStatus.currentObj, 'touchOver');
				}
				callGlobalListeners('down');
				//console.log("Touch start element: "+debugObject(pointerStatus.currentObj));
				if (!myself.touchBrowserScrollEnabled) {
					//console.log("Touch start returned false");
					evt.preventDefault();
					return false;
				} else {
					//console.log("Touch start returned true");
				}
			};
			domNode.ontouchmove = function (evt) {
				evt = makeIEcompatible(evt);
				pointerStatus.currentObj = evt.target;
				updateTouchPoint(evt);
				if ((pointerStatus.currentObj != null) && (pointerStatus.currentObj.tagName == 'INPUT')) {
					return true;
				}
				pointerStatus.touches = evt.touches;
				pointerStatus.oldObj = pointerStatus.currentObj;
				if (pointerStatus.dragObj != null)
					dragUpdate();
				callGlobalListeners('move');
				if (!myself.touchBrowserScrollEnabled) {
					//console.log(" === scrolling disabled. move returning false");
					evt.preventDefault();
					return false;
				} else {
					//console.log(" move returning true");
				}
			};
			domNode.ontouchend = function (evt) {
				evt = makeIEcompatible(evt);
				if (pointerStatus.currentObj != null) {
					firePointerEvent(pointerStatus.currentObj, 'touchOut');
				}
				pointerStatus.currentObj = evt.target;
				pointerStatus.down = false;
				pointerStatus.endTime = new Date().getTime();
				pointerStatus.touches = evt.touches;
				//console.log("Touch end. "+(pointerStatus.endTime-pointerStatus.startTime)+"ms. element: "+debugObject(pointerStatus.currentObj));
				if ((pointerStatus.currentObj != null) && (pointerStatus.currentObj.tagName == 'INPUT')) {
					return true;
				}
				if (resetTouchBrowserScrollOnUp) {
					myself.resetTouchScroll();
				}
				callGlobalListeners('up');
				// Wenn der User gerade am Zoomen war, dann den Zoom auf jeden Fall auflösen
				if (pointerStatus.status == STATUS_ZOOMING) {
					// Erst dann wieder auf IDLE setzen, wenn der letzte Finger vom Display weg ist.
					if (pointerStatus.touches.length == 0) {
						firePointerEvent(pointerStatus.currentObj, 'touchZoomEnd', true, pointerStatus.lastZoomLevel);
						pointerStatus.status = STATUS_IDLE;
					}
				} else {
					// Hat vorher nicht gezoomt und der letzte Finger weg? Dann touchOut feuern
					if (pointerStatus.touches.length == 0 || (pointerStatus.touches.length === 1 && pointerStatus.touches[0].type === "MSPointerUp")) {
						firePointerEvent(pointerStatus.currentObj, 'touchEnd');
						pointerStatus.firstTouchID = -1;
					}
				}
				if ((pointerStatus.endTime - pointerStatus.startTime) < 500) {
					firePointerEvent(pointerStatus.currentObj, 'touchClick');
				}

				dragEnd();
				if (!myself.touchBrowserScrollEnabled) {
					//					console.log(" === scrolling disabled. returning false");
					return false;
				}
			};
			domNode.addEventListener("MSPointerDown", domNode.ontouchstart, false);
			domNode.addEventListener("MSPointerMove", domNode.ontouchmove, false);
			domNode.addEventListener("MSPointerUp", domNode.ontouchend, false);
		}
	};

	function elementFromDocumentPoint (x, y) {
		return window.document.elementFromPoint(x, y);
	}

	function updateTouchPoint (evt, isFirstDown) {
		if (pointerStatus.touches.length == 1) {
			var touch = pointerStatus.touches[0];
			var winPageX = window.pageXOffset,
				winPageY = window.pageYOffset,
				x = touch.clientX,
				y = touch.clientY;
			if (touch.pageY === 0 && Math.floor(y) > Math.floor(touch.pageY) ||
				touch.pageX === 0 && Math.floor(x) > Math.floor(touch.pageX)) {
				// iOS4 clientX/clientY have the value that should have been
				// in pageX/pageY. While pageX/page/ have the value 0
				x = x - winPageX;
				y = y - winPageY;
			} else
				if (y < (touch.pageY - winPageY) || x < (touch.pageX - winPageX)) {
					// Some Android browsers have totally bogus values for clientX/Y
					// when scrolling/zooming a page. Detectable since clientX/clientY
					// should never be smaller than pageX/pageY minus page scroll
					x = touch.pageX - winPageX;
					y = touch.pageY - winPageY;
				}
			pointerStatus.current.x = x;
			pointerStatus.current.y = y;
			pointerStatus.current.clientX = pointerStatus.current.x;
			pointerStatus.current.clientY = pointerStatus.current.y;
		} else {
			var oldRect = new Array();
			var rect = new Array();
			pointerStatus.current.x = 0;
			pointerStatus.current.y = 0;
			// Die pointerStatus.current Variable updaten mit den Koordinaten des Fingers, der zuerst auf dem Screen war.
			// Gleichzeitig die Veränderung in den Fingerbewegungen seit start messen (zum Zoomen)
			for (var i = 0; i < pointerStatus.touches.length; i++) {
				var id = pointerStatus.touches[i].identifier;
				oldRect.push({x: pointerStatus.touchesStart[id].x, y: pointerStatus.touchesStart[id].y});
				rect.push({x: evt.touches[i].clientX, y: evt.touches[i].clientY});
				// Nur wenn nicht gezoomt wird die Koordinaten des ersten Fingers updaten. Ansonsten interessieren sie nur fürs Zoomen selber.
				if (pointerStatus.status != STATUS_ZOOMING) {
					if (id == pointerStatus.firstTouchID) {
						pointerStatus.current.x = evt.touches[i].clientX;
						pointerStatus.current.y = evt.touches[i].clientY;
					}
				} else {
					pointerStatus.current.x += evt.touches[i].clientX;
					pointerStatus.current.y += evt.touches[i].clientY;
				}
			}
			if (pointerStatus.status == STATUS_ZOOMING) {
				pointerStatus.current.x /= 2;
				pointerStatus.current.y /= 2;
			}
			// Zoom?
			if (pointerStatus.touches.length == 2) {
				var oldDist = Math.sqrt(Math.pow(oldRect[0].x - oldRect[1].x, 2) + Math.pow(oldRect[0].y - oldRect[1].y, 2));
				var newDist = Math.sqrt(Math.pow(rect[0].x - rect[1].x, 2) + Math.pow(rect[0].y - rect[1].y, 2));
				// Wenn vorher noch nicht gezoomt wurde, dann touchZoomStart feuern
				if (pointerStatus.status != STATUS_ZOOMING)
					firePointerEvent(pointerStatus.currentObj, 'touchZoomStart', true, newDist / oldDist);
				pointerStatus.lastZoomLevel = newDist / oldDist;
				pointerStatus.status = STATUS_ZOOMING;
				firePointerEvent(pointerStatus.currentObj, 'touchZoom', true, newDist / oldDist);
			}
		}
		if (pointerStatus.status != STATUS_ZOOMING) {
			pointerStatus.currentObj = elementFromDocumentPoint(pointerStatus.current.x, pointerStatus.current.y);
			if (!isFirstDown) {
				if (pointerStatus.oldObj != pointerStatus.currentObj) {
					firePointerEvent(pointerStatus.oldObj, 'touchOut');
					firePointerEvent(pointerStatus.currentObj, 'touchOver');
				} else {
					firePointerEvent(pointerStatus.currentObj, 'touchMove');
				}
			}
		}
	}

	function debugObject (obj) {
		if (obj == null)
			return "(null)";
		if (obj.src) {
			return obj.id + " src:" + obj.src + " (" + obj.tagName + ")";
		} else
			if (obj.style) {
				return obj.id + " class:" + obj.className + " (" + obj.tagName + ") style: " + obj.style.cssText.substr(0, 40);
			} else {
				return obj.id + " class:" + obj.className + " (" + obj.tagName + ")";
			}
	}

	/**
	 * Feuert den angegebenen Event auf das Objekt ab.
	 * @param obj Das oberste Element, auf das das Event gefeuert wird. Es "bubbled" automatisch bis zum body hoch.
	 * @param eventName Der Name, der identisch zu dem per bindObjWithEvent vergebenen ist.
	 * @param bubble Soll das Event nach oben gereicht werden, bis es einen Listener dafür gibt?
	 */
	function firePointerEvent (obj, eventName, bubble, additionalParam) {
		if (obj == null)
			return true;
		//		console.log(eventName+"? :"+debugObject(obj));
		if (typeof(obj.pointerListeners) != "undefined") {
			//			console.log("Fire "+eventName+" !! -> "+debugObject(obj));
			var result = true;
			if (!obj.muteEvents)
				result = obj.pointerListeners.fire(eventName, additionalParam);
			if (result === false)
				return false;
		}
		if (bubble || arguments.length < 3)
			firePointerEvent(obj.parentNode, eventName, true, additionalParam);
	}

	function domListenerList () {
		var lists = new Array();

		this.removeAll = function (eventName) {
			if ((arguments.length == 0) || (eventName == null)) {
				lists = new Array();
				return true;
			}
			lists[eventName] = new Array();
		};

		this.remove = function (fct) {
			for (var eventName in lists) {
				for (var i = 0; i < lists[eventName].length; i++) {
					if (lists[eventName][i].fct == fct) {
						lists[eventName].splice(i, 1);
						i--;
					}
				}
			}
		};

		this.add = function (eventName, fct, param) {
			if (typeof(lists[eventName]) == "undefined")
				lists[eventName] = new Array();
			var l = lists[eventName];
			var found = false;
			for (var i = 0, len = l.length; i < len; i++) {
				if (fct == l[i].fct)
					found = true;
			}
			if (!found)
				lists[eventName].push({fct: fct, param: param});
		};
		this.fire = function (eventName, additionalParam) {
			if (typeof(lists[eventName]) == "undefined")
				return null;
			var l = lists[eventName];
			var returnVal = true;
			var firedOnce = false;
			//try {
			for (var i = 0, len = l.length; i < len; i++) {
				var ret = l[i].fct(l[i].param, additionalParam);
				firedOnce = true;
				if (ret === false)
					returnVal = false;
			}
			/*} catch (ex) {
			 pointerException(ex);
			 } finally {
			 return false;
			 }*/
			return returnVal;
		};
	}

	function didMove () {
		if ((Math.abs(pointerStatus.start.x - pointerStatus.current.x) < 5) && (Math.abs(pointerStatus.start.y - pointerStatus.current.y) < 5)) {
			return false;
		} else {
			return true;
		}
	}

	/**
	 * Interne Methode. Bindet das Objekt ja nach verwendetem Interface an das Maus- oder Touchevent.
	 */
	function bindObjWithEvent (obj, mouseEventName, touchEventName, fct, param) {
		if (obj == null)
			return false;
		if (typeof(obj.pointerListeners) == "undefined") {
			obj.pointerListeners = new domListenerList();
			obj.muteEvents = false;
			if (pointer == 'mouse') {
				obj.onclick = function () {if (!didMove()) return firePointerEvent(this, 'onclick');};
				obj.ondblclick = function () {return firePointerEvent(this, 'ondblclick');};
				obj.onmousedown = function () {return firePointerEvent(this, 'onmousedown');};
				obj.onmouseup = function () {return firePointerEvent(this, 'onmouseup');};
				obj.onmousewheel = function (evt) {return handleWheelEvent(evt, this, 'onmousewheel');};
			}
		}
		if (pointer == 'mouse') {
			if ((mouseEventName == 'onmouseover') && (typeof(obj['mouseEventName']) == 'undefined')) {
				obj.onmouseover = function () {return firePointerEvent(this, 'onmouseover');};
			}
			if ((mouseEventName == 'onmouseout') && (typeof(obj['mouseEventName']) == 'undefined')) {
				obj.onmouseout = function (event) {
					var e = event.toElement || event.relatedTarget;
					if (e == this) {
						return;
					}
					for (var depth = 0; depth < 6; depth++) { // 6 is a very random number. Let's hope we don't have uglier nested elements.
						if (e == null) {
							break;
						}
						if (e.parentNode == this) {
							return;
						}
						e = e.parentNode;
					}
					return firePointerEvent(this, 'onmouseout', false);
				};
			}
			if ((mouseEventName == 'onmousemove') && (typeof(obj['mouseEventName']) == 'undefined')) {
				obj.onmousemove = function () {return firePointerEvent(this, 'onmousemove');};
			}
			obj.pointerListeners.add(mouseEventName, fct, param);
		} else {
			obj.pointerListeners.add(touchEventName, fct, param);
		}
	}

	function handleWheelEvent (event, obj, eventName) {
		var delta = 0;
		if (!event) event = window.event;
		if (event.detail) {
			delta = -event.detail / 3;
		} else
			if (event.wheelDelta) {
				delta = event.wheelDelta / 120;
				if (window.opera) delta = -delta;
			}
		if (delta) {
			var res = null;
			// Reinzoomen
			if (delta > 0) {
				firePointerEvent(obj, 'touchZoomStart');
				res = firePointerEvent(obj, 'onmousewheel', true, 1.2);
				firePointerEvent(obj, 'touchZoomEnd', true, 1.2);
			} else {
				// Rauszoomen
				firePointerEvent(obj, 'touchZoomStart');
				res = firePointerEvent(obj, 'onmousewheel', true, 1 / 1.2);
				firePointerEvent(obj, 'touchZoomEnd', true, 1 / 1.2);
			}
			return res;
		}
	}

	/**
	 * Kopiert alle Eventhandler von src auf dest. Wird beim Drag'n'drop verwendet für das geclonte Objekt.
	 */
	function cloneEventHandlers (src, dest) {
		dest.pointerListeners = src.pointerListeners;
		dest.onmousemove = src.onmousemove;
		dest.onclick = src.onclick;
		dest.onmousedown = src.onmousedown;
		dest.onmouseup = src.onmouseup;
	}

	/**
	 * Returns if "touch" or "mouse" was initialized
	 */
	this.getPointerType = function () {
		return pointer;
	};

	this.onPointerOver = function (obj, fct, param) {
		bindObjWithEvent(obj, 'onmouseover', 'touchOver', fct, param);
	};

	this.onPointerOut = function (obj, fct, param) {
		// Closure compiler ist doof. Weitersagen.
		var x = param;
		var y = arguments;
		bindObjWithEvent(obj, 'onmouseout', 'touchOut', fct, arguments[2]);
	};

	this.onPointerDown = function (obj, fct, param) {
		// Closure compiler ist doof. Weitersagen.
		var x = param;
		var y = arguments;
		bindObjWithEvent(obj, 'onmousedown', 'touchStart', fct, arguments[2]);
	};

	this.onPointerMove = function (obj, fct, param) {
		bindObjWithEvent(obj, 'onmousemove', 'touchMove', fct, param);
	};

	this.onPointerUp = function (obj, fct, param) {
		bindObjWithEvent(obj, 'onmouseup', 'touchEnd', fct, param);
	};

	this.onPointerClick = function (obj, fct, param) {
		bindObjWithEvent(obj, 'onclick', 'touchClick', fct, param);
	};

	this.onPointerDblClick = function (obj, fct, param) {
		bindObjWithEvent(obj, 'ondblclick', 'touchClick', fct, param);
	};

	this.onPointerClickedMove = function (obj, fct, param) {
		if (obj == null) {
			return false;
		}

		if (pointer == 'mouse') {
			bindObjWithEvent(obj, 'onmousedown', 'onmousedown', function () {
				pointerStatus.down = true;
			}, param);
			bindObjWithEvent(obj, 'onmousemove', 'onmousemove', function (param) {
				if (pointerStatus.down) {
					fct(param);
				}
			}, param);
		} else {
			bindObjWithEvent(obj, 'touchMove', 'touchMove', fct, param);
		}
	};

	this.onSwipe = function (obj, fct) {
		obj.onmousedown = function () {return false;};
		bindObjWithEvent(obj, 'onSwipeLeft', 'onSwipeLeft', fct, 'left');
		bindObjWithEvent(obj, 'onSwipeRight', 'onSwipeRight', fct, 'right');
		bindObjWithEvent(obj, 'onSwipeUp', 'onSwipeUp', fct, 'up');
		bindObjWithEvent(obj, 'onSwipeDown', 'onSwipeDown', fct, 'down');
	};

	this.onPointerZoom = function (obj, zoomFct, startFct, endFct) {
		bindObjWithEvent(obj, 'onmousewheel', 'touchZoom', zoomFct);
		if (startFct != null)
			bindObjWithEvent(obj, null, 'touchZoomStart', startFct);
		if (endFct != null)
			bindObjWithEvent(obj, null, 'touchZoomEnd', endFct);
	};

	/**
	 * Löscht entweder alle Listener auf einem Objekt oder nur selektiv nach einem Eventnamen.
	 * Ist kein eventName angegeben, werden alle gelöscht
	 */
	this.removeBindings = function (obj, eventName) {
		if ((typeof(obj) == 'undefined') || (obj == null)) {
			return true;
		}
		if (typeof(obj.pointerListeners) != "undefined") {
			obj.pointerListeners.removeAll(eventName);
		}
	};

	/**
	 * Löscht genau einen Listener aus der angegebenen Liste
	 */
	this.removeBinding = function (obj, fct) {
		if ((typeof(obj) == 'undefined') || (obj == null)) {
			return true;
		}
		if (typeof(obj.pointerListeners) != "undefined") {
			obj.pointerListeners.remove(fct);
		}
	};

	/**
	 * Registriert eine neue Callback Funktion für Mausbewegungen
	 */
	this.addGlobalCallback = function (type, fct) {
		// Nach freiem Platz im Array suchen
		var foundSpace = -1;
		for (var i = 0; i < globalListeners[type].length; i++) {
			if ((globalListeners[type][i] == null) || (globalListeners[type][i] == fct)) {
				foundSpace = i;
				break;
			}
		}
		if (foundSpace != -1) {
			globalListeners[type][foundSpace] = fct;
		} else {
			foundSpace = globalListeners[type].length;
			globalListeners[type].push(fct);
		}
		return function () {
			globalListeners[type][foundSpace] = null;
		};
	};

	this.setTouchScroll = function (newValue, resetOnPointerUp) {
		oldTouchBrowserScrollEnabled = myself.touchBrowserScrollEnabled;
		myself.touchBrowserScrollEnabled = newValue;
		if (resetOnPointerUp) {
			resetTouchBrowserScrollOnUp = true;
		}
	};

	this.resetTouchScroll = function () {
		myself.touchBrowserScrollEnabled = oldTouchBrowserScrollEnabled;
	};

	/**
	 *  Ab hier nur noch Drag'n'Drop
	 */
	function dragStart (params) {
		var obj = params['obj'];
		//console.log(" === Disabling scrolling");
		if (pointerStatus.dragObj != null) {
			myself.stopDragDrop();
			return false;
		}
		// Kein onmouseout feuern lassen
		obj.muteEvents = true;
		pointerStatus.dragObj = obj;
		pointerStatus.dragObjParams = params;
		// Absolute Position auf dem Bildschirm auslesen
		if (params['doClone']) {
			var pos = obj.getBoundingClientRect();
			// Den ursprünglichen Style des zu bewegenden Objekts speichern
			pointerStatus.dragObjStyle = [];
			pointerStatus.dragObjStyle.width = computedStyle(obj, 'width');
			pointerStatus.dragObjStyle.height = computedStyle(obj, 'height');
			pointerStatus.dragObjStyle.top = computedStyle(obj, 'top');
			pointerStatus.dragObjStyle.left = computedStyle(obj, 'left');
			pointerStatus.dragObjStyle.display = computedStyle(obj, 'display');
			// Der IE liefert keine berechneten Pixelwerte aus, sondern genau das, was man definiert hat.
			if (pointerStatus.dragObjStyle.width.indexOf('%') > 0) {
				// Pixelwerte auslesen
				pointerStatus.dragObjStyle.width = obj.clientWidth;
				pointerStatus.dragObjStyle.height = obj.clientHeight;
			}
			// Objekt verstecken
			obj.style.visibility = 'hidden';

			// Objekt Clonen und am Body absolut positioniert wieder aufhängen
			pointerStatus.dragObjClone = obj.cloneNode(true);
			pointerStatus.dragObjClone.className += ' msAntiScroll drag';
			pointerStatus.dragObjClone.isClone = true;

			cloneEventHandlers(obj, pointerStatus.dragObjClone);
			var cloneObj = pointerStatus.dragObjClone;
			cloneObj.style.display = 'block';
			cloneObj.style.visibility = 'visible';
			cloneObj.style.position = 'absolute';
			cloneObj.style.width = pointerStatus.dragObjStyle.width;
			cloneObj.style.height = pointerStatus.dragObjStyle.height;
			cloneObj.style.top = pos['top'] + "px";
			cloneObj.style.left = pos['left'] + "px";
			cloneObj.style.zIndex = 99999;
			document.body.appendChild(cloneObj);
			pointerStatus.dragObjClone['ondragstart'] = function () {return false;};
		} else {
			var pos = {'left': parseInt(computedStyle(obj, 'left')), 'top': parseInt(computedStyle(obj, 'top'))}
			cloneObj = obj;
			pointerStatus.dragObjStyle = obj.style;
			pointerStatus.dragObjClone = obj;
		}
		pointerStatus.dragObjStyle.startTop = pos['top'];
		pointerStatus.dragObjStyle.startLeft = pos['left'];
		var currentTransform = getTransform(obj);
		pointerStatus.dragObjStyle.startX = parseInt(currentTransform[0]);
		pointerStatus.dragObjStyle.startY = parseInt(currentTransform[1]);
		pointerStatus.dragObj['ondragstart'] = function () {return false;};
		if ((pointerStatus.dragObj != null) && (typeof(pointerStatus.dragObj.dragStart) != "undefined") && (pointerStatus.dragObj.dragStart != null)) {
			pointerStatus.dropObjParam = pointerStatus.dragObj.dragStart(cloneObj, params['callBackParams']);
		}
		return false;
	}

	function getTransform(el) {
		var transform = $(el).css('transform');
		var results = transform.match(/matrix(?:(3d)\(-{0,1}\d+(?:, -{0,1}\d+)*(?:, (-{0,1}\d+))(?:, (-{0,1}\d+))(?:, (-{0,1}\d+)), -{0,1}\d+\)|\(-{0,1}\d+(?:, -{0,1}\d+)*(?:, (-{0,1}\d+))(?:, (-{0,1}\d+))\))/);

		if(!results) return [0, 0, 0];
		if(results[1] == '3d') return results.slice(2,5);

		results.push(0);
		return results.slice(5, 8); // returns the [X,Y,Z,1] values
	}

	this.getObjectTransform = getTransform;
	this.getObjectPosition = getObjectPosition;

	function dragUpdate () {
		if (pointerStatus.down) {
			var newX = Math.round(pointerStatus.dragObjStyle.startX + (pointerStatus.current.x - pointerStatus.start.x) / viewPortScale) + "px";
			var newY = Math.round(pointerStatus.dragObjStyle.startY + (pointerStatus.current.y - pointerStatus.start.y) / viewPortScale) + "px";
			setTranslate3d(pointerStatus.dragObjClone, newX, newY);
		}
	}

	function setTranslate3d(obj, newX, newY) {
		obj.style.transform = 'translate3d('+newX+", "+newY+",0)";
		obj.style.msTransform = 'translate3d('+newX+", "+newY+",0)";
		obj.style.mozTransform = 'translate3d('+newX+", "+newY+",0)";
		obj.style.OTransform = 'translate3d('+newX+", "+newY+",0)";
		obj.style.webkitTransform = 'translate3d('+newX+", "+newY+",0)";
	}

	function dragEnd () {
		var dist = Math.round(Math.sqrt(Math.pow(pointerStatus.start.x - pointerStatus.current.x, 2) + Math.pow(pointerStatus.start.y - pointerStatus.current.y, 2)));
		// Nichts gedraggt? Dann evtl Wischgeste
		if (pointerStatus.dragObj == null) {
			// Weit genug gewischt?
			if (dist > 70) {
				// Horizontal?
				if (Math.abs(pointerStatus.start.x - pointerStatus.current.x) > Math.abs(pointerStatus.start.y - pointerStatus.current.y)) {
					if (pointerStatus.start.x > pointerStatus.current.x)
						firePointerEvent(pointerStatus.start.obj, 'onSwipeLeft');
					else
						firePointerEvent(pointerStatus.start.obj, 'onSwipeRight');
				} else {
					if (pointerStatus.start.y > pointerStatus.current.y)
						firePointerEvent(pointerStatus.start.obj, 'onSwipeUp');
					else
						firePointerEvent(pointerStatus.start.obj, 'onSwipeDown');
				}
			}
			return false;
		}
		if (pointerStatus.dragObjParams && pointerStatus.dragObjParams['saveTransformInName']) {
			oldDragPosition[pointerStatus.dragObjParams['saveTransformInName']] = getTransform(pointerStatus.dragObj);
		}
		if (pointerStatus.dragObjClone.isClone) {
			pointerStatus.dragObjClone.style.display = "none";
			var elemUnderPointer = elementFromDocumentPoint(pointerStatus.current.clientX, pointerStatus.current.clientY);
			pointerStatus.dragObjClone.style.display = "block";
		}
		var dest = findDroppable(elemUnderPointer);
		var revert = true;
		// Gab es ein Ziel für das Objekt?
		if (dest != null) {
			revert = false;
			// Wird das Objekt von dem Ziel akzeptiert?
			if (!dest.dropFct(pointerStatus.dragObj, dest, pointerStatus.dropObjParam)) {
				revert = true;
			} else {
				if (pointerStatus.dragObj == null) {
					// stopDragDrop() wurde bereits von der funktion aufgerufen. Hier nichts mehr weiter machen.
					return;
				}
				if (typeof(pointerStatus.dragObj.dragStop) == 'function') {
					pointerStatus.dragObj.dragStop(true);
				}
			}
		}
		if (typeof(pointerStatus.dragObj.dragStop) == 'function') {
			revert = !pointerStatus.dragObj.dragStop(false);
		}
		// Nicht valides Ziel
		if (revert) {
			myself.dropBack();
		} else {
			if ((dest == null) || (typeof(dest.manuallyStopDrag) == "undefined") || (!dest.manuallyStopDrag)) {
				// Wenn manuallyStopDrag gesetzt kümmert sich der Aufrufer selbst um ein Stop des DnD.
				// Da keins gesetzt ist: breche DnD ab.
				myself.stopDragDrop();
			}
		}
	}

	this.dropBack = function () {
		// Wenn das ursprüngliche Objekt noch besteht
		// wieder zurück an den ursprünglichen Platz federn lassen und
		// danach durch das ursprüngliche Objekt austauschen.
		//revertObject(pointerStatus.dragObj, pointerStatus.style);
		if (typeof(AnimationManager) != "undefined") {
			var yMove = new Animation(pointerStatus.dragObjClone, 'top', parseInt(pointerStatus.dragObjClone.style.top), parseInt(pointerStatus.dragObjStyle.startTop));
			yMove.cssSuffix = 'px';
			yMove.duration = 300;
			yMove.timing = Animation.EASE_IN;
			if ((pointerStatus.dragObj != null) && (typeof(pointerStatus.dragObj.dragStop) != "undefined")) {
				yMove.callback = pointerStatus.dragObj.dragStop;
			}
			var xMove = yMove.clone();
			xMove.cssProperty = 'left';
			xMove.startVal = parseInt(pointerStatus.dragObjClone.style.left);
			xMove.endVal = parseInt(pointerStatus.dragObjStyle.startLeft);
			AnimationManager.addAnimation(yMove);
			AnimationManager.addAnimation(xMove);
			xMove.callback = function () {
				myself.stopDragDrop();
			};
		} else {
			myself.stopDragDrop();
		}
	};

	this.stopDragDrop = function () {
		if (pointerStatus.dragObj == null)
			return false;
		pointerStatus.dragObj.muteEvents = false;
		if (pointerStatus.dragObjClone.isClone) {
			//pointerStatus.dragObj.style.cssText = pointerStatus.dragObjClone.style.cssText;
			document.body.removeChild(pointerStatus.dragObjClone);
			pointerStatus.dragObjClone.style.display = "none";
			pointerStatus.dragObj.style.display = pointerStatus.dragObjStyle.display;
			pointerStatus.dragObj.style.visibility = 'visible';
		}
		pointerStatus.dragObj = null;
		pointerStatus.dragObjClone = null;
	};

	function findDroppable (dest) {
		if (dest == null)
			return null;
		if (dest.droppable)
			return dest;
		return findDroppable(dest.parentNode);
	}

	/**
	 * Deklariert ein Element als Draggable. Es kann also an einen anderen Ort gezogen werden.
	 * Wenn dieser andere Ort kein "Droppable" ist, wird das Objekt wieder zurück an seinen Ursprungspunkt gesetzt.
	 *
	 * Andere Events können evtl mit Draggable kollidieren und so überschrieben werden.
	 */
	this.isDraggable = function (obj, onDragStartFct, onDragStopFct, dragStartParams, overrideDragObj, doClone, saveTransformInName, onPointerDownFct) {
		if (obj == null)
			return false;
		obj.className += ' msAntiScroll';
		var waitForMove = function (params) {
			if (onPointerDownFct !== undefined) {
				onPointerDownFct();
			}
			var initialPosition = {'x': pointerStatus.current.x, 'y': pointerStatus.current.y};
			myself.setTouchScroll(false, true);
			// Nach dem Klick auf das Objekt auf Mausbewegungen achten
			myself.onPointerMove(params['obj'], function () {
				// Strangly enough we get called without the point being moved. So check if the coordinated change enough
				var currentPosition = {'x': pointerStatus.current.x, 'y': pointerStatus.current.y};
				var pointerDist = Math.sqrt(Math.pow(currentPosition['x'] - initialPosition['x'], 2) + Math.pow(currentPosition['y'] - initialPosition['y'], 2));
				if (pointerDist < 5) {
					return;
				}
				if (pointerStatus.dragObj != params['obj'] && pointerStatus.down) {
					dragStart(params);
					// We can remove this binding afterwards.
					myself.removeBindings(params['obj'], 'onmousemove');
					myself.removeBindings(params['obj'], 'touchMove');
				} else {
					if (pointerStatus.dragObj == params['obj']) {
						// Das Objekt wird jetzt gedraggt, die Bewegungs Listener können also entfernt werden
						myself.removeBindings(pointerStatus.dragObj, 'onmousemove');
						myself.removeBindings(pointerStatus.dragObjClone, 'onmousemove');
						myself.removeBindings(pointerStatus.dragObj, 'touchMove');
						myself.removeBindings(pointerStatus.dragObjClone, 'touchMove');
						// Und einfach mal einen mouseout feuern.
						firePointerEvent(pointerStatus.oldObj, 'onmouseout');
					} else {
						// Das Objekt wurde nur angeklickt
						myself.removeBindings(params['obj'], 'onmousemove');
						myself.removeBindings(params['obj'], 'touchMove');
					}
				}
			}, params);
		}
		var draggingObj = obj;
		if (overrideDragObj) {
			draggingObj = overrideDragObj;
		}
		if (typeof(doClone) == 'undefined') {
			doClone = true;
		}
		bindObjWithEvent(obj, 'onmousedown', 'touchStart', waitForMove, {'obj': draggingObj, 'callBackParams': dragStartParams, 'doClone': doClone, 'saveTransformInName': saveTransformInName});
		obj['ondragstart'] = function () {return false;};
		obj.draggable = true;
		obj.dragStart = onDragStartFct;
		obj.dragStop = onDragStopFct;
		draggingObj.dragStart = onDragStartFct;
		draggingObj.dragStop = onDragStopFct;
		if (saveTransformInName && oldDragPosition[saveTransformInName] !== undefined) {
			var pos = oldDragPosition[saveTransformInName];
			setTranslate3d(draggingObj, pos[0]+"px", pos[1]+"px");
		}
	};

	/**
	 * Deklariert ein Element als Drop Target. Hier können als Objekte drauf gedraggt werden.
	 * Sobald ein Element fallengelassen wird, wird "fct" aufgerufen mit den Parametern dragObj und dropObj.
	 * Falls der optionale Parameter "manuallyStopDrag" gesetzt ist, wird nicht automatisch stopDragDrop() aufgerufen.
	 * Das ist dann sinnvoll, wenn noch nicht sofort der alte Zustand der DOM-Objekte hergestellt werden soll,
	 * sondern zB auf eine Serverantwort gewartet wird.
	 */
	this.isDroppable = function (obj, fct, manuallyStopDrag) {
		if (obj == null)
			return false;
		obj.droppable = true;
		obj.dropFct = fct;
		obj.manuallyStopDrag = manuallyStopDrag;
	};
});
