GameLobby.app.controller('EnterBetaKeyController', ['$scope', 'Request', '$state', 'WindowService', function ($scope, Request, $state, WindowService) {

	$scope['betaKey'] = '';
	$scope['proceed'] = false;
	$scope['checkKey'] = function() {
		Request.create(
			'gameworld/checkBetaKey',
			{
				'betaKey' : $scope['betaKey']
			},
			function (data) {
				$scope['data'] = data;
				if($scope['data']['valid']) {
					$scope['proceed'] = true;
				} else {
					$scope['error'] = 'EnterBetaKey.Invalid';
					$scope['proceed'] = false;
				}
			},
			function (data) {
				if (data['message'].indexOf('userError') >= 0) {
					$scope['userError'] = data['message'];
				} else {
					$scope['error'] = data;
				}
			}
		);
	}
}]);