GameLobby.app.controller('HeaderController', ['$scope', 'Request', 'SessionService', '$state', function ($scope, Request, SessionService, $state) {

	$scope.menuBarOptions = {
		visible: false,
		currentTab: 'news'
	};

	$scope['countries'] = [];
	$scope['country'] = 'en';

	var session = SessionService.getSession();
	var sessionObj = Session.get(session.sessionId);
	var unsubscribeSession = sessionObj.subscribe(function () {
		$scope['country'] = sessionObj['data']['country'];

		for (var region in GameLobby.Countries) {
			for (var j = 0; j < GameLobby.Countries[region].length; j++) {
				if ($scope['country'] != GameLobby.Countries[region][j]) {
					$scope['countries'].push(GameLobby.Countries[region][j]);
				}
			}
		}
	});

	$scope.switchCountry = function (country) {
		Request.create(
			'player/switchCountry',
			{
				'country': country
			},
			function (data) {
				// sets the language for mellon
				GameLobby.config.mellon.application.languageId = GameLobby.config.languages[country];
				window.location.replace('/');
			},
			function (data) {
			}
		);
	};

	$scope.logout = function () {
		SessionService.logout();
	};

	$scope.openHelpCenter = function () {
		var country = 'en';
		if(sessionObj) {
			country = sessionObj['data']['country'];
		}
		window.open(GameLobby.config.links.urlHelpCenter + getLocaleRegion(getLocale(country)), '_blank');
	};

	$scope.openForum = function () {
		var country = 'en';
		if(sessionObj) {
			country = sessionObj['data']['country'];
		}
		var url = GameLobby.config.links.urlForum + getLocaleRegion(getLocale(country));
		if(country == 'ae') {
			url = 'http://kingdoms.travian-forum.com/';
		}
		window.open(url, '_blank');
	};

	$scope.openWiki = function () {
		var country = 'en';
		if(sessionObj) {
			country = sessionObj['data']['country'];
		}
		window.open(GameLobby.config.links.urlWiki + getLocaleLanguage(getLocale(country)), '_blank');
	};

	$scope.$on('$destroy', function () {
		sessionObj.unsubscribe(unsubscribeSession);
	});
}]);