GameLobby.app.controller('IframeController', ['$scope', '$sce', 'SessionService', '$state', function ($scope, $sce, SessionService, $state) {

	var page = $state['current']['name'];

	if (typeof($scope.modal) != 'undefined') {
		page = $scope.modal.page;
	}

	var session = SessionService.getSession();
	var sessionObj = null;
	var unsubscribeSession = null;

	var initPage = function() {
		sessionObj = Session.get(session.sessionId);
		unsubscribeSession = sessionObj.subscribe(function () {
			setLink(sessionObj['data']['country']);
		});
	};

	initPage();

	$scope.$on('$destroy', function () {
		if(!sessionObj) {
			return;
		}
		sessionObj.unsubscribe(unsubscribeSession);
	});

	function setLink (country) {
		if (!country) {
			country = 'en';
		}

		var link = 'http://kingdoms.travian.com/' + country + '/' + page;
		switch (page) {
			case 'privacy':
				link = 'http://agb.traviangames.com/privacy-' + getLocaleLanguage(getLocale(country)) + '.pdf';
				break;
			case 'terms':
				link = 'http://agb.traviangames.com/terms-' + getLocaleLanguage(getLocale(country)) + '.pdf';
				break;
			case 'rules':
			case 'gameRules':
				link = GameLobby.config.links['urlGameRules'].replace("X", country);
				break;
		}

		$scope['link'] = $sce['trustAsResourceUrl'](link);
	}
}]);