function Player () {
	this.defaultData = {
		'playerId': 0,
		'avatarName': '',
		'password': '',
		'userAccountIdentifier': 0,
		'logins': 0,
		'loginLast': 0,
		'signupTime': 0,
		'bannedUntil': 0,
		'isInstantAccount': 0,
		'isActivated': 0
	};

	this.data = {};
	this.set(this.defaultData);
	this.name = 'Player';
}

Player.MAX_SITTERS = 2;
Player.MAX_DUALS = 3;

Player.prototype = new AbstractModel();
Cache.register(Player);
