GameLobby.app.directive('modals', ['WindowService', function (WindowService) {
	return {
		restrict: 'A',
		templateUrl: 'templates/partials/modals.html',
		scope: {

		},
		link: function ($scope, element, attrs) {
			$scope.modals = WindowService.getModals();

			$scope.shouldShowBackdrop = function () {
				return WindowService.openWindows > 0;
			};
		}
	}
}]);