GameLobby.app.directive('notification', ['$location', 'Request', function ($location, Request) {
	return {
		restrict: 'E',
		replace: true,
		templateUrl: 'templates/partials/notification.html',
		scope: {
			notification: '=data'
		},
		'link': function (scope, element) {
			var closed = false;
			var unsubscribeWorld = null;
			var unsubscribeAvatar = null;
			var avatar = null;
			var gameWorld = null;

			// we need the avatarName for punishment notifications and
            // worldName for punishment and some achievement notifications
			if (scope.notification.avatarIdentifier > 0) {
                if (scope.notification.type == Notification.TYPE_AVATAR_SUSPENDED ||
                    scope.notification.type == Notification.TYPE_AVATAR_LOCKED ||
                    scope.notification.type == Notification.TYPE_AVATAR_ISOLATED ||
                    scope.notification.type == Notification.TYPE_ACHIEVEMENT_ACHIEVED) {
                    avatar = Avatar.get(scope.notification.avatarIdentifier);
                    unsubscribeAvatar = avatar.subscribe(updateAvatar);
                }
			}

			if (scope.notification.type == Notification.TYPE_ACHIEVEMENT_ACHIEVED) {
				scope['achievement'] = true;
			}

			scope.close = function () {
				closed = true;
				Request.create('notification/markAsRead', {'id': scope.notification.id});
			};
			scope.click = function () {
				if (closed == false && scope.notification.route) {
					var route = '/' + scope.notification.route;
					if (scope.notification.routeParam) {
						route += '/' + scope.notification.routeParam;
					}
					$location.path(route);
					this.close();
				}
			};

			scope.$on('$destroy', function () {
				if (avatar !== null) {
					avatar.unsubscribe(unsubscribeAvatar);
				}
				if (gameWorld !== null) {
					gameWorld.unsubscribe(unsubscribeWorld);
				}
			});

			function updateAvatar () {
				scope['avatarName'] = avatar.data.avatarName;
				gameWorld = GameWorld.get(avatar.data.consumersId);
				if (unsubscribeWorld === null) {
					unsubscribeWorld = gameWorld.subscribe(function () {
						scope['worldName'] = gameWorld.data.worldName;
					});
				}
			}
		}
	};
}]);