/**
 * Uses the jQuery flot library to display a pie chart.
 * Enabled hovering over items and highlights them in the legend.
 *
 * You will have to pass a data object compatible with flot in the attribute "data"
 *
 **/
/** @expose */
GameLobby.app.directive('pieChart', ['$window', '$compile', '$timeout', '$filter', function ($window, $compile, $timeout, $filter) {
	return  {
		restrict: 'EA',
		replace: false,
		scope: {
			'data': '='
		},
		link: function (scope, elem, attrs) {
			scope.$watch('data', function (value) {
				if (typeof(scope['data']) == 'undefined') {
					return
				}
				var showLabelInPie = true;
				if (typeof(attrs['labelInPie']) != 'undefined') {
					showLabelInPie = scope.$eval(attrs['labelInPie']);
				}
				var percentInPie = false;
				if (typeof(attrs['percentInPie']) != 'undefined') {
					percentInPie = scope.$eval(attrs['percentInPie']);
				}

				$['plot'](elem, scope['data'], {
					'series': {
						'pie': {
							'show': true,
							'radius': 0.98, // Don't use the full radius to have some space for the border
							'label': {
								'show': showLabelInPie,
								'radius': 0.6,
								'formatter': function (label, series) {
									if (!percentInPie) {
										return '<div>' + series['data'][0][1] + '</div>';
									} else {
										var bidiPercent = $filter('bidiNumber');
										var value = bidiPercent(Math.round(series['percent']), 'percent');
										return '<div>' + value + '</div>';
									}
								}
							}
						}
					},
					'legend': {
						//'show': false,
						'backgroundColor': 'transparent',
						'labelBoxBorderColor': "none",
						'labelFormatter': function (label, series) {
							var bidiPercent = $filter('bidiNumber');
							var value = bidiPercent(Math.round(series['percent']), 'percent');
							return '<div>' + label + '<br>' + value + ' (' + series['data'][0][1] + ')</div>';
						}
					},
					'grid': {
						'hoverable': true
					}
				});

				elem.find('.legend table').addClass('transparent');
				var selected = -1;

				elem.find('.pieLabel').bind('mouseover', function (event) {
					var id = event.currentTarget.id
					var index = id.substr(id.length - 1);
					$(event.relatedTarget).trigger('plothover', [
						{},
						{'seriesIndex': index}
					]);
				});

				elem.bind("plothover", function (evt, pos, obj) {
					if (obj == null) {
						if (selected != -1) {
							elem.find('.legend table .legendLabel').css('font-weight', 'normal');
							selected = -1;
						}
						return;
					}
					if (obj['seriesIndex'] == selected) {
						return;
					}
					selected = obj['seriesIndex'];
					elem.find('.legend table .legendLabel').css('font-weight', 'normal');
					elem.find('.legend table .legendLabel')[selected].style.fontWeight = 'bold';
				});
			});
		}
	};
}]);