/**
 * ElementTip Library is used by the tooltip directive.
 * It places some kind of DOM element right next to a specified other DOM element.
 *
 * @constructor
 * @param element The DOM element where you want to display some extra information for
 * @param childHTML the innerHTML part of the newly created DOM element
 * @param placement One of before, after, above, below. You may leave this out and it will be automatically picked
 * @param additionalClass A class name for the new child element
 * @param instantShow Set this to true if you don't have to compile anything first (would cause flicker)
 */
function ElementTip (element, childHTML, placement, additionalClass, instantShow) {
	var direction = '';
	var offset = 0;
	var ttPosition = {};
	var $body = null;

	this.domElement = null;
	var myself = this;
	var destroyed = false;

	/**
	 * prepare data
	 */
	setDirection();
	function setDirection () {
		switch (placement) {
			case 'before':
				direction = (getDirection() == 'ltr' ? 'left' : 'right');
				break;
			case 'after':
				direction = (getDirection() == 'ltr' ? 'right' : 'left');
				break;
			case 'above':
				direction = 'top';
				break;
			case 'below':
			default:
				direction = 'bottom';
				break;
		}
	}

	function flipDirection () {
		switch (placement) {
			case 'before':
				placement = 'after';
				break;
			case 'after':
				placement = 'before';
				break;
			case 'above':
				placement = 'below';
				break;
			// below
			default:
				placement = 'above';
				break;
		}
		setDirection();
	}

	function getOffset () {
		switch (placement) {
			case 'before':
				if (getDirection() == 'ltr') {
					offset = parseInt(myself.domElement.css('margin-right'));
				}
				else {
					offset = parseInt(myself.domElement.css('margin-left'));
				}
				break;
			case 'after':
				if (getDirection() == 'ltr') {
					offset = parseInt(myself.domElement.css('margin-left'));
				}
				else {
					offset = parseInt(myself.domElement.css('margin-right'));
				}
				break;
			case 'above':
				offset = parseInt(myself.domElement.css('margin-bottom'));
				break;
			case 'below':
			default:
				offset = parseInt(myself.domElement.css('margin-top'));
				break;
		}

		myself.domElement.css('margin', '0');
	}

	/**
	 * Checks if tooltip is within the body.
	 * If not, tries to flip the tooltip or shift it depending on placement
	 */
	function moveTooltipToBody () {
		$body = $body || $('body');

		switch (direction) {
			case 'right':
				// flip
				if (ttPosition['x'] + ttWidth > $body.width()) {
					flipDirection();
					calculatePosition();
				}
				// shift
				shiftVertical();
				break;
			case 'left':
				// flip
				if (ttPosition['x'] < 0) {
					flipDirection();
					calculatePosition();
				}
				// shift
				shiftVertical();
				break;
			case 'top':
				// flip
				if (ttPosition['y'] < 0) {
					flipDirection();
					calculatePosition();
				}
				// shift
				shiftHorizontal();
				break;
			default:
				// flip
				if (ttPosition['y'] + ttHeight > $body.height()) {
					flipDirection();
					calculatePosition();
				}
				// shift
				shiftHorizontal();
				break;
		}
	}

	/**
	 * will shift the tooltip horizontally to fit into the window
	 * also provides, the arrow is still pointing at the element
	 */
	var shiftHorizontal = function () {
		var arrowPosition = Math.floor(ttWidth / 2);
		if (ttPosition['x'] < 0) {
			arrowPosition = Math.floor(ttWidth / 2) - Math.abs(ttPosition['x']);
			ttPosition['x'] = 0;
		}
		else
			if (ttPosition['x'] + ttWidth > $body.width()) {
				arrowPosition = Math.floor(ttWidth / 2) + Math.abs($body.width() - (ttPosition['x'] + ttWidth));
				ttPosition['x'] = $body.width() - ttWidth;
			}

		// absolute positioning ignores border
		arrowPosition -= parseInt(myself.domElement.css('border-left-width'));
		$arrowElem.css('left', arrowPosition + 'px');
	};

	/**
	 * will shift the tooltip vertically to fit into the window
	 * also provides, the arrow is still pointing at the element
	 */
	var shiftVertical = function () {
		var arrowPosition = Math.floor(ttHeight / 2);
		if (ttPosition['y'] < 0) {
			arrowPosition = Math.floor(ttHeight / 2) - Math.abs(ttPosition['y']);
			ttPosition['y'] = 0;
		}
		else
			if (ttPosition['y'] + ttHeight > $body.height()) {
				arrowPosition = Math.floor(ttHeight / 2) + Math.abs($body.height() - (ttPosition['y'] + ttHeight));
				ttPosition['y'] = $body.height() - ttHeight;
			}
		// absolute positioning ignores border
		arrowPosition -= parseInt(myself.domElement.css('border-top-width'));
		$arrowElem.css('top', arrowPosition + 'px');
	};

	function calculatePosition () {
		position = getPosition(element);
		if ((placement == null) && ((position['width'] > 0) || (position['left'] > 0))){
			placement = 'below';
			var screenHeight = $('body').height();
			if (position['top'] > screenHeight * 0.7) {
				placement = 'above';
			}
			setDirection();
		}

		// maybe this is another calculation after flipping tooltip
		// so reset the arrow direction
		$arrowElem.removeClass('arrowDirectionFrom arrowDirectionTo arrowDirectionDown arrowDirectionUp');
		// Calculate the tooltip's top and left coordinates to center it with
		// this directive.
		switch (direction) {
			case 'right':
				ttPosition = {
					'y': (position.top + position.height / 2 - ttHeight / 2),
					'x': Math.floor((position.left + position.width)) + offset
				};
				if (getDirection() == 'ltr') {
					$arrowElem.addClass('arrowDirectionFrom');
				}
				else {
					$arrowElem.addClass('arrowDirectionTo');
				}
				break;
			case 'left':
				ttPosition = {
					'y': (position.top + position.height / 2 - ttHeight / 2),
					'x': Math.floor((position.left - ttWidth)) - offset
				};
				if (getDirection() == 'ltr') {
					$arrowElem.addClass('arrowDirectionTo');
				}
				else {
					$arrowElem.addClass('arrowDirectionFrom');
				}
				break;
			case 'top':
				ttPosition = {
					'y': (position.top - ttHeight) - offset,
					'x': Math.floor((position.left + position.width / 2 - ttWidth / 2))
				};
				$arrowElem.addClass('arrowDirectionDown');
				break;
			default:
				ttPosition = {
					'y': (position.top + position.height) + offset,
					'x': Math.floor((position.left + position.width / 2 - ttWidth / 2))
				};
				$arrowElem.addClass('arrowDirectionUp');
				break;
		}
	}

	function getPosition () {
		var boundingClientRect = element[0].getBoundingClientRect();
		var ret = {
			'width': element.prop('offsetWidth'),
			'height': element.prop('offsetHeight'),
			'top': boundingClientRect.top,
			'left': boundingClientRect.left
		};
		if (((ret['width'] == 0) && (ret['left'] == 0)) || (ret['left'] < 0)){
			window.setTimeout(myself['setPosition'], 50);
		}
		if (typeof(window.pageYOffset) != 'undefined') {
			ret['top'] += window.pageYOffset;
			ret['left'] += window.pageXOffset;
		} else {
			ret['top'] += document.body.scrollTop;
			ret['left'] += document.body.scrollLeft;
		}
		return ret;
	}

	/**
	 * Public functions and constructor
	 */

	this['setPosition'] = function() {
		if (element.length == 0) {
			myself['hide']();
			element = $(element.selector);
			window.setTimeout(myself['setPosition'], 50);
			return;
		}

		// Get the height and width of the tooltip so we can center it.
		// (including padding and border)
		ttWidth = myself.domElement.outerWidth();
		ttHeight = myself.domElement.outerHeight();
		calculatePosition();
		// When the element is not in the DOM, it will have a height of 0 and top position of 0
		if ((position['top'] == 0) && (position['height'] == 0)) {
			myself['hide']();
			// Select it again. It my be removed and re-added by angular when in an ng-repeat some elements change.
			element = $(element.selector);
			window.setTimeout(myself['setPosition'], 50);
			return;
		}
		moveTooltipToBody();

		var cssPos = {'top': ttPosition['y'] + "px", 'left': ttPosition['x'] + "px"};

		// Now set the calculated positioning.
		myself.domElement.css(cssPos);
		if (instantShow) {
			myself['show']();
		}
	};
	this['remove'] = function() {
		myself.domElement.remove();
		destroyed = true;
	};
	this['show'] = function() {
		if (!destroyed) {
			myself.domElement.css('visibility', 'visible');
		}
	};
	this['hide'] = function() {
		myself.domElement.css('visibility', 'hidden');
	};
	var className= 'glTooltip ' + additionalClass+' '+placement;
	myself.domElement = $('<div>', {'class': className});
	var $arrowElem = $('<i>');
	myself.domElement.append($arrowElem);
	this['hide']();
	myself.domElement.append($('<div>', {
		'class': 'tooltipContent'
	}).append(childHTML));

	$body = $body || $('body');
	$body.append(myself.domElement);

	// offset should be defined once, regardless of later flips of the tooltip
	getOffset();
	this['setPosition']();
}