GameLobby.app.controller('NewsController', ['$scope', '$sce', '$timeout', 'SessionService', '$cookieStore', function ($scope, $sce, $timeout, SessionService, $cookieStore) {
	$scope['news'] = [];
	$scope['loadingState'] = true;

	var country = '';
	var startFromIndex = 0;
	var newsEntriesToShow = 5;
	var allNewsLoaded = false;
	var loadingNews = false;
	var sessionSubscribeIndex = null;
	/**
	 * used to make sure we have each message only once in the $scope['news'] object
	 * @type {Array}
	 */
	var newsObjList = [];
	var $elem = null, $outerWrapper = null, $innerWrapper = null, $container = null;

	// Store hided news entries in cookie storage
	var hidedNews = $cookieStore.get('hidedNews');
	if (!hidedNews) {
		hidedNews = [];
	}

	var session = SessionService.getSession();
	var sessionObj = Session.get(session.sessionId);
	sessionSubscribeIndex = sessionObj.subscribe(function () {
		$scope['news'] = [];
		newsObjList = [];
		country = sessionObj['data']['country'];

		// Start the initial loading of news
		loadNews(country, 0);
	});

	/**
	 * Load the new entries
	 * @param country
	 * @param startFromIndex
	 */
	function loadNews (country, startFromIndex) {
		var news = Feed.get(country, startFromIndex);
		$scope['loadingState'] = true;
		var subscribeIndex = news.subscribe(function () {
			if (news['data']['entries'].length > 0) {
				for (var i = 0; i < news['data']['entries'].length; i++) {
					var entry = news['data']['entries'][i];
					// only add the entry if it has not been added already
					if (newsObjList.indexOf(entry['id']) == -1) {
						newsObjList.push(entry['id']);
						$scope['news'].push({
							'id': entry['id'],
							'title': entry['title'],
							'date': new Date(entry['dateCreated'] * 1000),
							'content': $sce.trustAsHtml(entry['content']),
							'visible': hidedNews.indexOf(entry['id']) === -1
						});
					}
				}
				if (news['data']['entries'].length < newsEntriesToShow) {
					allNewsLoaded = true;
				}
				doAngularApply();
			} else {
				allNewsLoaded = true;
			}
			news.unsubscribe(subscribeIndex);

			// Check if we filled the whole container with news
			if ($innerWrapper === null) {
				$innerWrapper = $('.slotNews .scrollContentInnerWrapper');
			}

			$scope['loadingState'] = false;

			// If not, load more elements until it's full or all elements are loaded
			$timeout(function () {
				if (($innerWrapper.height() + 90 < $(window).height()) && !allNewsLoaded) {
					startFromIndex += newsEntriesToShow;
					loadNews(country, startFromIndex);
				}

				loadingNews = false;
			}, 0);
		});
	}

	/**
	 * Toggle news entry visibility, the hided entries are stored in cookies
	 * @param entry
	 */
	$scope['toggleNewsEntry'] = function (entry) {
		if (entry['visible']) {
			hidedNews.push(entry['id']);
		} else {
			hidedNews.splice(hidedNews.indexOf(entry['id']), 1);
		}
		entry['visible'] = !entry['visible'];
		$cookieStore.put('hidedNews', hidedNews);

		// Check if we have space for new news
		$timeout(function () {
			if (($innerWrapper.height() + 90 < $(window).height()) && !allNewsLoaded) {
				startFromIndex += newsEntriesToShow;
				loadNews(country, startFromIndex);
			}
		}, 0);
	};

	$scope.$on('contentScrolling', function () {
		if (allNewsLoaded || loadingNews) {
			return false;
		}
		loadingNews = true;

		// Cache references to dom objects
		if ($elem === null) {
			$elem = $('.slotNews');
			$outerWrapper = $elem.find('.scrollContentOuterWrapper');
			$container = $elem.find('.scrollContent');
		}

		var contentHeight = $container[0].scrollHeight;
		var diff = $container.scrollTop() - (contentHeight - $outerWrapper.height());

		// If user scrolled down to the end, load news
		if (Math.abs(diff) < 10) {
			startFromIndex += newsEntriesToShow;
			loadNews(country, startFromIndex);
		} else {
			loadingNews = false;
		}
	});

	$scope.$on('$destroy', function () {
		if (sessionSubscribeIndex != null) {
			sessionObj.unsubscribe(sessionSubscribeIndex);
		}
	});
}]);