function GameWorld () {
	this.defaultData = {
		'consumersId': 0,
		'identifier': '',
		'country': '',
		'region': '',
		'worldName': '',
		'status': 0,
		'recommended': 0,
		'blacklisted': 0
	};

	this.data = {};
	this.set(this.defaultData, true);
	this.name = 'GameWorld';
}

GameWorld.STATUS = {
	'STATUS_SETUP': 0,
	'STATUS_BETA': 1,
	'STATUS_ACTIVE': 2,
	'STATUS_CLOSED': 3, //Only registration is closed.
	'STATUS_FINISHED': 4, //The game finished and no login possible anymore.
	'STATUS_ENDGAME_FINISHED': 5 // World wonder finished but players can still login to see the end state
};

/**
 * Length in Days of a server for different speeds
 * NORMAL_SPEED: 1x
 * OTHER_SPEED: >1x
 * @type {
 * 			{
 * 				NORMAL_SPEED: number,
 * 				OTHER_SPEED: number
 * 			}
 * 		}
 */
GameWorld.SERVER_LENGTH = {
	'NORMAL_SPEED': 180,
	'OTHER_SPEED': 100
};

GameWorld.prototype = new AbstractModel();
Cache.register(GameWorld);