function PrestigeDetails () {
	this.defaultData = {
		'globalPrestige': 0,
		'finishedGameWorldsPrestige': 0,
		'activeGameWorldsPrestige': 0,
		'level': 0,
		'stars': {'bronze':0, 'silver':0, 'gold':0},
		'nextLevelPrestigePoints': 0,
		'currentLevelPrestigePoints': 0,
		'nextLevel': 0,
		'nextLevelProgress' : 0
	};

	this.data = {};
	this.set(this.defaultData);
	this.name = 'PrestigeDetails';
	var MAX_PRESTIGE_LEVEL = 12;

	this.set = function(data){
		var nextLevel = data['level'];
		var progress = 100;
		if (data['level'] < MAX_PRESTIGE_LEVEL) {
			var pointLeft = data['nextLevelPrestigePoints'] - data['globalPrestige'];
			var totalPointsForNextLevel = data['nextLevelPrestigePoints'] - data['currentLevelPrestigePoints'];
			progress = ((totalPointsForNextLevel - pointLeft) / totalPointsForNextLevel) * 100;
			nextLevel++;
		}
		data['nextLevel'] = nextLevel;
		data['nextLevelProgress'] = progress;
		this.parent.set.call(this, data);
	};
}

PrestigeDetails.prototype = new AbstractModel();
Cache.register(PrestigeDetails);
