/**
 * USAGE:
 * <player-link user-account-identifier="{{1}}" ></player-link>
 */
/** @expose */
GameLobby.app.directive('playerLink', ['NameService', function (NameService) {
	return  {
		restrict: 'EA',
		template: '<p class="playerLink truncated"></p>',
		replace: true,
		scope: true,
		link: function (scope, elem, attrs) {
			var playerName = '';
			attrs.$observe('userAccountIdentifier', function (id) {
				if(typeof(attrs.name) != 'undefined' && attrs.name != '') {
					playerName = attrs.name;
					fillElement();
				} else if (id != '') {
					NameService.getNameForId(parseInt(id, 0), function (name) {
						if (name.trim() == '') {
							playerName = GameLobby.txt['DefaultName.Player'];
						} else {
							playerName = name;
						}
						fillElement();
					});
				}
			});

			function fillElement () {
				$(elem).text(playerName);
			}
		}
	};
}]);