GameLobby.app.controller('EditPortraitController', ['$scope', 'Request', function ($scope, Request) {

	Request.create(
		'player/getAvatarData',
		{},
		function (data) {
			$scope['data'] = data;
		},
		function (data) {
			$scope['data'] = data;
		}
	);

	$scope['selectAvatar'] = function (selectedAvatarId) {
		$scope['selectedAvatarId'] = selectedAvatarId;
	};

	$scope['savePortrait'] = function () {
		if ($scope['selectedAvatarId'] > 0) {
			Request.create(
				'player/savePortrait',
				{
					'avatarIdentifier': $scope['selectedAvatarId']
				},
				function (data) {
					$scope.modal.close();
				},
				function (data) {
					$scope.modal.close();
				}
			);
		}
	};

}]);